export function getDisplayObject(src: any, target?: DisplayObject) {
    if (src instanceof DisplayObject) {
        return src;
    } else if (src instanceof DisplayObjectAbstract) {
        return src;
    }
    if ($getUIDisplayObjectPath) {
        src = $getUIDisplayObjectPath(src, target);
    }
    return src;
}

/**
 * 
 * @param DisplayObject 
 */
export function getStage(target: DisplayObject | DisplayObjectAbstract | Stage): Stage | undefined {
    if (target.$stage) {
        return target.$stage;
    }
    if (target.parent instanceof Stage) {
        return target.parent;
    }
    if (target.parent) {
        return getStage(target.parent);
    }
    return undefined;
}

/**
 * (jsoniduuid)
 * @param target 
 * @param ids 
 */
export function getDisplayPathById(target: DisplayObject | DisplayObjectAbstract | Stage, ids: string[] = []): string[] {

    if (target.id === '') {
        return ids;
    }

    ids.push(target.id);

    if (target.parent) {
        if (target.parent instanceof Stage) {
            return ids;
        }
        return getDisplayPathById(target.parent, ids);
    }
    return ids;
}


