/**  */
export function now() {
    if (performance !== undefined && performance.now !== undefined) {
        // This must be bound, because directly assigning this function
        // leads to an invocation exception in Chrome.
        return performance.now.bind(performance)();
        // Use Date.now if it is available.
    } else {
        const offset = performance && performance.timing && performance.timing.navigationStart ? performance.timing.navigationStart : Date.now()
        return Date.now() - offset;
    }
}

/**
 * 
 * @param source 
 */
export function deepCopy(source: any, target?: any) {
    if (source === null || source === undefined || typeof source !== 'object') {
        return source;
    } else if (Array.isArray(source)) {
        return [].concat(source as []);
    } else if (typeof source === 'object') {
        const tempTarget: { [key: string]: any } = target || {};
        for (const prop in source) {
            tempTarget[prop] = deepCopy(source[prop], tempTarget[prop]);
        }
        return tempTarget;
    }
    return source;
}

/**
 * helper function to convert string hex to int or default
 * 
 * 16int
 * @param str #FFFFFF,0xFFFFFF
 * @param def 
 */
export function hexToInt(str?: string | number, def?: any) {
    if (str === undefined) {
        return def;
    }
    if (typeof str === 'number')
        return str;
    const sign = "#";
    if (str.charAt(0) === sign) {
        const len = str.length;
        const colorLength = 7;
        if (len > colorLength) {
            console.log(`#color longChars need <= ${colorLength}`);
            str = sign + str.slice(len - colorLength + 1);
        }
        str = str.replace('#', '0x');
    }

    const result = parseInt(str);

    if (isNaN(result)) return def;
    return result;
}
/**
 * helper function to convert string hex to int or default
 * 
 * 16int
 * @param str #FFFFFF,0xFFFFFF
 * @param def 
 */
export function hexListToIntList(str?: string | number | string[] | number[], def?: any) {
    if (str === undefined) {
        return def;
    }
    let colorlist = [];
    if (Array.isArray(str)) {
        let strList = str as [];
        let len = strList.length;
        for (let i: number = 0; i < len; i++) {
            let color = hexToInt(strList[i], undefined);
            colorlist.push(color);
        }
    } else {
        colorlist = hexToInt(str as string | number, def);
    }
    return colorlist;
}

//helper function to convert hex to rgba
/**
 * 
 * @param hex 16  #FFFFFF 
 * @param alpha 
 * @returns "rgba(255,255,255,1)" || false
 */
export function hexToRgba(hex: string, alpha: number) {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? "rgba(" + parseInt(result[1], 16) + "," + parseInt(result[2], 16) + "," + parseInt(result[3], 16) + "," + alpha + ")" : false;
}
/**
 * 1620 01
 * @param c 
 */
export function componentToHex(c: number) {
    const hex = c.toString(16);
    if (hex.length == 4) {
        return "00" + hex;
    }
    return hex.length == 1 ? "0" + hex : hex;
}
/**
 * RGB16
 * @param r  0-255
 * @param g  0-255
 * @param b  0-255
 */
export function rgbToHex(r: number, g: number, b: number) {
    return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
}

/**
 * RGBnumber
 * @param r  0-255
 * @param g  0-255
 * @param b  0-255
 */
export function rgbToNumber(r: number, g: number, b: number) {
    return r * 65536 + g * 256 + b;
}

/**
 * rgb
 * @param color rgb(255,255,255)
 */
export function rgbStrToNumber(color: string) {
    const colors = color.substring(4, color.length - 1).split(",");
    return rgbToNumber(parseInt(colors[0]), parseInt(colors[1]), parseInt(colors[2]));
}
/**
 * 10RGB
 * @param c 
 */
export function numberToRgb(c: number) {
    return {
        r: Math.floor(c / (256 * 256)),
        g: Math.floor(c / 256) % 256,
        b: c % 256,
    };
}

/**
 * hex  RGB
 * 
 * hex: "#ffffff"->255,255,255
 * 
 * 16: 0xffffff->255,255,255
 * @param hex 
 */
export function hexToRgb(hex?: string | number) {
    if (hex === undefined)
        hex = 0xffffff;

    if (typeof (hex) == "number") {
        return numberToRgb(hex);
    }

    // Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
    const shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
    hex = hex.replace(shorthandRegex, function (m, r, g, b) {
        return r + r + g + g + b + b;
    });

    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16)
    } : { r: 255, g: 255, b: 255 };
}

/**
 * amtstart-stop
 * @param start 
 * @param stop  
 * @param amt 0-1  >1100
 */
export function Lerp(start: number, stop: number, amt: number) {
    if (amt > 1) amt = 1;
    else if (amt < 0) amt = 0;
    return start + (stop - start) * amt;
}

/**
 * 
 * @param num 
 * @param decimals 
 */
export function Round(num: number, decimals: number) {
    const pow = Math.pow(10, decimals);
    return Math.round(num * pow) / pow;
}


