/**
 * DIVcanvas
 * @param localDisplayObject 
 * @returns 
 */
export function toDomRectangle(localDisplayObject: DisplayObjectAbstract | Stage, point?: vf.IPointData) {

    let rect = new vf.Rectangle();
    if (point) {
        const p = localDisplayObject.container.toGlobal(point);
        rect.x = p.x;
        rect.y = p.y;
        const worldTransform = localDisplayObject.container.worldTransform;
        rect.width = worldTransform.a;
        rect.height = worldTransform.d;
    }
    else {
        rect = localDisplayObject.container.getBounds();
    }

    let viweCanvas: HTMLCanvasElement;
    if (localDisplayObject.stage) {
        viweCanvas = localDisplayObject.stage.app.view;
    } else {
        viweCanvas = (localDisplayObject as Stage).app.view;
    }
    rect.x += viweCanvas.offsetLeft;
    rect.y += viweCanvas.offsetTop;

    return rect
}


