/**
 * 
 * @param moduleName 
 * @param option 
 * @param parent 
 * example:
 *   let img = vf.gui.Utils.createDisplayObject("Image", {x: 0, y: 0, src: 'xxxx.png'}, this) as vf.gui.Image;
 */
export function createDisplayObject(moduleName: string, option: any, parent?: DisplayObjectAbstract) {
    let ModuleClass = vf.utils.getGuiModule(moduleName);
    if (ModuleClass) {
        try {
            let obj = new ModuleClass();
            for (let key in option) {
                obj[key] = option[key];
            }
            parent && parent.addChild(obj);
            return obj;
        }
        catch (e) {
            console.error(`${moduleName}`, e);
        }
    }
    else {
        console.error(`${moduleName}gui`);
    }
}