import { DisplayObject } from '../core/DisplayObject';
import { ComponentEvent } from '../interaction/Index';
import * as UIKeys from '../core/DisplayLayoutKeys';
import { getSource, hexToInt, hexListToIntList, debug } from '../utils/Utils';
/**
 * 
 *
 *  xxxx.style.breakWords = true;
 *
 * .
 *
 *  style.textAlign 
 *
 * @example let label = new vf.gui.Label();
 *
 */
export class Label extends DisplayObject {
    public readonly sprite: vf.Text;
    private _lineGraphics: any;
    private _textDecorationWidth = 3; //
    private _textDecorationStyle: 'Solid' | 'Double' = 'Solid'; //
    private _textAlpha = 1;

    private readonly _defaultColor = 0xffffff;
    private _color?: number | number[] | string | string[] = this._defaultColor;

    public constructor(text = '') {
        super();
        this.sprite = new vf.Text(text, { breakWords: true, fill: '0xffffff', align: this.style.textAlign });
        this.container.addChild(this.sprite);
        this.on(ComponentEvent.WIDTH_CHANGE, this.onWidthChange, this);
    }

    private get widthNoShadow() { //     
        const width = this.sprite.width;
        const style = this.sprite.style;
        const shadowW = Math.max(0, Math.cos(style.dropShadowAngle) * style.dropShadowDistance);
        if (!style.dropShadow) {
            return width;
        }
        return Math.ceil(width - shadowW);
    }
    private get heightNoShadow() {
        const height = this.sprite.height;
        const style = this.sprite.style;
        const shadowH = Math.max(0, Math.sin(style.dropShadowAngle) * style.dropShadowDistance);
        if (!style.dropShadow) {
            return height;
        }
        return Math.ceil(height - shadowH);
    }
    private onWidthChange(width: number) {
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        if (isNaN(this.style.wordWrapWidth!)) {
            this.sprite.style.wordWrapWidth = width;
        }
    }

    /**
     * 
     */
    public get resolution() {
        return this.sprite.resolution;
    }
    public set resolution(value) {
        this.sprite.resolution = value;
    }


    public get fillColor() {
        return this.style.fillColor;
    }
    public set fillColor(value: number[] | number[][]) {
        if (value.length === 0) {
            this.sprite.style.fill = undefined;
        } else {
            if (Array.isArray(value[0])) {
                this.sprite.style.fill = [];
                const items: string[] = [];
                value.forEach((item: any) => {
                    items.push(`rgba(${item[0]}, ${item[1]}, ${item[2]}, ${item[3]})`)
                });
                this.sprite.style.fill = items;
            } else {
                this.sprite.style.fill = `rgba(${value[0]}, ${value[1]}, ${value[2]}, ${value[3]})`;
            }

        }
    }
    public set fillAlpha(value: number) {
        // 
    }
    /**
     * 
     */
    public get color() {
        return this._color;
    }

    public set color(value) {
        const color = hexListToIntList(value, undefined);
        if (this._color === color) {
            return;
        }
        this.sprite.style.fill = this._color = color;
        // this.style.color 
        (this.style as any)._color = color;
        this.invalidateDisplayList();
    }

    /**
     * 
     */
    public get textAlpha() {
        return this._textAlpha;
    }
    public set textAlpha(value) {
        if (this._textAlpha === value) {
            return;
        }
        this.sprite.alpha = this._textAlpha = value;
        this.invalidateDisplayList();
    }

    public get textDecorationStyle(): 'Solid' | 'Double' {
        return this._textDecorationStyle;
    }

    public set textDecorationStyle(value: 'Solid' | 'Double') {
        if (this._textDecorationStyle === value) {
            return;
        }
        this._textDecorationStyle = value;
        this.invalidateDisplayList();
    }
    /**
     * 
     */
    public get text() {
        return this.sprite.text;
    }
    public set text(value) {
        if (this.sprite.text === value) {
            return;
        }
        this.sprite.text = value;
        this.setActualSize(this.widthNoShadow, this.heightNoShadow);
        this.invalidateSize();
        this.invalidateDisplayList();
        this.emit(ComponentEvent.CHANGE, this);
    }

    public set fontCssStyle(value: any) {
        if (value.color) {
            value.fill = value.color;
        }
        value.breakWords = true;
        this.sprite.style = value;
        if (value.fontFamily) {
            this.sprite.style.fontFamily = value.fontFamily;
        }

        this.setActualSize(this.widthNoShadow, this.heightNoShadow);
        this.invalidateSize();
        this.invalidateDisplayList();
    }

    private setLine() {
        const type = this.style.textDecoration;
        this.clearLineGraphics();
        if (
            type == 'None' ||
            !this.sprite ||
            !this.sprite.text ||
            this.sprite.text == ''
        ) {
            return;
        } else {
            this.cacheOldData();
            this.showUnderLine();
        }
    }

    private showUnderLine(): void {
        if (!this._lineGraphics) {
            this._lineGraphics = new vf.Graphics();
        }
        if (!this._lineGraphics.parent) {
            this.container.addChild(this._lineGraphics);
        }
        //
        this.autoDrawLine();
    }

    private autoDrawLine() {
        const vfMeasured = this.sprite.vfMeasured; //vf.TextMetrics.measureText(this.text, this.sprite.style, this.style.wordWrap);////
        if (vfMeasured == null) {
            console.log('');
            return;
        }
        this._textDecorationWidth = this.style.fontSize / 10 + 1;
        let leftX: number = Number.MAX_VALUE;
        const lineInfo = []; //
        const linesNum = vfMeasured.lines.length;

        for (let i = 0; i < linesNum; i++) {
            const x = this.getStartPosX(vfMeasured.lineWidths[i]);
            const liney = this.getStartPosY(vfMeasured.lineHeight, i);
            leftX = leftX < x ? leftX : x;
            lineInfo.push({
                leftX: x,
                lineY: liney,
                lineWidths: vfMeasured.lineWidths[i],
            });
        }

        const spriteY = this.sprite.y;
        for (let i = 0; i < lineInfo.length; i++) {
            const infoItem = lineInfo[i];
            const x = infoItem.leftX;
            this.drawLine(x, infoItem.lineY + spriteY, infoItem.lineWidths);
        }
    }

    private getStartPosX(width: number): number {
        const textAlign = this.style.textAlign; //
        let startPosX = 0;
        switch (textAlign) {
            case 'left':
                startPosX = 0;
                break;
            case 'right':
                startPosX = this.width - width;
                break;
            case 'center':
                startPosX = (this.width - width) * 0.5;
                break;
        }
        return startPosX;
    }

    private getStartPosY(height: number, lineIndex: number): number {
        const vfMeasured = this.sprite.vfMeasured;
        let startPosY = 0;
        const type = this.style.textDecoration;
        const strok = this.sprite.style.strokeThickness;
        const dis = vfMeasured.fontProperties.fontSize + strok;
        const lineOffsetY = 1;
        switch (type) {
            case 'None':
                break;
            case 'UnderLine':
                startPosY = (lineIndex) * height + lineOffsetY + dis;
                break;
            case 'LineThrough':
                startPosY = (lineIndex) * height + dis * 0.5;
                break;
            case 'Overline':
                startPosY = lineIndex * height;
                break;
        }

        return startPosY;
    }

    private drawLine(startPosX: number, startPosY: number, lineWidth: number) {
        const lineG = this._lineGraphics as vf.Graphics;
        const style = this._textDecorationStyle;

        switch (style) {
            case 'Solid':
                lineG.lineStyle(
                    this._textDecorationWidth,
                    this.style.textDecorationColor
                );
                lineG.moveTo(startPosX, startPosY);
                lineG.lineTo(startPosX + lineWidth, startPosY);
                break;
            case 'Double':
                lineG.lineStyle(
                    this._textDecorationWidth * 0.5,
                    this.style.textDecorationColor
                );
                lineG.moveTo(startPosX, startPosY);
                lineG.lineTo(startPosX + lineWidth, startPosY);
                lineG.moveTo(
                    startPosX,
                    startPosY + this._textDecorationWidth * 1
                );
                lineG.lineTo(
                    startPosX + lineWidth,
                    startPosY + this._textDecorationWidth * 1
                );
                break;
        }
    }
    /**
     * this.container.width..
     */
    protected measure(): void {
        const values = this.$values;
        values[UIKeys.measuredWidth] = this.widthNoShadow;
        values[UIKeys.measuredHeight] = this.heightNoShadow;
    }
    protected updateDisplayList(
        unscaledWidth: number,
        unscaledHeight: number
    ): void {
        super.updateDisplayList(unscaledWidth, unscaledHeight);
        const values = this.$values;

        if (!isNaN(values[UIKeys.explicitWidth])) {
            switch (this.style.textAlign) {
                case 'left':
                    this.sprite.x = 0;
                    break;
                case 'right':
                    this.sprite.x =
                        values[UIKeys.explicitWidth] - this.widthNoShadow;
                    break;
                case 'center':
                    this.sprite.x =
                        (values[UIKeys.explicitWidth] - this.widthNoShadow) >> 1;
                    break;
            }
        }

        if (!isNaN(values[UIKeys.explicitHeight])) {
            switch (this.style.verticalAlign) {
                case 'top':
                    this.sprite.y = 0;
                    break;
                case 'bottom':
                    this.sprite.y =
                        values[UIKeys.explicitHeight] - this.sprite.height;
                    break;
                case 'middle':
                    this.sprite.y =
                        (values[UIKeys.explicitHeight] - this.sprite.height) >>
                        1;
                    break;
            }
        }
        this.setLine();
    }

    private cacheOldData() {
        if (isNaN(this.style.textDecorationColor) || this.style.textDecorationColor === undefined || this.style.textDecorationColor === null) {
            let color: any = this._defaultColor;
            if (this.style.color !== undefined) {
                if (typeof this.style.color === 'number') {
                    color = this.style.color;
                } else {
                    let color: any = (this.style
                        .color as number[])[0].toString();
                    color = hexToInt(color, color);
                }
                this.style.textDecorationColor = color;
            }
            this.style.textDecorationColor = color;
        }
    }

    private clearLineGraphics() {
        const graphics = this._lineGraphics;
        if (graphics) {
            if (graphics.parent) {
                graphics.parent.removeChild(this._lineGraphics);
            }
            graphics.clear();
        }
    }

    public release() {
        super.release();
        const sprite = this.sprite;
        if (sprite && sprite.parent) {
            sprite.parent.removeChild(sprite).destroy();
        }
        this.offAll(ComponentEvent.CHANGE);
        this.clearLineGraphics();
        this._textDecorationStyle = 'Solid';
        this._textDecorationWidth = 3;
        this.off(ComponentEvent.WIDTH_CHANGE, this.onWidthChange, this);
        this._color = 0xfffff0;
    }
}
