import { DisplayObject } from "../core/DisplayObject";
import { getSource, getTexture, toDomRectangle } from "../utils/Utils";
import { VideoGroupController } from "../interaction/Index";
import { VideoBtn } from "./VideoBtn";
/**
 * 
 * 
 */
export class Video extends DisplayObject {

  private _video: HTMLVideoElement;

  private _videoHasEvent = false;
  private _src: any;
  protected _canvasBounds: { top: number; left: number; width: number; height: number } | undefined;
  protected _lastRenderer: vf.Renderer | undefined;
  protected _resolution = 1;

  //  ~~~~~~~~~~~~~~~
  private _canplayFun: any;
  private _canplaythroughFun: any;
  private _completeFun: any;
  private _endedFun: any;
  private _loadeddataFun: any; //    dom( android webgl)
  private _durationchangeFun: any;
  private _pauseFun: any;
  private _playFun: any;

  //  ~~~~~~~~~~
  private _autoplay = false;
  private _canPlayFirst = false; // canplay  currenttime   
  private _videoTextrue: any;
  private _sprite: any;
  private _poster = ""; // url
  private _posterImg?: vf.Sprite; // 
  private _videoBtn?: VideoBtn; // 
  private _firstLoad = false; //
  private _muted = false;
  private _loop = false;
  private _controls = true;
  private _volume = 1;
  private _currentTime = 0;
  private _oldPoster = "";
  private _oldSrc = "";
  private _canPlay = false;
  private _visibleStatus = true; //  or 

  private _elemDraw = false; //   
  private _isplaying = false; //  play or pause

  public forceElementDraw = false; // 


  public constructor() {
    super();

    this._sprite = new vf.Sprite();
    this.container.addChildAt(this._sprite, 0); //   dom
    this.videoGroup = "";
    this._video = document.createElement('video');

    vf.utils.deprecation("1.0.0", "VideosilentSrcsoundSrc");
  }

  private createVideoBtn() {
    if (!this._videoBtn) {
      this._videoBtn = new VideoBtn();
      this._videoBtn.width = 50;
      this._videoBtn.height = 50;
      this._videoBtn.interactabled = true;
      this._videoBtn.on(vf.gui.Interaction.TouchMouseEvent.onClick, this.clickAudio, this);
      this.addChild(this._videoBtn);
      this._videoBtn.visible = this._controls;
    }
  }

  private clickAudio(e: any, thisObj: any) {
    if (this._isplaying === true) {
      this.pause();
    } else {
      this.play();
    }
  }
  /**  
   *   srcsprite
   *  spritesrc
   */
  private createVideoSource() {
    if (this.useNativeAudio()) {
      return;
    }
    if (!this._sprite) {
      this._sprite = new vf.Sprite();
      this.container.addChildAt(this._sprite, 0);
    }

    if (!this._video) {
      this._video = document.createElement('video');
    }
    // mediaSteam  srcObject
    const video = this._video;
    if (window["MediaStream"] && this._src instanceof MediaStream) {
      video.srcObject = this._src || "";
      this._autoplay = true;
    } else {
      video.src = this._src || "";
    }

    video.setAttribute('preload', 'auto');//  
    video.setAttribute('webkit-playsinline', '');
    video.setAttribute('playsinline', '');
    video.setAttribute('x5-video-player-type', 'h5');
    video.setAttribute('x5-video-player-fullscreen', "true");
    video.setAttribute('playsinline', "true");
    video.crossOrigin = "anonymous";

    this._videoTextrue = vf.Texture.from(video, { resourceOptions: { autoPlay: this._autoplay, autoLoad: true, crossorigin: true } });

    const v = this._videoTextrue;
    this._video.onseeked = function () {
      if (v.baseTexture && v.baseTexture.resource) {
        v.update();
      }
    };
    this._sprite.texture = this._videoTextrue;

    video.muted = this._muted;
    video.loop = this._loop;
    video.currentTime = this._currentTime;
    video.volume = this._volume;
    video.autoplay = this._autoplay;
    video.controls = this._controls;

    let url = this._poster;
    if (this._poster && this._poster !== "") {
      const o = getSource(this._poster);
      if (typeof (o) === "object") {
        if (o.baseTexture && o.baseTexture.resource && o.baseTexture.resource.url) {
          url = o.baseTexture.resource.url;
        }
      }
    }
    video.poster = url;

    if (this._autoplay && this.useNativeAudio()) {
      if (this.stage?.syncManager && this.stage.syncManager.resumeStatusFlag) {
        return;
      }
      this.nativeEmit(this._audio, 'playAudio');
    }
    /**
    * 
    */
    this.clearVideoEvent();
    if (!this._videoHasEvent) { //
      this._videoHasEvent = true;
      this._canplayFun = this.canplayFun.bind(this);
      this._canplaythroughFun = this.canplaythroughFun.bind(this);
      this._completeFun = this.completeFun.bind(this);
      this._endedFun = this.endedFun.bind(this);
      this._loadeddataFun = this.loadeddataFun.bind(this);
      this._durationchangeFun = this.durationchangeFun.bind(this);
      this._pauseFun = this.pauseFun.bind(this);
      this._playFun = this.playFun.bind(this);

      //
      video.addEventListener('canplay', this._canplayFun);
      //
      video.addEventListener('canplaythrough', this._canplaythroughFun);
      //
      video.addEventListener('complete', this._completeFun);
      //
      video.addEventListener('play', this._playFun);
      //
      video.addEventListener('pause', this._pauseFun);
      //
      video.addEventListener('ended', this._endedFun);
      //
      video.addEventListener('loadeddata', this._loadeddataFun);
      //duration 
      video.addEventListener('durationchange', this._durationchangeFun);
    }
  }

  private canplayFun(e: any) {
    if (this._canPlayFirst === false) { //  currentTime canplay video  
      this._canPlayFirst = true;
      this.emit('canplay', e, this);
    }
  }
  private canplaythroughFun(e: any) {
    this.emit('canplaythrough', e, this);
  }
  private completeFun(e: any) {
    this.emit('complete', e, this);
  }
  private pauseFun(e: any) {
    (this._videoBtn && this._videoBtn.setStates("pause"));
    this._isplaying = false;
    this.emit("paused", e, this);
  }
  private playFun(e: any) {
    (this._videoBtn && this._videoBtn.setStates("play"));
    this._isplaying = true;
    VideoGroupController.updateVideoGroupStates(this);
    this.emit("play", e, this);
  }

  private endedFun(e: any) {
    this._isplaying = false;
    this.emit('ended', e, this);
  }

  private loadeddataFun(e: any) {
    const video = this._video;
    //
    if (this._canPlay == false && (video.videoWidth <= 0) || this.checkComatibility() === true) {
      this._canPlay = true;
      this._elemDraw = true;
      console.log("");
      this.clearSprite();
      if (!video.parentElement && this.stage && this.stage.app) {
        const canvas: HTMLCanvasElement = this.stage.app.view;
        if (canvas && canvas.parentElement) {
          canvas.parentElement.appendChild(this._video);
          //
          this.setPos();
          //
          this.invalidateDisplayList();
        }
      }
    } else {
      this._posterImg && (this._posterImg.visible = true);
      this.createVideoBtn();
    }

    this._firstLoad = true;
    if (this._fullStates == "fulling") {
      this.fullScreen();
    }
    this.emit('loadeddata', e, this);
  }
  // 
  private checkComatibility(): boolean {
    if (this.forceElementDraw === true) {
      return true;
    }
    const agent = navigator.userAgent;
    if (agent) {
      if (agent.indexOf("HUAWEICMR-W09") > -1) {
        return true;
      }

      const info = vf.utils.getSystemInfo();
      const bowser = info.browser;
      const name = bowser.name as string;
      const version = bowser.version;
      const type = info.device.type;
      let sing = Number.MAX_VALUE.toString();
      if (name.toLocaleLowerCase() === "chrome") {
        sing = version.split(".")[0];
      }
      // alert("bowser :" + version + "000000" + type);
      if (Number(sing) <= 62 && (type === "tablet" || type === "mobile")) {
        // chrome62 
        return true;
      }
    }

    return false;
  }
  private durationchangeFun(e: any) {
    this.emit('durationchange', e, this);
  }

  private checkVideoPlay() {
    const video = this._video;
    this.updateSystem();
    if (this._sprite) {
      if (this._sprite.texture) {
        this._sprite.texture.destroy();
      }
      this._sprite.destroy();
    }

    this._sprite = undefined;
    video.width = this.width;
    video.height = this.height;
    video.style.position = "absolute";
    //
    this.setPos();
  }

  public get x() {
    return this.container.x;
  }
  public set x(value: number) {
    this.container.position.x = value;
    this.invalidateDisplayList();
  }

  public get y() {
    return this.container.y;
  }

  public set y(value: number) {
    this.container.position.y = value;
    this.invalidateDisplayList();
  }

  private setPos() {
    this._canvasBounds = this._getCanvasBounds();
    const cb = this._canvasBounds;
    this.stage && this.container.updateTransform();
    const m = this.container.worldTransform.clone();
    const transform = 'matrix(' + [m.a, m.b, m.c, m.d, 0, 0].join(',') + ')';
    if (cb) {
      const vleft = cb.left + m.tx + this.width * (m.a - 1) * 0.5;
      const vtop = cb.top + m.ty + this.height * (m.d - 1) * 0.5;
      this.updatePostion(vtop, vleft, transform, this.container.worldAlpha);
    }
  }

  private _getCanvasBounds() {
    if (this._lastRenderer) {
      const rect = this._lastRenderer.view.getBoundingClientRect();
      const bounds = { top: rect.top, left: rect.left, width: rect.width, height: rect.height };
      bounds.left += window.scrollX;
      bounds.top += window.scrollY;
      return bounds;
    }
    return undefined;
  }

  private updateSystem() {
    if (this.stage) {
      const renderer = this.stage.app.renderer as vf.Renderer;
      this._resolution = renderer.resolution;
      this._lastRenderer = renderer;
    }

  }
  private updatePostion(top: string | number, left: string | number, transform: string, opacity?: string | number) {
    this._video.style.top = Number(top) + 'px'
    this._video.style.left = Number(left) + 'px'
    this._video.style.transform = transform;
    if (opacity)
      this._video.style.opacity = opacity.toString();

  }



  private _lastPostion = new vf.Rectangle();

  protected updateDisplayList(unscaledWidth: number, unscaledHeight: number) {
    super.updateDisplayList(unscaledWidth, unscaledHeight);

    const lastPostion = this._lastPostion;
    if (lastPostion.x !== this.x || lastPostion.y !== this.y ||
      lastPostion.width !== this.width || lastPostion.height !== this.height) {
      this._lastPostion.x = this.x;
      this._lastPostion.y = this.y;
      this._lastPostion.width = this.width;
      this._lastPostion.height = this.height;
      this.validateDisplayList();
      // if (this.useNativeAudio()) {
      //   this.nativeEmit(this.src, 'position')
      // }

    }

    if (this.useNativeAudio()) {
      if (this._silent) {
        this._src = this._silent;
      }
      else {
        console.warn('silent');
        return;
      }
    }

    if (unscaledWidth === 0 && unscaledHeight === 0) {
      return;
    }

    if (!this._src || this._src === undefined || this._src === "" || this._src != this._oldSrc) {
      this._oldSrc = this._src;
      this.clearSource();
      if (this._src && this._src != "") {
        this.createVideoSource();
      }
    }
    if (this._canPlay) {
      this.checkVideoPlay();
    }

    if (this._sprite && this._fullStates === "noFull") {
      this._sprite.width = unscaledWidth;
      this._sprite.height = unscaledHeight;
    }


    this.updataPoster();
  }

  private updataPoster() {

    if (this._poster && this._poster !== this._oldPoster) {
      if (!this._posterImg) {
        this._posterImg = new vf.Sprite();
        const index = this.container.getChildIndex(this._sprite);
        this.container.addChildAt(this._posterImg, index + 1);
      }
      this._posterImg.texture = getTexture(this._poster);
      this._oldPoster = this._poster;
      this._posterImg.width = this.width;
      this._posterImg.height = this.height;
      this._posterImg.visible = false;
    }
  }

  private clearPoster() {
    if (this._posterImg) {
      if (this._posterImg.parent) {
        this._posterImg.parent.removeChild(this._posterImg);
      }
      this._posterImg.destroy();
      this._posterImg = undefined;
    }
    this._oldPoster = "";
  }

  /**
   * ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
   */

  //
  private _silent: any;
  public set silent(value: any) {
    value = getSource(value);
    this._silent = value;
  }

  public get silent() {
    return this._silent;
  }

  //
  private _audio: any;
  public set audio(value: any) {
    value = getSource(value);
    this._audio = value;
  }
  public get audio() {
    return this._audio;
  }

  //src
  public get src() {
    return this._src;
  }
  public set src(value) {
    const o = getSource(value);
    let checkUrl = false;
    checkUrl = (typeof value === 'string' && value.indexOf('//') !== -1);
    let v = value;
    if (!o && !checkUrl) { //  
      this._src = null;
      this._oldSrc = undefined as any;
      //
      this.clearSource();
      this.clearVideoBtn();
      this.clearPoster();
      this.clearSprite();
      this.clearVideo();
      return;
    }
    if (o && typeof (o) === "object" && o.url) {
      v = o.url
      if (o["audio"]) {
        this._audio = o["audio"];
      }
      if (o["silent"]) {
        this._silent = o["silent"];
      }
    }

    if (this._src == v) {
      return;
    }

    if (v instanceof HTMLVideoElement) {
      this._video = v;
      this._src = this._video.src;
    } else {
      this._src = v;
    }
    // this.checkSrcLegal();
    //  
    this.invalidateDisplayList();
  }


  public get srcObject() {
    if (this._src && window["MediaStream"] && this._src instanceof MediaStream) {
      return this._src;
    }
    if (!window["MediaStream"]) {
      console.log(" mediaStream");
    }
    return undefined as any;
  }

  public set srcObject(value: any) {
    if (this._src === value) {
      return;
    }
    this._src = value;
    this.invalidateDisplayList();
  }

  public get autoplay() {
    return this._autoplay;
  }

  public set autoplay(value) {
    this._autoplay = value;
    this._video && (this._video.autoplay = value);
  }

  public get loop() {
    return this._loop;
  }
  public set loop(value: boolean) {
    this._loop = value;
    this._video && (this._video.loop = value);
  }

  public get poster() {
    return this._poster;
  }

  public set poster(value) {
    if (this._poster === value) {
      return;
    }
    this._poster = value;
    this.invalidateDisplayList();
  }
  //
  public get muted(): boolean {
    return this._muted;
  }
  public set muted(boo: boolean) {
    this._muted = boo;
    this._video && (this._video.muted = boo);
  }

  public get controls(): boolean {
    return this._controls;
  }
  public set controls(boo: boolean) {
    this._controls = boo;
    this._video && (this._video.controls = boo);
    this._videoBtn && (this._videoBtn.visible = (this._controls && !this._elemDraw));
  }

  public get volume() {
    return this._volume;
  }
  public set volume(value) {
    value = value > 1 ? 1 : value;
    value = value < 0 ? 0 : value;
    this._volume = value;
    this._video && (this._video.volume = value);
  }

  public get visible() {
    return this._visibleStatus;
  }

  public set visible(boo: boolean) {
    if (this._visibleStatus === boo) {
      return;
    }
    this._visibleStatus = boo;
    if (boo === true) {
      this.validateNow();
    }
    this.container.visible = boo;
    this._video && (this._video.style.visibility = this.visible ? "visible" : "hidden");
  }

  public get videoElement() {
    return this._video;
  }

  //
  public get currentTime(): number {
    return this._currentTime;
  }

  public set currentTime(value: number) {

    this._currentTime = value;
    this._video && (this._video.currentTime = this._currentTime);
  }

  /** 
   * 
   */
  public get videoGroup(): string | undefined {
    return this._groupName;
  }

  public set videoGroup(value: string | undefined) {
    if (value === undefined) {
      VideoGroupController.unRegistrerVideoGroup(this)
    }
    if (this._groupName == value) {
      return;
    }
    this._groupName = value;//registrerCheckGroup
    VideoGroupController.registrerVideoGroup(this);
  }

  /** 
   * ~~~~~~~~~~~~~~~~
   * */
  public get duration() {//
    if (this._video) {
      return this._video.duration;
    }
    return 0;
  }

  public get isPlaying() {
    return this._isplaying;
  }

  /**
  * ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
  **/
  public play() {
    //
    if (this.stage?.syncManager && this.stage.syncManager.resumeStatusFlag) {
      return;
    }

    //nativeAudio
    if (this.useNativeAudio()) {
      if (!this._audio) {
        console.warn('audio');
      }
      this.nativeEmit(this._audio, 'playAudio');
    }
    if (this._video) {
      this.clearPoster();
      try {
        this._video.play();
      } catch (error) {
        console.log(error);
      }
      return;
    } else {
      this.autoplay = true; // 
    }
    // throw new Error("Video is undefined!");
  }

  public pause() {
    //
    if (this.stage?.syncManager && this.stage.syncManager.resumeStatusFlag) {
      return;
    }

    //nativeAudio
    if (this.useNativeAudio()) {
      this.nativeEmit(this._audio, 'pauseAudio');
    }
    this._video && this._video.pause();
  }

  private _oldX = 0;
  private _oldY = 0;

  private _fullStates: "full" | "fulling" | "noFull" = "noFull";
  //
  public requestFullScreen() {
    if (this._elemDraw == true) {
      this.requestFullScreenDom();
      return;
    }

    if (this._fullStates == "full") {
      return;
    }
    //   
    //1 stage
    //2.  sprite
    const stage = this.stage;
    if (!stage || this._firstLoad == false) {
      this._fullStates = "fulling";
      return;
    }
    this.fullScreen();
  }
  private fullScreen() {
    const stage = this.stage;
    if (!stage || !this._sprite) {
      return;
    }
    this._fullStates = "full";
    this._oldX = this.x;
    this._oldY = this.y;
    const stageWidth = stage.width;
    const stageHeight = stage.height;
    const scale = Math.min(stageWidth / this.width, stageHeight / this.height);
    const w = this.width * scale;
    const h = this.height * scale;
    this._sprite.width = w;
    this._sprite.height = h;
    if (this._posterImg) {
      this._posterImg.width = w;
      this._posterImg.height = h;
    }

    let disX = 0;
    let disY = 0;
    if (this.container.parent) {
      const pos1 = this.container.parent.toLocal(new vf.Point(0, 0));
      disX = pos1.x;
      disY = pos1.y;
    }

    this.x = (stageWidth - this._sprite.width) * 0.5 + disX;
    this.y = (stageHeight - this._sprite.height) * 0.5 + disY;

  }

  private requestFullScreenDom() {
    const de: any = this._video;

    if (de.requestFullscreen) {
      de.requestFullscreen();
    } else if (de.mozRequestFullScreen) {
      de.mozRequestFullScreen();
    } else if (de.webkitRequestFullScreen) {
      de.webkitRequestFullScreen();
    } else if (de.webkitEnterFullScreen) {
      de.webkitEnterFullScreen();
    }
  }

  //
  public exitFullscreen() {
    if (this._elemDraw == true) {
      this.exitFullscreenDom();
      return;
    }
    if (this._fullStates == "noFull" || !this._sprite) {
      return;
    }
    this._fullStates = "noFull";
    this._sprite.width = this.width;
    this._sprite.height = this.height;
    if (this._posterImg) {
      this._posterImg.width = this.width;
      this._posterImg.height = this.height;
    }
    this.x = this._oldX;
    this.y = this._oldY;
  }
  public exitFullscreenDom() {
    const de: any = this._video;
    if (de.exitFullscreen) {
      de.exitFullscreen();
    } else if (de.mozCancelFullScreen) {
      de.mozCancelFullScreen();
    } else if (de.webkitCancelFullScreen) {
      de.webkitCancelFullScreen();
    } else if (de.webkitExitFullScreen) {
      de.webkitExitFullScreen();
    }
  }

  private clearSource() {
    const v = this._videoTextrue;
    if (v) {
      this.exitFullscreen();
      if (v.baseTexture && v.baseTexture.resource.source) {
        v.baseTexture.resource.source.onseeked = null;
      }
      v.destroy();
    }

  }

  private clearVideoBtn() {
    if (this._videoBtn) {
      if (this._videoBtn.parent) {
        this._videoBtn.parent.removeChild(this._videoBtn);
      }
      this._videoBtn.off(vf.gui.Interaction.TouchMouseEvent.onClick, this.clickAudio, this);
      this._videoBtn.release();
      this._videoBtn = undefined;
    }
  }

  private clearVideoEvent() {
    if (this._video) {
      const video = this._video;
      video.removeEventListener('canplay', this._canplayFun);
      //
      video.removeEventListener('canplaythrough', this._canplaythroughFun);
      //
      video.removeEventListener('complete', this._completeFun);
      //
      video.removeEventListener('ended', this._endedFun);
      //
      video.removeEventListener('loadeddata', this._loadeddataFun);
      //duration 
      video.removeEventListener('durationchange', this._durationchangeFun);
      //
      video.removeEventListener('pause', this._pauseFun);
      //
      video.removeEventListener('play', this._playFun);
    }
    this._videoHasEvent = false;
  }

  private clearVideo() {
    if (this._video) {
      const video = this._video;
      this.clearVideoEvent();
      if (video.parentElement) {
        video.parentElement.removeChild(this._video);
      }
    }
    this._video && (this._video.pause());
    this._video = null as any;
    this._isplaying = false;
  }

  private clearSprite() {
    if (this._sprite) {
      this._sprite.parent && this._sprite.parent.removeChild(this._sprite);
      this._sprite.texture && this._sprite.texture.destroy();
      this._sprite.destroy();
      this._sprite = undefined;
    }
  }

  private useNativeAudio() {
    return (this.stage as any)?.config?.vfvars.useNativeAudio;
  }

  private nativeEmit(url: string, typeTag: 'pauseAudio' | 'playAudio' | 'position',) {

    this.stage?.getSystemEvent().emit('message', {
      code: 'native',
      type: 'native',
      level: 'command',
      target: this,
      data: {
        type: typeTag,
        src: url,
        mode: 'sound',
        signalling: false,
      }
    });
  }

  public release() {
    super.release();
    //nativeAudio
    if (this.useNativeAudio()) {
      this.nativeEmit(this._audio, 'pauseAudio');
    }

    this._src = null;
    this._oldSrc = null as any;
    this._canvasBounds = undefined;
    this._lastRenderer = undefined;
    this._resolution = 1;
    this._poster = this._oldPoster = "";
    VideoGroupController.unRegistrerVideoGroup(this);
    this._canplayFun = null;

    this.clearSource();
    this.clearPoster();
    this.clearSprite();
    this.clearVideo();
    this.clearVideoBtn();

    this._canplaythroughFun = null;
    this._completeFun = null;
    this._endedFun = null;
    this._loadeddataFun = null;
    this._durationchangeFun = null;
    this._firstLoad = false;
    this._fullStates = "noFull";
    this._canPlay = false;
    this._elemDraw = false;
    this._canPlayFirst = false;
  }
}

