class Ticker extends vf.Ticker {
    public constructor(){
        super();
        
    }

    public start(){
        super.start();
        this.currentTickerTime = performance.now();
    }

    public currentTickerTime = performance.now();

    public update(currentTime: number){
        if (currentTime === void 0) { currentTime = performance.now(); }
        if(currentTime - (this as any)._lastFrame >= (this as any)._minElapsedMS){
            this.currentTickerTime += (currentTime - (this as any).lastTime);
        }
        super.update(currentTime);
    }

    /**
     *  ms
     * @param duration 
     */
    public timeTravel(duration: number){
        // let deltaTime = (this as any)._minElapsedMS;  //
        let deltaTime = 50;  //50 by ziye+
        // deltaTime = parseFloat(deltaTime.toFixed(6));
        while(duration > 0){
            duration -= deltaTime;
            //Tween
            // if(duration < 0){
            //     deltaTime += duration;
            // }
            
            this.currentTickerTime += deltaTime;
            if(duration < 0){
                this.currentTickerTime += duration;  //
            }
            //vf.gui.ticker vf.ticker
            for(let i = 0; i < 2; ++i){
                const _target = (i === 0 ? this : vf.Ticker.shared);
                const head = (_target as any)._head;
                let listener = head.next;
                while (listener)
                {
                    _target.deltaMS = deltaTime;
                    listener = listener.emit(deltaTime * vf.settings.TARGET_FPMS);
                }
            }
        }
    }
}

export const TickerShared = new Ticker();
TickerShared.autoStart = false;
TickerShared.maxFPS = 30;


