import { getSource } from '../../utils/Utils';
import { GifFrame, GifFrames } from '../../utils/GifFrames';
import { GifSequence } from '../../utils/GifSequence';
export class GifSprite extends vf.Sprite {

    static EVENT = {
        FRAME_CHANGED: 'frameChanged',
        PLAY: 'play',
        STOP: 'stop',
        COMPLETE: 'complete',
        END: 'end'
    };

    private _frames: GifFrames;

    private _sequences: { [key: string]: GifSequence } = {};

    private _currentSequence: GifSequence;

    private _currentFrame: any;


    constructor(resource: vf.LoaderResource | string | GifFrames) {
        super();
        // eslint-disable-next-line no-debugger
        // debugger;
        if (typeof resource === 'string') {
            resource = getSource(resource);
            this._frames = (resource as any)['frames'];
        } else if (resource && ("gif" in resource) && "frames" in resource) {
            this._frames = resource;
        } else {
            this._frames = (resource as any)['frames'];
        }

        const allFrames = [];
        for (let i = 0; i < this._frames.numFrames; ++i) {
            allFrames.push(i);
        }
        this._currentSequence = this._addSequence('', this._frames.frames, this._frames.gif.loopCount);
        this.setSequence('', true);
    }

    public get gifFrames(): GifFrames {
        return this._frames;
    }

    public get totalFrames(): number {
        return this._frames.numFrames;
    }

    public getSequence(name?: string): GifSequence {
        return this._sequences[name as any] || this._sequences[''];
    }

    public addSequence(name: string, frames: Array<number>, loopCount?: number): GifSequence {
        return this._addSequence(name, frames.map(f => this._frames.getFrame(f)), loopCount as any);
    }

    private _addSequence(name: string, frames: Array<GifFrame>, loopCount: number): GifSequence {
        const seq = new GifSequence(this, name, frames, loopCount);
        this._sequences[name] = seq;
        return seq;
    }

    public setSequence(name: string, play?: boolean) {
        const wasPlaying = this._currentSequence.playing;
        if (play === undefined) {
            play = wasPlaying;
        }
        this._currentSequence.stop(true);
        this._currentSequence = this.getSequence(name);
        this.setGifFrame(this._currentSequence.frames[0]);
        if (!(play && this._currentSequence.play())) {
            wasPlaying && this.emit(GifSprite.EVENT.STOP, this);
        }
    }

    public destroy() {
        this.stop();
        super.destroy();
    }

    public play(): void {
        this._currentSequence.play();
    }

    public stop(): void {
        this._currentSequence.stop();
    }

    public setFrame(frame: number): void {
        this.setGifFrame(this._frames.getFrame(frame));
    }

    public setGifFrame(frame: GifFrame): void {
        if (this._currentFrame != frame) {
            this._currentFrame = frame;
            this.texture = frame ? frame.texture : null as any;
            this.emit(GifSprite.EVENT.FRAME_CHANGED, this);
        }
    }

    public gotoAndStop(frame: number): void {
        this.setSequence('', false);
        this.setFrame(frame);
    }

    public gotoAndPlay(frame: number): void {
        this.setSequence('', true);
        this.setFrame(frame);
    }

    public get currentFrame(): number {
        return this._currentFrame ? this._currentFrame.index : 0;
    }

    public get currentGifFrame(): GifFrame {
        return this._currentFrame;
    }

    public get currentSequence(): GifSequence {
        return this._currentSequence;
    }

    public get playing(): boolean {
        return this._currentSequence.playing;
    }

    public get speedScale(): number {
        return this._currentSequence.speedScale;
    }

    public set speedScale(value: number) {
        this._currentSequence.speedScale = value;
    }
}

