import { DisplayObject } from "../../core/DisplayObject";
import { Interaction, Utils } from "../../UI";
import { Container } from "../Container";
import { Graphics } from "../Graphics";
import { Rect } from "../Rect";

export enum BoundsEventType {
    boundsScale = 'boundsScale',   //
    boundsAngle = 'boundsAngle',    //
    selectedBoundsClicked = 'selectedBoundsClicked',  //  
}

export class SelectedBounds extends DisplayObject {
    constructor() {
        super()
        this.addSelectedShapeRect();
        this.interactabled = true;
        this.container.hitArea = new vf.Rectangle(0, 0, 2000, 2000);
        this.on('move', this.onMove);
        this._lastPos = new vf.Point();
    }

    private _boundsRect?: Graphics;  //
    private _selectedRect?: Rect;  //
    private _lastPos: vf.Point;
    private _bounds: vf.Rectangle = new vf.Rectangle();
    private _uiContainer?: Container;
    private _cornerContainer?: Container;   //

    private onMove(e: Interaction.InteractionEvent) {
        if (this._selectedRect && this._boundsRect) {
            let pos = this.container.toLocal(e.data.global);
            const len = Utils.pointDistance(this._lastPos, pos);
            if (len < 10) return;
            let offx = this._lastPos.x - pos.x;
            let offy = this._lastPos.y - pos.y;
            switch (parseInt(this._selectedRect.id)) {
                case 0: {
                    //
                    let scaleX = (this._bounds.width + offx) / this._bounds.width;
                    let scaleY = (this._bounds.height + offy) / this._bounds.height;
                    this.emit(BoundsEventType.boundsScale, scaleX, scaleY);
                }
                    break;
                case 1: {
                    let scaleY = (this._bounds.height + offy) / this._bounds.height;
                    this.emit(BoundsEventType.boundsScale, 1, scaleY);
                }
                    break;
                case 2: {
                    let scaleX = (this._bounds.width - offx) / this._bounds.width;
                    let scaleY = (this._bounds.height + offy) / this._bounds.height;
                    this.emit(BoundsEventType.boundsScale, scaleX, scaleY);
                }
                    break;
                case 3: {
                    let scaleX = (this._bounds.width - offx) / this._bounds.width;
                    this.emit(BoundsEventType.boundsScale, scaleX, 1);
                }
                    break;
                case 4: {
                    let scaleX = (this._bounds.width - offx) / this._bounds.width;
                    let scaleY = (this._bounds.height - offy) / this._bounds.height;
                    this.emit(BoundsEventType.boundsScale, scaleX, scaleY);
                }
                    break;
                case 5: {
                    let scaleY = (this._bounds.height - offy) / this._bounds.height;
                    this.emit(BoundsEventType.boundsScale, 1, scaleY);
                }
                    break;
                case 6: {
                    let scaleX = (this._bounds.width + offx) / this._bounds.width;
                    let scaleY = (this._bounds.height - offy) / this._bounds.height;
                    this.emit(BoundsEventType.boundsScale, scaleX, scaleY);
                }
                    break;
                case 7: {
                    let scaleX = (this._bounds.width + offx) / this._bounds.width;
                    this.emit(BoundsEventType.boundsScale, scaleX, 1);
                }
                    break;
                case 8: {
                    // let offy = pos.y - (this._uiContainer as any).y;
                    // let offx = pos.x - (this._uiContainer as any).x;
                    // let angle = Math.tan(offy / offx) * 180 / Math.PI;
                    // console.log(angle);
                    // this.emit('boundsAngle', angle);
                    let origin = new vf.Point(this._uiContainer?.x, this._uiContainer?.y);
                    let angle = this.measureAngle(origin, this._lastPos, pos);
                    this.emit(BoundsEventType.boundsAngle, angle);
                }
                    break;
                default:
                    break;
            }

            this._lastPos = pos;
        }
    }

    /**
     * 
     */
    private measureAngle(origin: vf.Point, point1: vf.Point, point2: vf.Point) {
        let vector1x = origin.x - point1.x;
        let vector1y = origin.y - point1.y;
        let vector2x = origin.x - point2.x;
        let vector2y = origin.y - point2.y;
        let t = ((vector1x) * (vector2x) + (vector1y) * (vector2y)) / (Math.sqrt(Math.pow(vector1x, 2) + Math.pow(vector1y, 2)) * Math.sqrt(Math.pow(vector2x, 2) + Math.pow(vector2y, 2)));
        let angle = Math.acos(t) * 180 / Math.PI;
        if (point2.y > origin.y) {
            if (point2.x > point1.x) {
                angle = 0 - angle;
            }
        }
        else {
            if (point2.x < point1.x) {
                angle = 0 - angle;
            }
        }
        return angle;
    }

    /**
     * 8
     */
    private addSelectedShapeRect() {

        let container = new Container();
        this._uiContainer = container;
        this.addChild(container);

        let boundsRect = new Graphics();
        this._boundsRect = boundsRect;
        container.addChild(boundsRect);

        let cornerContainer = new Container();
        container.addChild(cornerContainer);
        this._cornerContainer = cornerContainer;

        for (let i = 0; i < 9; ++i) {
            let rect = new Rect();
            rect.width = rect.height = 10;
            rect.anchorX = rect.anchorY = 0.5;
            rect.id = i.toString();
            rect.lineWidth = 1;
            rect.lineColor = 0x000088;
            rect.color = 0xffffff;
            cornerContainer.addChild(rect);

            rect.interactabled = true;
            rect.syncabled = false;
            rect.on('down', (e: Interaction.InteractionEvent) => {
                this._selectedRect = rect;
                this._lastPos = this.container.toLocal(e.data.global);
                this.emit(BoundsEventType.selectedBoundsClicked, true);
            })
            rect.on('up', () => {
                this._selectedRect = undefined;
                this.emit(BoundsEventType.selectedBoundsClicked, false);
            })
        }
    }

    private getBoundsPosList(bounds: vf.Rectangle) {
        let posList = [];
        posList.push(bounds.x);
        posList.push(bounds.y);
        posList.push(bounds.x + bounds.width * 0.5);
        posList.push(bounds.y);
        posList.push(bounds.x + bounds.width);
        posList.push(bounds.y);
        posList.push(bounds.x + bounds.width);
        posList.push(bounds.y + bounds.height * 0.5);
        posList.push(bounds.x + bounds.width);
        posList.push(bounds.y + bounds.height);
        posList.push(bounds.x + bounds.width * 0.5);
        posList.push(bounds.y + bounds.height);
        posList.push(bounds.x);
        posList.push(bounds.y + bounds.height);
        posList.push(bounds.x);
        posList.push(bounds.y + bounds.height * 0.5);

        posList.push(bounds.x + bounds.width * 0.5);
        posList.push(bounds.y - 30);
        return posList;
    }

    /**
     * 
     */
    public show(parent: DisplayObject, bounds: vf.Rectangle, scaleX: number = 1, scaleY: number = 1, angle: number = 0, showCorner: boolean = true) {
        parent.addChild(this);
        this.update(parent, bounds, scaleX, scaleY, angle, showCorner);
    }

    /**
     * 
     */
    public update(parent: DisplayObject, bounds: vf.Rectangle, scaleX: number = 1, scaleY: number = 1, angle: number = 0, showCorner: boolean = true) {
        if (this.parent !== parent) {
            this.parent && this.parent.removeChild(this);
            parent.addChild(this);
        }

        bounds.x -= bounds.width * (scaleX - 1) * 0.5;
        bounds.y -= bounds.height * (scaleY - 1) * 0.5;
        bounds.width *= scaleX;
        bounds.height *= scaleY;

        if (this._uiContainer) {
            this._uiContainer.x = bounds.x + bounds.width * 0.5;
            this._uiContainer.y = bounds.y + bounds.height * 0.5;
        }

        bounds.x = -bounds.width * 0.5;
        bounds.y = -bounds.height * 0.5;

        this._bounds = bounds;

        //
        if (this._boundsRect) {
            let graphic = this._boundsRect.graphics;
            graphic.clear();
            graphic.lineStyle(1, 0x000088, 1, undefined, undefined, 'dash');
            graphic.moveTo(bounds.x, bounds.y);
            graphic.lineTo(bounds.x + bounds.width, bounds.y);
            graphic.lineTo(bounds.x + bounds.width, bounds.y + bounds.height);
            graphic.lineTo(bounds.x, bounds.y + bounds.height);
            graphic.lineTo(bounds.x, bounds.y);
        }

        if (showCorner) {
            this._cornerContainer && (this._cornerContainer.visible = true);
            //
            let posList = this.getBoundsPosList(bounds);

            for (let i = 0; i < posList.length; i += 2) {
                let rect = this._cornerContainer?._getChildById(i * 0.5 + '') as Rect;
                rect.x = posList[i];
                rect.y = posList[i + 1];
            }
        }
        else {
            this._cornerContainer && (this._cornerContainer.visible = false);
        }

        this._uiContainer && (this._uiContainer.rotation = angle);
    }

    /**
     * 
     */
    public hide() {
        this.parent && this.parent.removeChild(this);
    }
}