
const segment = /([astvzqmhlc])([^astvzqmhlc]*)/ig;
const number = /-?[0-9]*\.?[0-9]+(?:e[-+]?\d+)?/ig
const TAU = Math.PI * 2;
const LEN = { a: 7, c: 6, h: 1, l: 2, m: 2, q: 4, s: 4, t: 2, v: 1, z: 0 }

//
export function parseValues(args: any) {
    const numbers = args.match(number)
    return numbers ? numbers.map(Number) : []
}

export function vectorAngle(ux: any, uy: any, vx: any, vy: any) {
    const sign = (ux * vy - uy * vx < 0) ? -1 : 1;
    const umag = Math.sqrt(ux * ux + uy * uy);
    const vmag = Math.sqrt(ux * ux + uy * uy);
    const dot = ux * vx + uy * vy;

    let div = dot / (umag * vmag);

    if (div > 1) {
        div = 1
    }

    if (div < -1) {
        div = -1;
    }

    return sign * Math.acos(div);
}

export function getArcCenter(px: number,
    py: number,
    cx: number,
    cy: number,
    rx: number,
    ry: number,
    largeArcFlag: number,
    sweepFlag: number,
    sinphi: number,
    cosphi: number,
    pxp: number,
    pyp: number
) {
    const rxsq = Math.pow(rx, 2);
    const rysq = Math.pow(ry, 2);
    const pxpsq = Math.pow(pxp, 2);
    const pypsq = Math.pow(pyp, 2);

    let radicant = (rxsq * rysq) - (rxsq * pypsq) - (rysq * pxpsq);

    if (radicant < 0) {
        radicant = 0;
    }

    radicant /= (rxsq * pypsq) + (rysq * pxpsq);
    radicant = Math.sqrt(radicant) * (largeArcFlag === sweepFlag ? -1 : 1);

    const centerxp = radicant * rx / ry * pyp;
    const centeryp = radicant * -ry / rx * pxp;

    const centerx = cosphi * centerxp - sinphi * centeryp + (px + cx) / 2;
    const centery = sinphi * centerxp + cosphi * centeryp + (py + cy) / 2;

    const vx1 = (pxp - centerxp) / rx;
    const vy1 = (pyp - centeryp) / ry;
    const vx2 = (-pxp - centerxp) / rx;
    const vy2 = (-pyp - centeryp) / ry;

    const ang1 = vectorAngle(1, 0, vx1, vy1);
    let ang2 = vectorAngle(vx1, vy1, vx2, vy2);

    if (sweepFlag === 0 && ang2 > 0) {
        ang2 -= TAU;
    }

    if (sweepFlag === 1 && ang2 < 0) {
        ang2 += TAU;
    }

    return [centerx, centery, ang1, ang2];
}


export function approxUnitArc(ang1: number, ang2: number) {
    const a = 4 / 3 * Math.tan(ang2 / 4);

    const x1 = Math.cos(ang1);
    const y1 = Math.sin(ang1);
    const x2 = Math.cos(ang1 + ang2);
    const y2 = Math.sin(ang1 + ang2);

    return [
        {
            x: x1 - y1 * a,
            y: y1 + x1 * a,
        },
        {
            x: x2 + y2 * a,
            y: y2 - x2 * a,
        },
        {
            x: x2,
            y: y2,
        },
    ];
}
export function mapToEllipse(data: { x: number; y: number }, rx: number, ry: number, cosphi: number, sinphi: number, centerx: number, centery: number) {
    data.x *= rx;
    data.y *= ry;

    const xp = cosphi * data.x - sinphi * data.y;
    const yp = sinphi * data.x + cosphi * data.y;

    return {
        x: xp + centerx,
        y: yp + centery,
    };
}

export function arcToBezier(px: number, py: number, cx: number, cy: number, rx: number, ry: number, xAxisRotation = 0, largeArcFlag = 0, sweepFlag = 0) {
    const curves = [];

    if (rx === 0 || ry === 0) {
        return [];
    }

    const sinphi = Math.sin(xAxisRotation * TAU / 360);
    const cosphi = Math.cos(xAxisRotation * TAU / 360);

    const pxp = cosphi * (px - cx) / 2 + sinphi * (py - cy) / 2;
    const pyp = -sinphi * (px - cx) / 2 + cosphi * (py - cy) / 2;

    if (pxp === 0 && pyp === 0) {
        return [];
    }

    rx = Math.abs(rx);
    ry = Math.abs(ry);

    const lambda =
        Math.pow(pxp, 2) / Math.pow(rx, 2) +
        Math.pow(pyp, 2) / Math.pow(ry, 2);

    if (lambda > 1) {
        rx *= Math.sqrt(lambda);
        ry *= Math.sqrt(lambda);
    }

    const d = getArcCenter(
        px,
        py,
        cx,
        cy,
        rx,
        ry,
        largeArcFlag,
        sweepFlag,
        sinphi,
        cosphi,
        pxp,
        pyp
    );
    const centerx = d[0];
    const centery = d[1];
    let ang1 = d[2];
    let ang2 = d[3];

    const segments = Math.max(Math.ceil(Math.abs(ang2) / (TAU / 4)), 1);

    ang2 /= segments;

    for (let i = 0; i < segments; i++) {
        curves.push(approxUnitArc(ang1, ang2));
        ang1 += ang2;
    }

    return curves.map(curve => {
        const { x: x1, y: y1 } = mapToEllipse(curve[0], rx, ry, cosphi, sinphi, centerx, centery);
        const { x: x2, y: y2 } = mapToEllipse(curve[1], rx, ry, cosphi, sinphi, centerx, centery);
        const { x, y } = mapToEllipse(curve[2], rx, ry, cosphi, sinphi, centerx, centery);

        return { x1, y1, x2, y2, x, y };
    });
}
/*eslint no-constant-condition: ["error", { "checkLoops": false }]*/
/**
 * svg
 * cmds
(4) [Array(3), Array(8), Array(8), Array(1)]
0: (3) ["M", 280, 250]
1: (8) ["A", 200, 200, 0, 1, 1, 680, 250]
2: (8) ["A", 200, 200, 0, 1, 1, 280, 250]
3: ["Z"]
length: 4
 */
export function parser(path: any) {
    const data: any = [];
    path.replace(segment, (a: any, command: any, args: any) => {
        let type = command.toLowerCase()
        args = parseValues(args)

        // overloaded moveTo
        if (type == 'm' && args.length > 2) {
            data.push([command].concat(args.splice(0, 2)))
            type = 'l'
            command = command == 'm' ? 'l' : 'L'
        }
        /*eslint no-constant-condition: ["error", { "checkLoops": false }]*/
        while (true) {
            if (args.length == (LEN as any)[type]) {
                args.unshift(command)
                return data.push(args)
            }
            if (args.length < (LEN as any)[type]) throw new Error('malformed path data')
            data.push([command].concat(args.splice(0, (LEN as any)[type])))
        }
    })
    return data;
}

export function q2b(x1: number, y1: number, x2: number, y2: number, x3: number, y3: number) {
    return [x1, y1, (x1 + 2 * x2) / 3, (y1 + 2 * y2) / 3, (x3 + 2 * x2) / 3, (y3 + 2 * y2) / 3, x3, y3]
}

/**
 * svgpath
 * 
 * 
    M = moveto
    L = lineto
    H = horizontal lineto
    V = vertical lineto
    C = curveto
    S = smooth curveto
    Q = quadratic Belzier curve
    T = smooth quadratic Belzier curveto
    A = elliptical Arc
    Z = closepath
    ()
 */

export function parseSvgData(path: string) {
    const cmds = parser(path),
        shapes = [],
        len = cmds.length;
    let preX = 0,
        preY = 0,
        j = 0,
        current: any = [],
        closeX,
        closeY,
        preCX = 0,
        preCY = 0,
        sLen,
        curves: any,
        lastCurve

    for (; j < len; j++) {
        const item = cmds[j]
        const action = item[0]
        const preItem = cmds[j - 1]

        switch (action) {
            case 'm':
                sLen = shapes.length
                shapes[sLen] = []
                current = shapes[sLen]
                preX = preX + item[1]
                preY = preY + item[2]
                break
            case 'M':

                sLen = shapes.length
                shapes[sLen] = []
                current = shapes[sLen]
                preX = item[1]
                preY = item[2]
                break

            case 'l':
                if (item[1] !== 0 || item[2] !== 0) {
                    current.push([preX, preY, preX, preY, preX, preY, preX + item[1], preY + item[2]])
                    preX += item[1]
                    preY += item[2]
                }
                break

            case 'L':
                if (preX !== item[1] || preY !== item[2]) {
                    current.push([preX, preY, item[1], item[2], item[1], item[2], item[1], item[2]])
                    preX = item[1]
                    preY = item[2]
                }
                break

            case 'h':

                current.push([preX, preY, preX, preY, preX, preY, preX + item[1], preY])
                preX += item[1]
                break


            case 'H':
                current.push([preX, preY, item[1], preY, item[1], preY, item[1], preY])
                preX = item[1]
                break

            case 'v':
                current.push([preX, preY, preX, preY, preX, preY, preX, preY + item[1]])
                preY += item[1]
                break

            case 'V':
                current.push([preX, preY, preX, item[1], preX, item[1], preX, item[1]])
                preY = item[1]
                break


            case 'C':

                current.push([preX, preY, item[1], item[2], item[3], item[4], item[5], item[6]])
                preX = item[5]
                preY = item[6]
                break
            case 'S':
                if (preItem[0] === 'C' || preItem[0] === 'c') {
                    current.push([preX, preY, preX + preItem[5] - preItem[3], preY + preItem[6] - preItem[4], item[1], item[2], item[3], item[4]])
                } else if (preItem[0] === 'S' || preItem[0] === 's') {
                    current.push([preX, preY, preX + preItem[3] - preItem[1], preY + preItem[4] - preItem[2], item[1], item[2], item[3], item[4]])

                }
                preX = item[3]
                preY = item[4]
                break

            case 'c':
                current.push([preX, preY, preX + item[1], preY + item[2], preX + item[3], preY + item[4], preX + item[5], preY + item[6]])
                preX = preX + item[5]
                preY = preY + item[6]
                break
            case 's':
                if (preItem[0] === 'C' || preItem[0] === 'c') {

                    current.push([preX, preY, preX + preItem[5] - preItem[3], preY + preItem[6] - preItem[4], preX + item[1], preY + item[2], preX + item[3], preY + item[4]])

                } else if (preItem[0] === 'S' || preItem[0] === 's') {
                    current.push([preX, preY, preX + preItem[3] - preItem[1], preY + preItem[4] - preItem[2], preX + item[1], preY + item[2], preX + item[3], preY + item[4]])
                }

                preX = preX + item[3]
                preY = preY + item[4]

                break
            case 'a':
                curves = arcToBezier(
                    preX,
                    preY,
                    preX + item[6],
                    preY + item[7],
                    item[1],
                    item[2],
                    item[3],
                    item[4],
                    item[5],

                )
                lastCurve = curves[curves.length - 1]


                curves.forEach((curve: any, index: any) => {
                    if (index === 0) {
                        current.push([preX, preY, curve.x1, curve.y1, curve.x2, curve.y2, curve.x, curve.y])
                    } else {
                        current.push([curves[index - 1].x, curves[index - 1].y, curve.x1, curve.y1, curve.x2, curve.y2, curve.x, curve.y])
                    }
                })

                preX = lastCurve && lastCurve.x
                preY = lastCurve && lastCurve.y

                break

            case 'A':

                curves = arcToBezier(
                    preX,
                    preY,
                    item[6],
                    item[7],
                    item[1],
                    item[2],
                    item[3],
                    item[4],
                    item[5],

                )
                lastCurve = curves[curves.length - 1]


                curves.forEach((curve: any, index: any) => {
                    if (index === 0) {
                        current.push([preX, preY, curve.x1, curve.y1, curve.x2, curve.y2, curve.x, curve.y])
                    } else {
                        current.push([curves[index - 1].x, curves[index - 1].y, curve.x1, curve.y1, curve.x2, curve.y2, curve.x, curve.y])
                    }
                })

                preX = lastCurve && lastCurve.x
                preY = lastCurve && lastCurve.y

                break
            case 'Q':
                current.push(q2b(preX, preY, item[1], item[2], item[3], item[4]))
                preX = item[3]
                preY = item[4]

                break
            case 'q':
                current.push(q2b(preX, preY, preX + item[1], preY + item[2], item[3] + preX, item[4] + preY))
                preX += item[3]
                preY += item[4]
                break

            case 'T':

                if (preItem[0] === 'Q' || preItem[0] === 'q') {
                    preCX = preX + preItem[3] - preItem[1]
                    preCY = preY + preItem[4] - preItem[2]
                    current.push(q2b(preX, preY, preCX, preCY, item[1], item[2]))


                } else if (preItem[0] === 'T' || preItem[0] === 't') {
                    current.push(q2b(preX, preY, preX + preX - preCX, preY + preY - preCY, item[1], item[2]))
                    preCX = preX + preX - preCX
                    preCY = preY + preY - preCY
                }

                preX = item[1]
                preY = item[2]
                break


            case 't':
                if (preItem[0] === 'Q' || preItem[0] === 'q') {
                    preCX = preX + preItem[3] - preItem[1]
                    preCY = preY + preItem[4] - preItem[2]
                    current.push(q2b(preX, preY, preCX, preCY, preX + item[1], preY + item[2]))


                } else if (preItem[0] === 'T' || preItem[0] === 't') {
                    current.push(q2b(preX, preY, preX + preX - preCX, preY + preY - preCY, preX + item[1], preY + item[2]))
                    preCX = preX + preX - preCX
                    preCY = preY + preY - preCY
                }

                preX += item[1]
                preY += item[2]
                break

            case 'Z':
            case 'z':
                closeX = current[0][0];
                closeY = current[0][1];

                if (preX !== closeX || preY !== closeY) {
                    current.push([preX, preY, closeX, closeY, closeX, closeY, closeX, closeY])
                }
                break
        }
    }

    return shapes
}

export function toSVGString(shapes: any) {
    /**/
    //var shapes = JSON.parse(JSON.stringify(shapes||[]));
    /*
    * */
    return shapes.map((shape: any) => {
        shape.forEach((point: any, idx: any) => {
            if (!idx) {
                /*
                 * M,C
                 * */
                point.splice(2, 0, "C");
                point.unshift("M");
            } else {
                /*
                 * ,
                 * */
                point.splice(0, 2, "C");
            }
        });
        return shape.map((point: any) => {
            return point.join(" ");
        }).join("");
    }).join("")

}