import { updateTextureFill } from '../core/plugs/LineGradient';
import { GraphBase } from './private/GraphBase'

/**
 * 
 * 
 *  lineWidth  color 
 * 
 */
export class Ellipse extends GraphBase {
    public constructor() {
        super();
    }

    public drawGraph() {
        const graphics = this.graphics;
        graphics.clear();
        if (this._lineWidth < 0) {
            console.log("0");
            return;
        }
        graphics.lineStyle(this._lineWidth, this._lineColor, this._lineAlpha, 0.5, false, this.lineType);
        if (this.color) {
            graphics.beginFill(this._color, this._fillAlpha);
        } else {
            updateTextureFill(this.graphics,this);
        }

        const centerX = (this._anchorX ? -this._anchorX * this.width : 0) + this.width * 0.5;
        const centerY = (this._anchorY ? -this._anchorY * this.height : 0) + this.height * 0.5;
        graphics.drawEllipse(centerX, centerY, this.width * 0.5, this.height * 0.5);
        graphics.endFill();
    }

}