import { DisplayObject } from "../core/DisplayObject";
import { GraphicsCore } from "./private/GraphicsCore";


/**
 * 
 * 
 * @example let graphics = new vf.gui.Graphics();
 * 
 */
export class Graphics extends DisplayObject {

    public constructor(geometry?: vf.GraphicsGeometry | undefined) {
        super();
        this.graphics = new GraphicsCore(geometry);
        this.container.addChild(this.graphics);
    }

    public readonly graphics: GraphicsCore;

}