import { DisplayObject } from "../core/DisplayObject";
import { MaskSprite } from "../core/MaskSprite";
import { getTexture } from "../utils/Utils";
import { ComponentEvent } from "../interaction/Index";
import { GifSprite } from "./private/GifSprite";
import { GifFrames } from "../utils/GifFrames";

/**
 * 
 * 
 * @example let image = new vf.gui.Image();
 * 
 */
export class Image extends DisplayObject implements MaskSprite {


    public constructor() {
        super();
    }

    /**  */
    public maskSprite() {
        this.srcSystem();
        this.commitProperties();
        return this._sprite as vf.Sprite;
    }

    protected _sprite: vf.Sprite | vf.TilingSprite | vf.NineSlicePlane | GifSprite | undefined;
    protected _texture: vf.Texture | undefined;
    protected _source: number | string | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | undefined;

    /**
     * 
     */
    private _src: number | string | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | undefined;
    private _oldSrc?: any;
    public get src(): number | string | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | undefined {
        return this._src;
    }
    public set src(value: number | string | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | undefined) {
        if (value === this._src) {
            return;
        }
        this._src = value;
        this.srcSystem();
    }

    private _isGif = false;

    private _scale9Grid?: number[] | any[] | undefined;
    private _oldScale9Grid?: any;
    /**
     * 
     * 
     * fillMode = scale [leftWidth,rightWidth,topHeight,bottomHeight]
     * 
     * fillMode = repeat [scalex,scaley,x,y]
     */
    public get scale9Grid() {
        return this._scale9Grid;
    }
    public set scale9Grid(value) {
        if (value === this._scale9Grid) {
            return;
        }
        this._scale9Grid = value;
        this.invalidateProperties();
    }


    private _fillMode?: "no-repeat" | "repeat" | "scale" = "no-repeat";
    private _oldFillMode?: any;
    /**
     * 
     * scalescale9Grid
     */
    public get fillMode() {
        return this._fillMode;
    }
    public set fillMode(value) {
        if (value === this._fillMode) {
            return;
        }
        this._fillMode = value;
        this.invalidateProperties();
    }

    /**
     *  0-1
     */
    public get anchorX() {
        return this._anchorX;
    }
    public set anchorX(value) {
        if (value === this._anchorX) {
            return;
        }
        this._anchorX = value;
        this.invalidateProperties();
    }
    /**
     *  0-1
     */
    public get anchorY() {
        return this._anchorY;
    }
    public set anchorY(value) {
        if (value === this.anchorY) {
            return;
        }
        this._anchorY = value;
        this.invalidateProperties();
    }

    public setSpeiteSize(unscaledWidth?: number, unscaledHeight?: number) {
        const sprite = this._sprite;

        if (sprite) {
            if (unscaledWidth)
                sprite.width = unscaledWidth;
            if (unscaledHeight)
                sprite.height = unscaledHeight;
            this.setActualSize(sprite.width, sprite.height, false);
        }
    }

    protected measure(): void {

        if (this._texture) {
            this.setMeasuredSize(this._texture.width, this._texture.height);
        } else {
            this.setMeasuredSize(0, 0);
        }

    }

    protected srcSystem() {

        if (this._oldSrc === this._src) {
            return;
        }
        this._oldSrc = this._src;
        if (this._src === undefined || this._src === '') {
            if (this._sprite && this._texture) {
                this._sprite.destroy();
                this._sprite = undefined;
                this._oldFillMode = undefined;
                this._oldScale9Grid = undefined;
                this._texture.baseTexture.dispose();
                this._texture.removeAllListeners && this._texture.removeAllListeners();
                this._texture = undefined;
            }
            return;
        }
        if (this._texture && this._texture.removeAllListeners) {
            this._texture.removeAllListeners();
        }
        let texture = this._texture = getTexture(this.src);

        if (!texture) return;  //by ziye+
        this._isGif = false;
        if (texture["extension"] && texture["extension"] === 'gif') {
            this._isGif = true;
            texture = texture.texture;
        }
        if (texture.frame.width > 1 && texture.frame.height > 1) {
            this.invalidateProperties();
            this.invalidateSize();
            this.invalidateDisplayList(); // todo: 
        }
        texture.once("update", () => {
            this.invalidateProperties();
            this.invalidateSize();
            this.invalidateDisplayList(); // todo: 
            this.emit(ComponentEvent.COMPLETE, this);
        }, this);
    }

    protected commitProperties(): void {

        if (this._texture === undefined) {
            return;
        }
        let sprite = this._sprite;
        const oldSprite = this._sprite;
        const texture = this._texture;

        if (((this._oldFillMode !== this._fillMode || (oldSprite instanceof GifSprite)) && this._isGif === false)) {
            this._oldFillMode = this._fillMode;
            try {
                if (this.fillMode === "no-repeat") {
                    sprite = new vf.Sprite(texture);
                } else if (this.fillMode === "repeat") {
                    sprite = new vf.TilingSprite(texture);
                } else if (this.fillMode === "scale") {
                    sprite = new vf.NineSlicePlane(texture);
                }
            } catch (e) {
                sprite = vf.Sprite.from(texture);
            }
        } else if ((!(oldSprite instanceof GifSprite) || !oldSprite) && this._isGif === true) {

            const resource = (this._texture as any);

            if (resource.data && this._isGif === true) {
                const frames = new GifFrames(resource);

                (resource as any)['frames'] = frames;
                resource.texture = frames.getFrame(0).texture;
            }
            sprite = new GifSprite(resource);
        }

        if (sprite !== oldSprite) {
            this._oldScale9Grid = undefined;
            if (oldSprite && oldSprite !== sprite) {
                this.container.removeChild(oldSprite);
                if (sprite) {
                    sprite.width = oldSprite.width;
                    sprite.height = oldSprite.height;
                }
                oldSprite.removeAllListeners();
                oldSprite.destroy();
            }
            if (sprite && !sprite.parent) {
                this._sprite = this.container.addChild(sprite);
            }
        }
        if (sprite === undefined) {
            return;
        }

        if (sprite.texture !== texture && this._isGif === false) {
            sprite.texture = texture;
        }
        if (sprite.tint !== this.tint) {
            sprite.tint = this.tint;
        }
        if (this.blendMode) {
            sprite.blendMode = this.blendMode;
        }
        if (this._oldScale9Grid !== this._scale9Grid) {
            this._oldScale9Grid = this._scale9Grid;
            let temp = this._scale9Grid;
            if (temp === undefined) {
                temp = [undefined, undefined, undefined, undefined];
            }
            if (sprite instanceof vf.TilingSprite) {
                sprite.tileScale.set(temp[0] || 1, temp[1] || 1);
                sprite.tilePosition.set(temp[2], temp[3]);
            } else if (sprite instanceof vf.NineSlicePlane) {
                sprite.leftWidth = temp[0] || 10;
                sprite.rightWidth = temp[1] || 10;
                sprite.topHeight = temp[2] || 10;
                sprite.bottomHeight = temp[3] || 10;
            }
        }
        if (this._anchorX || this._anchorY) {

            sprite.y = -Math.floor(sprite.height * this._anchorY);
            sprite.x = -Math.floor(sprite.width * this._anchorX);
        } else {
            sprite.x = 0;
            sprite.y = 0;
        }
    }

    protected updateDisplayList(unscaledWidth: number, unscaledHeight: number) {
        if (unscaledWidth === 0 && unscaledHeight === 0) {
            return;
        }
        if (unscaledWidth === 1 && unscaledHeight === 1) {
            return;
        }
        const sprite = this._sprite;
        if (sprite) {
            sprite.width = unscaledWidth;
            sprite.height = unscaledHeight;
            if (this._anchorX || this._anchorY) {
                sprite.y = -Math.floor(sprite.height * this._anchorY);
                sprite.x = -Math.floor(sprite.width * this._anchorX);
            } else {
                sprite.x = 0;
                sprite.y = 0;
            }
            super.updateDisplayList(unscaledWidth, unscaledHeight);
        }

    }

    public release() {
        super.release();
        this.offAll(ComponentEvent.COMPLETE);
        if (this._sprite && this._sprite.parent) {
            this._sprite.removeAllListeners();
            this._sprite.parent.removeChild(this._sprite).destroy();
        }
        this._sprite = undefined;
        if (this._texture) {
            this._texture.removeAllListeners();
            // this._texture.destroy(); //
            this._texture = undefined;
        }
        this._source = undefined;
        this._src = undefined;
        this._oldSrc = undefined;
    }
}