import { debug } from "./Utils";

/**
 * 
 * @param stage
 * @param parame
 */
export function setInteractiveElement(stage: vf.gui.Stage, parame: {className: string; defaultPointer: string; htmlElement?: HTMLElement;resolution?: number}): void {
    stage.originalEventPreventDefault = true;
    let element = document.getElementById('drawCanvas');

    if (parame.className && parame.className !== 'canvas-container') { // className 'canvas-container'
        element = document.getElementsByClassName(parame.className)[0] as any;
        if (element) {
            // slider1
            // (element as any).width = element.offsetWidth;
            // (element as any).height = element.offsetHeight;
        }
    }

    if(parame.htmlElement){
        element = parame.htmlElement;
    }

    if (element === null || element === undefined) {
        const canvasContainer = document.getElementsByClassName('canvas-container')[0];

        if (canvasContainer && canvasContainer.children[1]) {
            element = canvasContainer.children[1] as HTMLElement;
        }
    }

    if (element === null || element === undefined) {
        // \
        stage.sendToPlayer({
            code: 'E0002',
            level: 'error',
            target: 'setTargetElement',
            data: [parame],
        });

        return;
    }

    const renderer = stage.app.renderer as vf.Renderer;
    const interaction = renderer.plugins.interaction;

    interaction.setTargetElement(element, parame.resolution || renderer.resolution);
    interaction.cursorStyles.default = element.style.cursor;
    if (parame.defaultPointer) {
        interaction.cursorStyles.pointer = parame.defaultPointer;
    }
   
    element.style.display = 'none';
    setTimeout(() => { if (element) { element.style.display = ''; } }, 60); // BUG 
}
