import { MaskSprite } from "../core/MaskSprite";
import { getTexture, getDisplayObject } from "../utils/Utils";

export class Mask {

    constructor(parent: any) {
        this.parent = parent;
    }

    private parent: any;
    private _image?: string | vf.Graphics | MaskSprite;
    private _oldImage: any;
    private _mask: any;
    private _invalidateFlag = false;

    private _position?: number[];
    public get position() {
        return this._position;
    }
    public set position(value) {
        this._position = value;
        this._invalidateFlag = true;
    }

    private _size: number[] | undefined;
    public get size() {
        return this._size;
    }
    public set size(value) {
        this._size = value;
        this._invalidateFlag = true;
    }

    public get image() {
        return this._image;
    }
    public set image(value) {
        if (value === this._image) {
            return;
        }
        this._image = value;
        this._invalidateFlag = true;
    }

    public update(w: number, h: number) {

        if (!this._invalidateFlag) {
            return;
        }
        this._invalidateFlag = false;
        const target = this.parent;
        const container = target.container as vf.Container;
        const image = this._image as any;
        let maskdisplay: vf.Graphics | vf.Sprite | undefined;

        if (image !== this._oldImage) {
            this._oldImage = image;
            if(this._mask && this._mask.parent){
                this._mask.parent.removeChild(this._mask);
            }

            if(image === undefined) {
                container.mask = null as any;
                return;
            } 
            this._mask = undefined;
            if (typeof image === 'string' && image.indexOf('//') !== -1) { // http
                this._mask = maskdisplay = vf.Sprite.from(image);
            } else if (typeof image === 'string' && image.indexOf('this') !== -1) { // vf-player gui 'this#xxxx' 
                maskdisplay = getDisplayObject(this._image);
            } else if (typeof image === 'string' || typeof image === 'number') { // vf-player id, 
                const guiObject = getDisplayObject(image);
                if (typeof guiObject === 'string') {
                    this._mask = maskdisplay = vf.Sprite.from(guiObject);
                } else {
                    maskdisplay = this.getGuiObject(guiObject); 
                }
                
            } else if (typeof image === 'string' && image.indexOf('.') !== -1) { // file.xxx
                this._mask = maskdisplay = vf.Sprite.from(image);
            } else if (image instanceof vf.Graphics || image instanceof vf.Sprite) {
                this._mask = maskdisplay = image;
            } else if (image.maskSprite) { // gui
                maskdisplay = this.getGuiObject(image);
            }

            if (maskdisplay) {
                container.mask = maskdisplay;
                if (!maskdisplay.parent) {
                    container.addChild(maskdisplay);
                }
            }
        }

        const mask = container.mask as vf.Container;
        if (mask && this._size) {
            if(this._mask){
                this._mask.width = this._size[0];
                this._mask.height = this._size[1];
            }else{
                mask.width = this._size[0];
                mask.height = this._size[1];
            }
        }

        if (mask && this._position) {
            if (this._mask ) {
                this._mask.x = this._position[0] || 0;
                this._mask.y = this._position[1] || 0;
            }else {
                mask.x = this._position[0] || 0;
                mask.y = this._position[1] || 0;
            }

        }

    }

    private getGuiObject(sp: MaskSprite){
        this._mask = sp;
        if (!sp.parent) {
            this.parent.addChild(sp);
        }
        sp.validateNow();
        return sp.maskSprite() as vf.Sprite;
    }

    public release() {
        if(this.parent){
            this.parent.mask = null;
        }
        this._mask = undefined;
        this._oldImage = undefined;
        this.image = undefined;
        this.parent = undefined;
        this._invalidateFlag = false;
    }
}