import { Audio, PlayerStates } from "../display/Audio";

interface AudioGroupObject {
    groups: { [key: string]: { [value: string]: Audio } };
    values: { [key: string]: string | undefined };
}

export const _audioGroupObject: AudioGroupObject = {
    groups: {},
    values: {}
}


/* video 
* 
* checkGroups = [key]:{["value"]:cb}
*/
export function registrerAudioGroup(cb: Audio) {
    const name = cb.audioGroup;
    if (name || name === "") {
        let group = _audioGroupObject.groups[name];
        if (!group)
            group = _audioGroupObject.groups[name] = {};

        group[cb.uuid.toString()] = cb;

        // if (cb.checked)
        _audioGroupObject.values[name] = cb.uuid.toString();
    }
}

/**
 * 
 * @param cb video
 */
export function unRegistrerAudioGroup(cb: Audio) {
    if ((cb.audioGroup || cb.audioGroup === "") && _audioGroupObject.groups[cb.audioGroup]) {
        delete _audioGroupObject.groups[cb.audioGroup][cb.uuid.toString()];
        let isKey = false;
        for (const key in _audioGroupObject.groups[cb.audioGroup]) {
            if (key) isKey = true;
            break;
        }
        if (!isKey) {
            delete _audioGroupObject.groups[cb.audioGroup];
        }
        // if (cb.checked)
        _audioGroupObject.values[cb.audioGroup] = undefined;
    }
}

/** checkbox  */
export function updateAudioGroupStates(cb: Audio) {
    if (cb.audioGroup || cb.audioGroup === "") {
        const group = _audioGroupObject.groups[cb.audioGroup];
        for (const val in group) {
            const b = group[val];
            if (b !== cb && b.groupName === cb.groupName) {
                //  
                if (b) {
                    if (b.playerStates === PlayerStates.play) {
                        b.stop();
                    }
                    // if (b.playerStates === PlayerStates.end) {
                    //     console.error(" end other ");
                    //     b.stop();
                    // } else {
                    //     console.error(" pausee other ");
                    //     b.pause();
                    // }
                }
            }
        }
        _audioGroupObject.values[cb.audioGroup] = cb.uuid.toString();
    }
}
