import { DisplayObject } from "../DisplayObject";
import { DragEvent, DragDropController, InteractionEvent, ComponentEvent } from "../../interaction/Index";
import { TouchMouseEventEnum } from "../../interaction/TouchMouseEventEnum";
import { DisplayObjectAbstract } from "../DisplayObjectAbstract";
import { Stage } from "../Stage";
import { getDisplayObject, debug, toLocal, toWorldTransform, toLoacalTransform } from "../../utils/Utils";
import { SyncManager } from "../../interaction/SyncManager";

/**
 *  
 * 
 */
export class UIBaseDrag implements Lifecycle {
    /**
     * 
     */
    public constructor(target: DisplayObject) {
        this.target = target;
        target.plugs.set(UIBaseDrag.key, this);
        target.dragStopPropagation = true;
    }

    public static key = "UIBaseDrag";

    private target: DisplayObject | undefined;
    public $targetParent: DisplayObject | Stage | undefined;
    private oldInteractiveChildren = true;
    /** 
     * 
     *  @default
     */
    private dragInitialized = false;
    /** 
     * 
     * @default
    */
    private dropInitialized = false;
    /** 
     *  
     */
    private drag: DragEvent | undefined;
    /**
     * 
     * 0 
     * 1 
     * 2 
     * 3 
     * 4 
     */
    private _dragState: 0 | 1 | 2 | 3 | 4 = 0;
    /** 
     *  
     * 
     */
    private _dragPosition = new vf.Point();
    /**
     * 
     */
    private _dropPosition = new vf.Point();
    /**
     * 
     */
    private _startOffset = new vf.Point(0, 0);
    /** 
     * 
     * @default
     */
    public dragging = false;
    /** 
     * IDDragDropController 
     */
    public get dragDropEventId(): number | undefined {
        if (this.target) {
            return this.target.attach.dragDropEventId as number;
        }
    }

    /**
     * 
     * @default false
     */
    public set draggable(value: boolean) {
        if (value)
            this.initDraggable();
        else
            this.clearDraggable();
    }

    /**
     * 
     */
    public collision = false;

    /**
     * 
     * @default false
     */
    public dragBoundary = false;
    /**
     * 
     */
    public dragBounces = false;
    /**
     * 
     */
    public dragMoveCursor = 'pointer';

    /**
     * ,XY,XY
     */
    private _dragRestrictAxis?: "x" | "y";
    public get dragRestrictAxis() {
        return this._dragRestrictAxis;
    }
    public set dragRestrictAxis(value) {
        this._dragRestrictAxis = value;
        if (this.drag) {
            this.drag.dragRestrictAxis = this._dragRestrictAxis;
        }
    }

    /** 
     * 
     */
    public get dragGroup(): string {
        if (this.target) {
            return this.target.attach.dragGroup as string;
        }
        return "";
    }
    public set dragGroup(value: string) {
        if (this.target)
            this.target.attach.dragGroup = value;
    }


    /**
     * 
     */
    private _dragContainer: DisplayObjectAbstract | undefined;
    public get dragContainer() {
        return this._dragContainer;
    }
    public set dragContainer(value) {
        this._dragContainer = getDisplayObject(value, this.target);
    }

    /**
     * 
     */
    public set droppable(value: boolean | undefined) {
        if (value)
            this.initDroppable();
        else
            this.clearDroppable();
    }

    /**
     * 
     */
    private _droppableReparent: DisplayObject | undefined;
    public get droppableReparent() {
        return this._droppableReparent;
    }
    public set droppableReparent(value) {
        this._droppableReparent = getDisplayObject(value, this.target);
    }
    /**
     * 
     */
    private _dropGroup = '';
    public set dropGroup(value: string) {
        this._dropGroup = value;
        if (this.target) {
            DragDropController.addDropTarget(value, this.target);
        }
    }

    public get dropGroup() {
        return this._dropGroup;
    }

    protected clearDraggable() {
        if (this.dragInitialized) {
            this.dragInitialized = false;
            if (this.drag) {
                this.drag.stopEvent();
            }
        }

        if (this.target) {
            this.target.interactiveChildren = this.oldInteractiveChildren;
            DragDropController.remove(this.target);
        }
        this.dragging = false;
        this._dragState = 0;
    }

    private oldWorldTransform = new vf.Matrix();
    private oldLocalTransform = new vf.Matrix();

    protected initDraggable() {
        if (this.target == undefined) {
            return;
        }
        if (!this.dragInitialized) {

            this.dragInitialized = true;
            this._dragPosition.set(0, 0);

            this.drag = new DragEvent(this.target);
            this.drag.dragRestrictAxis = this._dragRestrictAxis;

            this.drag.onDragPress = (e: InteractionEvent, isPress: boolean) => {
                if (this.target == undefined) {
                    return;
                }
                const target = this.target;
                if (target.stage === undefined) {
                    return;
                }

                if (isPress) {
                    target.emit(ComponentEvent.DRAG_DOWN, target, e);
                } else {
                    target.emit(ComponentEvent.DRAG_UP, target, e);
                }
            }

            this.drag.onDragHover = (e: InteractionEvent, isHover: boolean) => {
                if (this.target == undefined) {
                    return;
                }
                const target = this.target;
                target.emit(ComponentEvent.DRAG_HOVER, target, e, isHover);
                if (isHover) {
                    target.emit(ComponentEvent.DRAG_MOUSEENTER, target, e);
                }
                else {
                    target.emit(ComponentEvent.DRAG_MOUSELEAVE, target, e);
                }
            }

            this.drag.onDragStart = (e: InteractionEvent) => {
                if (this.target == undefined) {
                    return;
                }

                this._dragState = 1;
                const target = this.target;
                this.$targetParent = target.parent;
                this.oldInteractiveChildren = target.interactiveChildren;

                if (this._dragContainer == undefined) {
                    this._dragContainer = this.target.stage;
                }
                const added = DragDropController.add(target, e);
                if (!this.dragging && added) {
                    target.emit(ComponentEvent.DRAG_START_BEFORE, target, e);
                    this.dragging = true;
                    target.interactive = false;
                    target.interactiveChildren = false;

                    this.oldWorldTransform = toWorldTransform(target).clone();
                    this.oldLocalTransform = toLoacalTransform(target).clone();

                    if (this._dragContainer) {
                        let c: DisplayObjectAbstract | undefined;
                        if (this._dragContainer instanceof DisplayObjectAbstract) {
                            c = this._dragContainer;
                        }
                        if (c && target.parent) {
                            c.addChild(target);
                            
                            target.scaleX = this.oldWorldTransform.a * (1/c.container.scale.x) / (target.container.transform as any)._cx;// this.oldWorldTransform.a;
                            target.scaleY = this.oldWorldTransform.d * (1/c.container.scale.y) / (target.container.transform as any)._sy;// this.oldWorldTransform.d ;
                            
                            const local = toLocal(new vf.Point(this.oldWorldTransform.tx , this.oldWorldTransform.ty), c);
                            target.setPosition(local.x + ((target.container.transform.pivot.x * this.oldWorldTransform.a) + (target.container.transform.pivot.y * this.oldWorldTransform.c)), local.y + ((target.container.transform.pivot.x * this.oldWorldTransform.b) + (target.container.transform.pivot.y * this.oldWorldTransform.d)));  //by ziye+
                            // target.setPosition((local.x + (target.pivotX * target.scaleX) - (target.width * target._anchorX)), (local.y + (target.pivotY * target.scaleY) - (target.height * target._anchorY)));
                        }
                    }

                    if (debug) { //debug 
                        const stage = target.stage;
                        if (stage) {
                            stage.sendToPlayer({
                                code: ComponentEvent.DRAG_START,
                                level: 'info', target: target,
                                data: [target.parent, target.x, target.y],
                                action: e.type,
                                message: 'parent,start,offset pos',
                            });
                        }
                    }

                    this._startOffset.set(target.x, target.y);
                    target.emit(ComponentEvent.DRAG_START, target, e);
                }
            };


            this.drag.onDragMove = (e: InteractionEvent, offset: vf.Point) => {
                if (this.target == undefined) {
                    return;
                }
                const target = this.target;

                if (target.stage && target.stage.app) {
                    const interaction = (target.stage.app.renderer as vf.Renderer).plugins.interaction;
                    (interaction as any).cursor = this.dragMoveCursor;
                }
                if (this.dragging && target.stage) {
                    const x = this._startOffset.x + (offset.x / target.stage.scaleX);
                    const y = this._startOffset.y + (offset.y / target.stage.scaleY);
                    const dragPosition = this._dragPosition;
                    if (this.dragRestrictAxis == "x") {
                        dragPosition.set(x, target.y);
                    } else if (this.dragRestrictAxis == "y") {
                        dragPosition.set(target.x, y);
                    } else {
                        this._dragPosition.set(x, y);
                    }
                    if (this.dragBoundary && target.parent) {
                        const {pivotX, pivotY, scaleX, scaleY, _anchorX, _anchorY, width, height, parent} = target;
                        dragPosition.x = Math.max(pivotX * scaleX + width * _anchorX * scaleX, dragPosition.x);
                        dragPosition.x = Math.min(dragPosition.x, parent.width - (width - pivotX) * scaleX + width * _anchorX * scaleX);
                        dragPosition.y = Math.max(pivotY * scaleY + height * _anchorY * scaleY, dragPosition.y);
                        dragPosition.y = Math.min(dragPosition.y, parent.height - (height - pivotY) * scaleY + height * _anchorY * scaleY);
                    }
                    if (debug) {//debug 
                        const stage = target.stage;
                        if (stage) {
                            stage.sendToPlayer({
                                code: ComponentEvent.DRAG_MOVE,
                                level: 'info',
                                target: target,
                                data: [target.parent, dragPosition.x, dragPosition.y],
                                action: e.type,
                                message: 'parent,move pos'
                            });
                        }

                    }
                    this._dragState = 2;
                    target.setPosition(dragPosition.x, dragPosition.y);
                    target.emit(ComponentEvent.DRAG_MOVE, target, e);
                }

            };

            this.drag.onDragEnd = (e: InteractionEvent) => {
                if (this.dragging && this.collision) {
                    this.checkCollision(e);
                }

                if (this.dragging) {
                    this.dragging = false;
                    //0
                    // TickerShared.addOnce(() => {
                    if (this.target == undefined) {
                        return;
                    }
                    //dragBounces
                    const target = this.target;
                    const parent = this.$targetParent;
                    target.interactive = true;
                    target.interactiveChildren = this.oldInteractiveChildren;
                    const item = DragDropController.getItem(target);
                    const dragPosition = this._dragPosition;
                    target.emit(ComponentEvent.DRAG_END_BEFORE, target, e);
                    if (item && parent) {
                        if (target.parent !== parent && target.parent) {
                            parent.container.toLocal(target.container.position, target.container.parent, dragPosition);
                            parent.addChild(target);
                            target.x = dragPosition.x;
                            target.y = dragPosition.y;
                        }
                        target.scaleX = this.oldLocalTransform.a  / (target.container.transform as any)._cx;
                        target.scaleY = this.oldLocalTransform.d  / (target.container.transform as any)._sy;

                        if (this.dragBounces) {
                            // target.x = this.oldLocalTransform.tx;
                            // target.y = this.oldLocalTransform.ty;

                            target.setPosition(this.oldLocalTransform.tx + ((target.container.transform.pivot.x * this.oldLocalTransform.a) + (target.container.transform.pivot.y * this.oldLocalTransform.c)), this.oldLocalTransform.ty + ((target.container.transform.pivot.x * this.oldLocalTransform.b) + (target.container.transform.pivot.y * this.oldLocalTransform.d)));  //by ziye+
                        }
                    }
                    if (debug) {//debug 
                        const stage = target.stage;
                        if (stage) {
                            stage.sendToPlayer({
                                code: ComponentEvent.DRAG_END,
                                level: 'info',
                                target: target,
                                data: [target.parent, target.x, target.y],
                                action: e.type,
                                message: 'parent,end pos'
                            });
                        }

                    }
                    if (target.stage && target.stage.app) {
                        target.stage.app.view.style.cursor = target.style.cursor;
                    }
                    this._dragState = 3;
                    e.data.tiltX = dragPosition.x;
                    e.data.tiltY = dragPosition.y;
                    target.emit(ComponentEvent.DRAG_END, target, e);
                    // }, this)
                }


            };
        }
    }

    protected checkCollision(e: InteractionEvent) {
        if (!this.target) return;
        const dragTarget = this.target;
        const dropTargets = DragDropController.getDropTargets(this.dragGroup);
        if (dropTargets.length == 0) return;

        const dragTargetRect = new vf.Rectangle();
        let offset = 0;
        if ((dragTarget as any).anchorX !== undefined) {
            offset = dragTarget.container.width * (dragTarget as any).anchorX - dragTarget.pivotX * dragTarget.scaleX;
        }
        dragTargetRect.x = dragTarget.x - offset;
        if ((dragTarget as any).anchorY !== undefined) {
            offset = dragTarget.container.height * (dragTarget as any).anchorY - dragTarget.pivotY * dragTarget.scaleY;
        }
        dragTargetRect.y = dragTarget.y - offset;
        dragTargetRect.width = dragTarget.container.width;
        dragTargetRect.height = dragTarget.container.height;


        for (let i = 0; i < dropTargets.length; ++i) {
            const dropTarget = dropTargets[i];
            dragTarget.parent && dragTarget.parent.container.toLocal(dropTarget.container.position, dropTarget.container.parent, this._dropPosition);
            const dropTargetRect = new vf.Rectangle();
            let offset = 0;
            if ((dropTarget as any).anchorX !== undefined) {
                offset = dropTarget.width * dropTarget.scaleX * (dropTarget as any).anchorX - dropTarget.pivotX * dropTarget.scaleX;
            }
            dropTargetRect.x = this._dropPosition.x - offset;
            if ((dropTarget as any).anchorY !== undefined) {
                offset = dropTarget.height * dropTarget.scaleY * (dropTarget as any).anchorY - dropTarget.pivotY * dropTarget.scaleY;
            }
            dropTargetRect.y = this._dropPosition.y - offset;
            dropTargetRect.width = dropTarget.width * dropTarget.scaleX;
            dropTargetRect.height = dropTarget.height * dropTarget.scaleY;
            if (this.checkBump(dragTargetRect, dropTargetRect)) {
                //
                dropTarget.dragOption.onDrop(e);
                break;
            }
        }
    }

    protected checkBump(origin: vf.Rectangle, target: vf.Rectangle): boolean {
        if (
            origin.y > target.y + target.height || // 
            origin.y + origin.height < target.y || // 
            origin.x > target.x + target.width || // 
            origin.x + origin.width < target.x // 
        ) {
            return false
        } else {
            console.log('dragAndDrop-----collision----true')
            return true
        }
    }

    protected clearDroppable() {
        if (this.target == undefined) {
            return;
        }
        const target = this.target;
        if (this.dropInitialized) {
            this.dropInitialized = false;
            target.container.off(TouchMouseEventEnum.mouseup, this.onDrop, this);
            target.container.off(TouchMouseEventEnum.touchend, this.onDrop, this);
        }

        DragDropController.removeDropTarget(target); // by ziye+
    }

    protected initDroppable() {
        if (this.target == undefined) {
            return;
        }
        const target = this.target;
        if (!this.dropInitialized) {
            this.dropInitialized = true;
            const container = target.container;
            //self = this;
            container.interactive = true;
            container.on(TouchMouseEventEnum.mouseup, this.onDrop, this);
            container.on(TouchMouseEventEnum.touchend, this.onDrop, this);

        }
    }

    public onDrop(e: InteractionEvent) {
        if (this.target == undefined) {
            return;
        }
        if (this.target.stage && this.target.stage.syncInteractiveFlag) {
            (SyncManager.getInstance(this.target.stage) as SyncManager).collectEvent(e, this.target);
        }

        const target = this.target;
        const item = DragDropController.getEventItem(e, this.dropGroup);
        if (item && item.dragOption.dragging) {
            item.dragOption.dragging = false;
            item.interactive = true;
            const dragPosition = this._dragPosition;
            const parent = item.dragOption.droppableReparent !== undefined ? item.dragOption.droppableReparent : target;
            if (parent) {
                parent.container.toLocal(item.container.position, item.container.parent, dragPosition);
                item.x = dragPosition.x;
                item.y = dragPosition.y;
                // item.scaleX = this.oldLocalTransform.a;
                // item.scaleY = this.oldLocalTransform.d;

                item.scaleX = this.oldLocalTransform.a  / (item.container.transform as any)._cx;
                item.scaleY = this.oldLocalTransform.d  / (item.container.transform as any)._sy;
                // debugger
                
                if (parent != item.parent) {
                    parent.addChild(item);
                    parent.emit(ComponentEvent.DROP_TARGET, parent, item, e);
                }
                item.dragOption.$targetParent = parent;
            }
            if (debug) {//debug 
                const stage = target.stage;
                if (stage) {
                    stage.sendToPlayer({
                        code: ComponentEvent.DRAG_TARGET,
                        level: 'info',
                        target: item,
                        data: [target.parent, item.x, item.y],
                        action: e.type,
                        message: 'drag target,item pos'
                    });
                }

            }
            if (target.stage && target.stage.app) {
                const interaction = (target.stage.app.renderer as vf.Renderer).plugins.interaction;
                (interaction as any).cursor = target.style.cursor;
            }
            this._dragState = 4;
            e.data.tiltX = dragPosition.x;
            e.data.tiltY = dragPosition.y;
            item.invalidateParentLayout();
            item.emit(ComponentEvent.DRAG_TARGET, item, e);

        }
    }

    /**
     * 
     */
    private executeDrop(e: InteractionEvent, parsentPath: string[]) {
        if (this.target && this.target.stage && parsentPath) {
            const parent = this.target.stage.getChildByPath(parsentPath);
            const item = this.target;
            item.dragOption.dragging = false;
            item.interactive = true;
            if (parent) {
                item.x = this._dragPosition.x;
                item.y = this._dragPosition.y;
                if (parent != item.parent) {
                    parent.addChild(item);
                    parent.emit(ComponentEvent.DROP_TARGET, parent, item, e);
                }
                this.$targetParent = parent as DisplayObject;
                item.emit(ComponentEvent.DRAG_TARGET, item, e);
            }
        }
    }

    load() {
        //
    }

    release() {

        this.clearDraggable();
        this.clearDroppable();

        if (this.target) {
            this.target.plugs.delete(UIBaseDrag.key);
            this.target = undefined;
            this.$targetParent = undefined;
            this.dragContainer = undefined;
        }
    }

}