import { Stage } from "../core/Stage";

export const enum ScaleMode {
    SHOW_ALL = 'showAll',
    NO_SCALE = 'noScale',
    COVER = 'cover',
    CONTAIN = 'contain',
    FIXEDHEIGHT = 'fixedHeight',
    FIXEDWIDTH = 'fixedWidth'
}
/**
 * @private
 * 
 * @param scaleMode 
 * @param screenWidth 
 * @param screenHeight 
 * @param contentWidth 
 * @param contentHeight 
 */
function calculateStageSize(
    scaleMode: ScaleMode,
    screenWidth: number,
    screenHeight: number,
    contentWidth: number,
    contentHeight: number) {
    let displayWidth = screenWidth;
    let displayHeight = screenHeight;
    let stageWidth = contentWidth;
    let stageHeight = contentHeight;
    const scaleX = (screenWidth / stageWidth) || 0;
    const scaleY = (screenHeight / stageHeight) || 0;

    switch (scaleMode) {
        case 'showAll':
            if (scaleX > scaleY) {
                displayWidth = Math.round(stageWidth * scaleY);
            }
            else {
                displayHeight = Math.round(stageHeight * scaleX);
            }
            break;
        case 'cover':
            if (scaleX > scaleY) {
                displayHeight = Math.round(stageHeight * scaleX);
            }
            else {
                displayWidth = Math.round(stageWidth * scaleY);
            }
            break;
        case 'contain':
            if (scaleX > scaleY) {
                stageWidth = Math.round(screenWidth / scaleY);
            }
            else {
                stageHeight = Math.round(screenHeight / scaleX);
            }
            break;
        case 'fixedHeight':
            displayWidth = Math.round(stageWidth * scaleY);
            break;
        case 'fixedWidth':
            displayHeight = Math.round(stageHeight * scaleX);
            break;
        default:
            displayWidth = contentWidth;
            displayHeight = contentHeight;
            stageWidth = contentWidth;
            stageHeight = contentHeight;
            break;
    }
    // 2
    if (stageWidth % 2 !== 0) {
        stageWidth += 1;
    }
    if (stageHeight % 2 !== 0) {
        stageHeight += 1;
    }
    if (displayWidth % 2 !== 0) {
        displayWidth += 1;
    }
    if (displayHeight % 2 !== 0) {
        displayHeight += 1;
    }

    return {
        stageWidth,
        stageHeight,
        displayWidth,
        displayHeight,
    };
}

export function getBoundingClientRect(dom: HTMLElement): DOMRect {
    if (dom === null) {
        throw new Error(`Error No find canvas parent dom`);
    }

    const rect = {} as any;

    rect.width = dom.offsetWidth;
    rect.height = dom.offsetHeight;
    return rect;
}

/**
 * @private
 *
 * 
 *
 * noScale 1:1
 *
 * showAll 
 *
 * contain 
 *
 * cover 
 */
export function calculateUpdatePlayerSize(scrennDiv: HTMLElement, app: vf.Application, stage: Stage, stageW: number, stageH: number, scaleMode: ScaleMode) {
    const canvas = app.view;
    const canvasScaleFactor = app.renderer.resolution;
    const isWebGl = vf.utils.isWebGLSupported();
    const top = 0;

    const clientRect = getBoundingClientRect(scrennDiv);
    const screenWidth = clientRect.width;
    const screenHeight = clientRect.height;

    stageW = vf.gui.Utils.formatRelative(stageW, screenWidth);
    stageH = vf.gui.Utils.formatRelative(stageH, screenHeight);

    const stageSize = calculateStageSize(scaleMode, screenWidth, screenHeight, stageW || canvas.width, stageH || canvas.height);

    const stageWidth = stageSize.stageWidth;
    const stageHeight = stageSize.stageHeight;
    const displayWidth = stageSize.displayWidth;
    const displayHeight = stageSize.displayHeight;

    if (canvas.style.transformOrigin) {
        canvas.style.transformOrigin = '0% 0% 0px';
    }
    else {
        canvas.style.webkitTransformOrigin = '0% 0% 0px';
    }

    const rotation = 0;

    if(scaleMode && scaleMode !== ScaleMode.NO_SCALE) {
        canvas.style.top = `${top + ((screenHeight - displayHeight) / 2)}px`;
        canvas.style.left = `${(screenWidth - displayWidth) / 2}px`;
    }

    const scalex = displayWidth / stageWidth;
    const scaley = displayHeight / stageHeight;

    let canvasScaleX = scalex * canvasScaleFactor;
    let canvasScaleY = scaley * canvasScaleFactor;

    if (!isWebGl) {
        canvasScaleX = Math.ceil(canvasScaleX);
        canvasScaleY = Math.ceil(canvasScaleY);
    }

    const m = new vf.Matrix();

    m.identity();
    m.scale(scalex / canvasScaleX, scaley / canvasScaleY);
    m.rotate(rotation * Math.PI / 180);

    canvas.style.position = 'absolute';
    if (canvas.style.transform) {
        canvas.style.transform = `matrix(${m.a},${m.b},${m.c},${m.d},${m.tx},${m.ty})`;
    }
    else {
        canvas.style.webkitTransform = `matrix(${m.a},${m.b},${m.c},${m.d},${m.tx},${m.ty})`;
    }

    app.renderer.resize(stageWidth * scalex, stageHeight * scaley);
    // canvas.style.border = '5px solid red';
    stage.container.hitArea = new vf.Rectangle(0, 0, stageWidth, stageHeight);
    stage.width = stageWidth;
    stage.height = stageWidth;
    stage.scaleX = canvasScaleX / canvasScaleFactor;
    stage.scaleY = canvasScaleY / canvasScaleFactor;

    return { width: stageWidth * scalex, height: stageHeight * scaley, scaleX: canvasScaleX, scaleY: canvasScaleY };
}