export class Timeline<T>  {

    public set defaultValue(value: T) {
        this._defaultValue = value;
        this._curValue = value;
    }
    public get curValue(): T {
        return this._curValue;
    }

    public set globalTime(v: number) {
        this._globalTime = v;
        this.tick();
        this._lastGlobalTime = v;
    }

    public frames: Array<Frame<T>> = [];
    private curTime = 0;
    public lastTime = 0;
    public loop = false;
    public totalTime = 0;
    protected _defaultValue: T = null as any;
    protected _globalTime = 0;

    protected _lastGlobalTime = 0;
    protected _lastFrame: Frame<T> | null = null as any;
    protected _curFrame: Frame<T> | null = null;
    protected _nextFrame: Frame<T> = null as any;
    protected _times = -1;

    protected _curValue: T = null as any;

    constructor() {
        //
    }

    protected getProgress(cur: number, min: number, max: number, ease: ((k: number) => number) | any): number {
        let lineProgress = 0;
        let curveProgress = 0;
        if (max === min) {
            lineProgress = 0;
        } else {
            lineProgress = (cur - min) / (max - min);
        }
        curveProgress = lineProgress;
        if (ease) {
            curveProgress = ease(lineProgress);
            // todo bezier
        }
        return curveProgress;
    }

    protected tick(): void {
        this.curTime = this._globalTime;
        if (this.loop) {
            this._times = Math.floor(this.curTime / this.totalTime);
            this.curTime = this.curTime - this.totalTime * this._times;
        }
        this.updateCurFrame();
        this.updateCurValue();
        this.lastTime = this.curTime;
    }
    protected updateCurFrame(): void {
        if (this.frames.length === 0) {
            return;
        }
        this._lastFrame = this._curFrame;
        let i = 0;
        let len = 0;
        for (i = 0, len = this.frames.length; i < len; i++) {
            if (this.frames[i].time > this.curTime) {
                if (i === 0) {
                    this._curFrame = null;
                    this._nextFrame = this.frames[i];
                } else {
                    this._curFrame = this.frames[i - 1];
                    this._nextFrame = this.frames[i];
                }
                return;
            }
        }
        this._curFrame = this.frames[this.frames.length - 1];
        this._nextFrame = this._curFrame;
    }

    protected updateCurValue(): void {
        if (this._curFrame && this._nextFrame) {
            const p = this.getProgress(this.curTime, this._curFrame.time,
                this._nextFrame.time, this._curFrame.ease);

            this._curValue = this._curFrame.getValue(p, this._nextFrame.value);
        }
    }


    public release() {
        this.frames = [];
        this.curTime = 0;
        this.lastTime = 0;
        this.loop = false;
        this.totalTime = 0;
        this._defaultValue = null as any;
        this._globalTime = 0;
        this._lastGlobalTime = 0;
        this._lastFrame = null as any;
        this._curFrame = null;
        this._nextFrame = null as any;
        this._times = -1;
    }
}


export class Frame<T> {

    public time = -1;
    public ease?: ((k: number) => number) | any
    public value: T = null as any;


    public getValue(progress: number, value: T): T {
        return null as any;
    }
}
export class NumberFrame extends Frame<number> {

    constructor() {
        super();
    }

    public getValue(progress: number, value: number): number {
        return this.value + (value - this.value) * progress;
    }
}