import { InteractionEvent } from "../event/InteractionEvent";
import { getDisplayPathById, debug } from "../utils/Utils";
import { Stage } from "../core/Stage";
import { DisplayObjectAbstract } from "../core/DisplayObjectAbstract";
import { TouchMouseEventEnum } from "./TouchMouseEventEnum";
import { TickerShared } from "../core/Ticker";
import { Role } from "../enum/FollowLineEnum";

export class SyncManager extends vf.utils.EventEmitter {
    public constructor(stage: Stage) {
        super();
        this._interactionEvent = new InteractionEvent();
        if (!this._interactionEvent.data) {
            this._interactionEvent.data = new vf.InteractionData();
        }
        this._stage = stage;
    }

    /**
     * stagesyncManager
     */
    public static getInstance(stage: Stage | undefined = undefined) {
        return SyncManager.instance;
    }
    public static getIns(){
        return SyncManager.instance;
    }
    public static instance?: SyncManager;
    private _role = Role.teacher; //
    public mode = 1;//  1-  2-   3-
    public resumeStatusFlag = false; //
    public resumeImmediately = false; //
    public offsetTime = 0; //Date.now()
    private _initTime = 0; //
    private _interactionEvent: InteractionEvent; //event
    private _stage: Stage; //stage
    private _lostMoveEvent: any[] = []; //moveevent
    private _throttleMoveFlag = false; //move
    private _throttleMoveTimer: any = null; //move
    private _evtDataList: any[] = []; //
    private _readystate = 1;
    private _sendId = 0; //eventId
    private _lastSId = 0; //eventId
    private _lastTId = 0; //eventId
    private _lastSEventData: any = undefined;  //eventData
    private _lastTEventData: any = undefined;  //eventData
    private _swaitingEventList: any[] = []; //event
    private _twaitingEventList: any[] = []; //event
    private _waitTimer = 0;
    private _resumeTimer = 0;
    private _heartTimer: any = undefined;
    private _autoHeartTimer: any = undefined;
    public autoHeartFlag: any = true; //
    public _initDataTimer = 0;
    public initDataFlag = false;  //
    private _resumeStoreList: any[] = [];  //
    private _startFlag = false;
    private _lostEvent: any[] = []; //event
    private _throttleEventTimer: any = null; //timer
    private _lastEventData?: any;  //,
    

    /**
     * log
     */
    private log(...data: any[]) {
        if ((window as any).syncManagerLog) {
            console.log(...data);
        }
    }

    /**
     * syncManager
     */
    public stop() {
        this.reset();
        this._readystate = 0;
        this._startFlag = false;
    }

    /**
     * 
     */
    public start(){
        this.reset();
        const stage = this._stage;
        const systemEvent = stage.getSystemEvent();
        if (systemEvent) {
            systemEvent.on("sendCustomEvent", this.sendCustomEvent, this);
            systemEvent.on("sendHeartEvent", this.sendHeartEvent, this);
        }
        this._startFlag = true;
    }

    /**
     * syncManager
     */
    private reset(){
        //
        this.dealLostEvent();

        //
        if(this._initDataTimer){
            clearTimeout(this._initDataTimer);
        }
        if(this._throttleMoveTimer){
            clearTimeout(this._throttleMoveTimer);
        }
        if(this._waitTimer){
            clearTimeout(this._waitTimer);
        }
        if(this._resumeTimer){
            clearTimeout(this._resumeTimer);
        }
        if (this._heartTimer) {
            clearInterval(this._heartTimer);
            this._heartTimer = undefined;
        }
        if(this._autoHeartTimer){
            clearInterval(this._autoHeartTimer);
            this._autoHeartTimer = undefined;
        }
        this.resumeStatusFlag = false; //
        this.resumeImmediately = false; //
        this.offsetTime = 0; //Date.now()
        this._initTime = TickerShared.currentTickerTime;
        this._lostMoveEvent = []; //event
        this._throttleMoveFlag = false; //
        this._throttleMoveTimer = null; //
        this._readystate = 1;
        this._sendId = 0; //eventId
        this._lastSId = 0; //eventId
        this._lastTId = 0; //eventId
        this._lastSEventData = undefined;
        this._lastTEventData = undefined;
        this._swaitingEventList = []; //event
        this._twaitingEventList = []; //event
        this._waitTimer = 0;
        this._resumeTimer = 0;
        this._heartTimer = undefined;
        this._autoHeartTimer = undefined;
        this.autoHeartFlag = true; //
        this._initDataTimer = 0;
        this.initDataFlag = false;  //
        this._resumeStoreList = [];  //
        this._lastEventData = undefined;

        this._lostEvent = [];
        this._throttleEventTimer && clearTimeout(this._throttleEventTimer);
        const stage = this._stage;
        const systemEvent = stage.getSystemEvent();
        if (systemEvent) {
            systemEvent.off("sendCustomEvent", this.sendCustomEvent, this);
            systemEvent.off("sendHeartEvent", this.sendHeartEvent, this);
        }
    }

    /**
     * 
     */
    public set role(value: Role){
        this._role = value;
        this.initDataFlag = true;
    }

    public get role(){
        return this._role;
    }

    /**
     * 100.5
     * @param duration 
     * @param role 
     */
    private autoHeartEvent(){
        if(!this.autoHeartFlag) return;
        if(this._autoHeartTimer){
            clearInterval(this._autoHeartTimer);
        }
        let times = 0;
        this._autoHeartTimer = setInterval(()=>{
            this.sendHeartEvent();
            times++;
            if(times >= 5){
                if(this._autoHeartTimer){
                    clearInterval(this._autoHeartTimer);
                }
            }
        }, 1000);
    }

    /**
     * 
     * @duration 
     * @role 1-  2-   12-
     */
    public syncHeartEvent(duration: number, role = 12) {
        if (role == 1 && this.role == Role.student) return;
        if (role == 2 && this.role == Role.teacher) return;
        if (duration < 2000) {
            duration = 2000;
        }
        if (this._heartTimer) {
            clearInterval(this._heartTimer);
        }
        //js
        this._heartTimer = setInterval(() => {
            this.sendHeartEvent();
        }, duration);
    }

    /**
     * 
     */
    public sendHeartEvent() {
        const eventData: any = {};
        const time = this.currentTime();
        eventData.code = "syncHeartEvent";
        eventData.time = time;
        this.sendEvent(eventData);
    }

    /**
     * 
     */
    public collectEvent(e: InteractionEvent, obj: DisplayObjectAbstract) {
        if (!this._startFlag) return;
        if (e.signalling) return; //
        if (!obj.syncabled) return; //

        const eventData: any = this.createEventData(e, obj);
        if(!eventData) return;

        if(this._autoHeartTimer){
            clearInterval(this._autoHeartTimer);
        }

        if (e.type === TouchMouseEventEnum.mousemove || e.type === TouchMouseEventEnum.touchmove) {
            this.throttle(eventData);
        } else {
            //move
            if (this._lostMoveEvent.length > 0) {
                clearTimeout(this._throttleMoveTimer);
                this.sendEvent(this._lostMoveEvent[0]);
                this._lostMoveEvent = [];
                this._throttleMoveFlag = false;
            }
            this.sendEvent(eventData);

            //
            if(e.type == TouchMouseEventEnum.mousedown ||
               e.type == TouchMouseEventEnum.mouseup ||
               e.type == TouchMouseEventEnum.touchstart ||
               e.type == TouchMouseEventEnum.touchend ){
                this.autoHeartEvent();
               }
        }
    }

    /**
     * 
     * customData 
     */
    public sendCustomEvent(customData: any) {
        if(!this._startFlag) return;
        const eventData: any = {};
        const time = this.currentTime();
        eventData.code = "syncCustomEvent_" + vf.utils.uid() + time;
        eventData.time = time;
        eventData.data = JSON.stringify(customData);
        this.sendEvent(eventData);
    }

    /**
     * 
     */
    private sendSyncTimeEvent() {
        const eventData: any = {};
        eventData.code = "syncTimeEvent";
        eventData.time = this.currentTime();
        this.sendEvent(eventData);
    }

    /**
     * 
     * @signalType   live-   history-
     */
    public receiveEvent(eventData: any, signalType = "live") {
        if (!this._startFlag) return;
        if (signalType == "history") {
            this.log("syncManager----receive history Event", eventData);
            this.dealHistoryEvent(eventData);
        } else {
            this.log("syncManager----receive live Event", eventData.id, eventData.time, eventData.code, eventData);
            this.dealLiveEvent(eventData);
        }
    }

    private dealLiveEvent(eventData: any){
        //
        if (eventData.code.indexOf('syncListEvent_') == 0){
            eventData.data.forEach((element: any) => {
                this.dealLiveEvent(element);
            });
            return;
        }
        if(eventData.role !== 'T' && eventData.role !== 'S') return;
        if (eventData.code === "syncTimeEvent") {
            //
            if (!this.resumeStatusFlag) {
                if (this.currentTime() > eventData.time + 300) {
                    //
                    this.sendSyncTimeEvent();
                } else {
                    //
                    const duration = eventData.time - this.currentTime();
                    this.timeTravel(duration);
                }
            }
            return;
        }
        if (eventData.code === "syncHeartEvent") {
            return;
        }
        if (eventData.code.indexOf("syncInteraction_") != 0 && eventData.code.indexOf("syncCustomEvent_") != 0) {
            return;
        }
        if (this.resumeStatusFlag) {
            this._resumeStoreList.push(eventData);
            return;
        }
        //
        if(this.mode == 3 && this._evtDataList.length > 0){
            this._resumeStoreList.push(eventData);
            return;
        }
        if (this.checkEventId(eventData)) {
            this.parseEventData(eventData);
            this.checkWaitingEvent();
        } else {
            if (this._swaitingEventList.length > 0 || this._twaitingEventList.length) {
                //0.5s0.5sidwaitevent
                clearTimeout(this._waitTimer);
                this._waitTimer = setTimeout(() => {
                    this.checkWaitingEvent(true);
                }, 500);
            }
        }
    }

    /**
     * eventId
     */
    private checkEventId(eventData: any): boolean {
        if (eventData.role == Role.teacher) {
            if (this._lastTId != 0 && this._lastTId != eventData.id - 1) {
                //idid
                if(eventData.id <= this._lastTId){
                    console.log('syncManager----TideventId', this._lastTId, eventData.id)
                    return false;
                }
                //
                console.log('syncManager----Tid', this._lastTId, eventData.id)
                this._twaitingEventList.push(eventData);
                return false;
            }
            this._lastTId = eventData.id;
            this._lastTEventData = eventData;
        } else {
            if (this._lastSId != 0 && this._lastSId != eventData.id - 1) {
                //idid
                if(eventData.id <= this._lastSId){
                    console.log('syncManager----SideventId', this._lastSId, eventData.id)
                    return false;
                }
                //
                console.log('syncManager----Sid', this._lastSId, eventData.id)
                this._swaitingEventList.push(eventData);
                return false;
            }
            this._lastSId = eventData.id;
            this._lastSEventData = eventData;
        }
        return true;
    }

    /**
     * event
     * forceFlag: 
     */
    private checkWaitingEvent(forceFlag: boolean = false) {
        clearTimeout(this._waitTimer);
        if (this._twaitingEventList.length > 0) {
            this._twaitingEventList.sort((a, b) => {
                return a.id - b.id;
            });
            if(forceFlag){
                this._lastTId = this._twaitingEventList[0].id - 1;
            }
            for (let i = 0; i < this._twaitingEventList.length; ++i) {
                //
                const eventData = this._twaitingEventList[i];
                console.log('syncManager----deal Tid', this._lastTId, eventData.id)
                if(eventData.id == this._lastTId + 1){
                    this.parseEventData(eventData);
                    this._lastTId = eventData.id;
                    this._lastTEventData = eventData;
                    this._twaitingEventList.splice(i, 1);
                    --i;
                }
            }
        }

        if (this._swaitingEventList.length > 0) {
            this._swaitingEventList.sort((a, b) => {
                return a.id - b.id;
            });
            if(forceFlag){
                this._lastSId = this._swaitingEventList[0].id - 1;
            }
            for (let i = 0; i < this._swaitingEventList.length; ++i) {
                //
                const eventData = this._swaitingEventList[i];
                console.log('syncManager----deal Sid', this._lastSId, eventData.id)
                if(eventData.id == this._lastSId + 1){
                    this.parseEventData(eventData);
                    this._lastSId = eventData.id;
                    this._lastSEventData = eventData;
                    this._swaitingEventList.splice(i, 1);
                    --i;
                }
            }
        }

        //0.5s
        if(this._swaitingEventList.length > 0 || this._twaitingEventList.length > 0){
            this._waitTimer = setTimeout(() => {
                this.checkWaitingEvent(true);
            }, 500);
        }
    }

    /**
     * 
     */
    private currentTime() {
        const time = TickerShared.currentTickerTime - this._initTime;
        return Math.floor(time);
    }

    /**
     * e
     */
    private createEventData(e: InteractionEvent, obj: DisplayObjectAbstract) {
        const event: any = {};
        event.type = e.type;  //  mousedownmouseupmousemove....
        event.path = getDisplayPathById(obj);//
        const data: any = {};
        event.data = data;
        data.identifier = e.data.identifier;
        data.global = { x: Math.floor(e.data.global.x), y: Math.floor(e.data.global.y) };
        //!!!important: e.data.originalEvent  
        const time = this.currentTime();
        const eventData = {
            code: "syncInteraction_" + vf.utils.uid() + time,
            time: time,
            data: event,
        };


        if(this._lastEventData){
            if(e.type !== 'mousemove' && e.type !== 'touchmove' && this._lastEventData.type == e.type && this._lastEventData.obj == obj && this._lastEventData.time == time){
                // this.log('syncManager----', this._lastEventData.time, time)
                return undefined;
            }
        }
        this._lastEventData = {
            type: e.type,
            obj,
            time
        }


        return eventData;
    }

    /**
     * ()
     */
    private sendEvent(eventData: any) {
        if(this.role !== 'T' && this.role !== 'S') return;//
        if(this.mode == 3) return;  //
        //eventId
        eventData.role = this.role;
        //id
        if(eventData.code.indexOf("syncInteraction") == 0 || eventData.code.indexOf("syncCustomEvent") == 0){
            eventData.id = ++this._sendId;
        }
        else{
            eventData.id = 0;
        }

        this.checkEventData(eventData);
    }

    /**
     * end
     * start300ms
     * 
     * @param eventData 
     */
    private checkEventData(eventData: any){
        if(eventData.code.indexOf("syncInteraction") == 0){
            //
            if(eventData.data.type == TouchMouseEventEnum.mouseup || eventData.data.type == TouchMouseEventEnum.touchend){
                eventData.data = JSON.stringify(eventData.data);
                this._lostEvent.push(eventData);
                this._throttleEventTimer && clearTimeout(this._throttleEventTimer);
                this.dealLostEvent();
            }
            else if(eventData.data.type == TouchMouseEventEnum.mousedown || eventData.data.type == TouchMouseEventEnum.touchstart){
                eventData.data = JSON.stringify(eventData.data);
                this._lostEvent.push(eventData);
                if(this._lostEvent.length == 1){
                    //
                    this._throttleEventTimer && clearTimeout(this._throttleEventTimer);
                    this._throttleEventTimer = setTimeout(() => {
                        this.dealLostEvent();
                    }, 300);
                }
            }
            else{
                eventData.data = JSON.stringify(eventData.data);
                this._lostEvent.push(eventData);
                if(this._lostEvent.length == 1){
                    this.dealLostEvent();
                }
            }
        }
        else{
            this._lostEvent.push(eventData);
            this._throttleEventTimer && clearTimeout(this._throttleEventTimer);
            this.dealLostEvent();
        }
    }

    private dealLostEvent(){
        if(this._lostEvent.length == 0) return;
        if(this._lostEvent.length == 1){
            this.realSendEvent(this._lostEvent[0]);
        }
        else{
            let event = {
                code: "syncListEvent_" + vf.utils.uid() + this.currentTime(),
                time: this.currentTime(),
                data: this._lostEvent,
            }
            this.realSendEvent(event);
        }
        this._lostEvent = [];
    }

    /**
     * ()
     */
    private realSendEvent(eventData: any){
        const stage = this._stage;
        //uistage
        stage.emit("sendSyncEvent", eventData);
        //player
        const msg = {
            level: "command",
            code: "syncEvent",
            data: eventData,
        };
        stage.sendToPlayer(msg);
        this.log("syncManager----sendSyncEvent", eventData.id, eventData.time, eventData.code, eventData);
    }

    /**
     * 
     */
    private throttleUpdate() {
        this._throttleMoveFlag = false;
        if (this._lostMoveEvent.length > 0) {
            this.throttle(this._lostMoveEvent[0]);
            this._lostMoveEvent = [];
        }
    }

    /**
     * 100ms
     * @param eventData
     */
    private throttle(eventData: any) {
        if (!this._throttleMoveFlag) {
            this._throttleMoveFlag = true;
            this.sendEvent(eventData);
            this._throttleMoveTimer = setTimeout(() => {
                this.throttleUpdate();
            }, 200);
        } else {
            this._lostMoveEvent = [];
            this._lostMoveEvent.push(eventData);
        }
    }

    private resetStage() {
        const stage = this._stage as any;
        if (stage.reset) {
            stage.reset(false);  //  by ziye+
        } else {
            console.error("stageresetreset!!!");
        }
        if(this._autoHeartTimer){
            clearInterval(this._autoHeartTimer);
        }
    }

    /**
     * event
     */
    private parseEventData(eventData: any) {
        const stage = this._stage;
        const time = eventData.time;
        //
        const currentTime = this.currentTime();
        if (currentTime < time) {
            const druation = time - currentTime;
            this.timeTravel(druation);
        }
        if (eventData.code.indexOf("syncInteraction_") == 0) {
            const event = JSON.parse(eventData.data);
            this._interactionEvent.signalling = true;
            this._interactionEvent.type = event.type;
            const data = event.data;
            this._interactionEvent.data.identifier = data.identifier;
            this._interactionEvent.data.global.set(data.global.x, data.global.y);
            let obj = stage.getChildByPath(event.path) as DisplayObjectAbstract;
            if(obj){
                if(this.resumeStatusFlag && !obj.resumeabled) return;
                obj.container.emit(this._interactionEvent.type, this._interactionEvent);
                this.log('syncManager----syncInteraction_success:', eventData);
            }
            else{
                console.error('path', event.path);
            }
        } else if (eventData.code.indexOf("syncCustomEvent_") == 0) {
            //
            const data = JSON.parse(eventData.data);
            const systemEvent = stage.getSystemEvent();
            if (systemEvent) {
                systemEvent.emit("receiveCustomEvent", {
                    code: 'receiveCustomEvent',
                    level: 'command',
                    data: data
                });
            }
            stage.emit("receiveCustomEvent", data);
            this.emit("receiveCustomEvent", data);
            this.log('syncManager----syncCustomEvent_success:', eventData);
        }
    }

    /**
     * 
     */
    private timeTravel(druation: number) {
        if(druation <= 0) return;  //
        let resetRenderFlag = false;
        if (this._stage.renderable) {
            this._stage.renderable = false;
            resetRenderFlag = true;
        }
        TickerShared.timeTravel(druation);
        if (resetRenderFlag) {
            this._stage.renderable = true;
        }
    }

    /**
     * 
     * @param history 
     */
    private initData(history: boolean){
        const initData = {
            level: 'command',
            code: 'VFInitData',
            data: {
                history
            }
        }
        //
        this._initDataTimer && clearTimeout(this._initDataTimer)
        // this._initDataTimer = setTimeout(() => {
            const stage = this._stage;
            const systemEvent = stage.getSystemEvent();
            if (systemEvent) {
                systemEvent.emit("VFInitData", initData);
            }
            stage.emit("VFInitData", initData);
        // }, 120);
        this.emit('VFInitData', {history});
    }

    /**
     * 
     * @param eventData
     */
    private dealHistoryEvent(eventData: any) {
        for(const key in eventData){
            if(key.indexOf("syncListEvent_") == 0){
                let event = eventData[key];
                event.data.forEach((element: any) => {
                    eventData[element.code] = element;
                });
                delete eventData[key];
            }
        }
        this._initTime = TickerShared.currentTickerTime;
        this._evtDataList = [];
        if (!eventData){
            this.sendSyncTimeEvent();
            this.autoHeartEvent();
            this.initData(false);
            return;
        }

        let hasCustomEvent = false;
        for (const key in eventData) {
            if (
                key.indexOf("syncInteraction_") == 0 ||
                key.indexOf("syncCustomEvent_") == 0 ||
                key.indexOf("syncHeartEvent") == 0
            ) {
                this._evtDataList.push(eventData[key]);
            }

            if(key.indexOf("syncCustomEvent_") == 0){
                hasCustomEvent = true;
            }
        }
        if (this._evtDataList.length == 0){
            this.sendSyncTimeEvent();
            this.autoHeartEvent();
            this.initData(false);
            return;
        }
        this.initData(hasCustomEvent);
        //stage
        if(this.resumeImmediately){
            this.resumeStatusImmediately();
        }
        else{
            this.resumeStatus();
        }
    }

    private resumeStatusImmediately() {
        if (this._readystate === 0) return;
        //
        this.resumeStatusFlag = this._stage.resumeStatusFlag = true;
        this._initTime = TickerShared.currentTickerTime;
        this._stage.renderable = false;
        this._evtDataList.sort((a: any, b: any) => {
            if(a.time !== b.time){
                return a.time - b.time;
            }
            else{
                return a.id - b.id;
            }
        });
        for (let i = 0; i < this._evtDataList.length; ++i) {
            //
            let eventData = this._evtDataList[i];
            this.parseEventData(eventData);
            if(eventData.code.indexOf("syncInteraction") == 0 || eventData.code.indexOf("syncCustomEvent") == 0){
                if(eventData.role == 'T'){
                    this._lastTId = eventData.id;
                    this._lastTEventData = eventData;
                    if(this.role == 'T'){
                        this._sendId = eventData.id;
                    }
                }
                else{
                    this._lastSId = eventData.id;
                    this._lastSEventData = eventData;
                    if(this.role == 'S'){
                        this._sendId = eventData.id;
                    }
                }
            }
        }

        this._evtDataList = [];

        //
        this._resumeStoreList.forEach(item => {
            this.receiveEvent(item, 'live');
        })
        this._resumeStoreList = [];

        this._stage.renderable = true;
        this.resumeStatusFlag = this._stage.resumeStatusFlag = false;
        //
        this.sendSyncTimeEvent();
        this.autoHeartEvent();
    }

    /**
     * 
     */
    private resumeStatus() {
        //
        //seekresetStage
        if(this.mode == 3){  
            const systemEvent = this._stage.getSystemEvent();
            if (systemEvent) {
                systemEvent.on("status", this.resetCallback, this);
            }
            this.resetStage();
        }
        else{
            this.resumeStatusImmediately();
        }
    }

    private resetCallback(event: any){
        if(event.code == 'ScenComplete'){
            const systemEvent = this._stage.getSystemEvent();
            if(systemEvent){
                systemEvent.off("status", this.resetCallback, this);
            }
            this.resumeStatusImmediately();
        }
    }
}
