import { updateTextureFill } from '../core/plugs/LineGradient';
import { GraphBase } from './private/GraphBase'

/**
 * (0)
 * 
 * 
 * 
 *  lineWidth  color 
 * 
 */
export class Isosceles extends GraphBase {
    public constructor() {
        super();
    }

    private _upLine = 0;
    private _downLine = 0;

    private _path: vf.Point[] = [];

    public get upLine(): number {
        return this._upLine;
    }

    public set upLine(value) {
        this._upLine = value;

        this.invalidateDisplayList();
    }
    public get downLine(): number {
        return this._downLine;
    }

    public set downLine(value) {
        this._downLine = value;
        this.invalidateDisplayList();
    }

    public drawGraph() {
        const graphics = this.graphics;
        graphics.clear();
        if (this._lineWidth < 0) {
            console.log("0");
            return;
        }
        graphics.lineStyle(this._lineWidth, this._lineColor, this._lineAlpha, 0.5, false, this.lineType);

        if (this.color) {
            graphics.beginFill(this._color, this._fillAlpha);
        } else {
            updateTextureFill(this.graphics,this);
        }

        this.setIsosPath();
        graphics.drawPolygonOne(this._path);


        graphics.endFill();
    }



    private setIsosPath() {
        this._path = [];
        const upWidth = this._upLine;
        const downWidth = this._downLine;
        const longWidth = Math.max(upWidth, downWidth);
        const height = this.height > 0 ? this.height : longWidth;
        const scaleWidth = (this.width > 0 ? this.width / longWidth : 1);
        const w = this._anchorX ? this.width * this._anchorX : 0;
        const h = this._anchorY ? this.height * this._anchorY : 0;
        const upY = 0 - h;
        const downY = height - h;

        const point1 = new vf.Point((longWidth - upWidth) * 0.5 * scaleWidth - w, upY);
        const point2 = new vf.Point((longWidth - downWidth) * 0.5 * scaleWidth - w, downY);
        const point3 = new vf.Point((longWidth * 0.5 + downWidth * 0.5) * scaleWidth - w, downY);
        const point4 = new vf.Point((longWidth * 0.5 + upWidth * 0.5) * scaleWidth - w, upY);
        this._path.push(point1, point2, point3, point4);

    }

}