import { AnimationBase } from "../private/AnimationBase";
import { PathTimeline } from './PathTimeline';
import { TickerShared } from "../../core/Ticker";
export class AnimationPath extends AnimationBase {
    private pathTimeline?: PathTimeline;
    constructor(sp?: any, options?: any, duratime?: number) {
        super(sp, options, duratime);
        this.pathTimeline = new PathTimeline(options.path);
        this.pathTimeline.frames = options.frames;
        if (options.frames && options.frames.length) {
            this.pathTimeline.totalTime = options.frames[options.frames.length - 1].time;
        }

        this.pathTimeline.loop = !!options.loop;
    }


    public play(timer = 0) {
        if (this.playing === true) {
            return;
        }
        super.play(timer);

    }


    public gotoAndStop(timer: number) {
        super.gotoAndStop(timer);
    }

    protected updata(delta: number) {
        if (this.playing === false) {
            return;
        }
        this._currenTimer += TickerShared.deltaMS;
        const { pathTimeline, sprite } = this;
        if (pathTimeline) {
            pathTimeline.globalTime = this._currenTimer;
            //path.getPointAtLengthapi  by ziye+
            if(sprite && sprite.stage && !sprite.stage.resumeStatusFlag){
                let curPos = pathTimeline.curPos;
                sprite.x = curPos[0];
                sprite.y = curPos[1];
            }

            if (!pathTimeline.loop && this._currenTimer >= pathTimeline.totalTime) {
                //
                let curPos = pathTimeline.curPos;
                sprite.x = curPos[0];
                sprite.y = curPos[1];
                this.complete();
            }
        }
    }


    public release() {
        if (this.pathTimeline) {
            this.pathTimeline.release();
            this.pathTimeline = null as any;
        }

        super.release();

    }




}