import { DisplayObjectAbstract } from "../core/DisplayObjectAbstract";
import { Scheduler } from "../core/Scheduler";
import { TimeLine } from "../tween/TimeLine";
import { Tween } from "../tween/Tween"
import { createDisplayObject } from "../utils/Utils";
import { Container } from "./Container";

/**
 *  timelinetweenschedule
 * scene
 * class Scene extends vf.gui.AutoContainer{
 *  
 * }
 */
export class AutoContainer extends Container{
    constructor(){
        super();
    }

    private idCountDict: { [key: string]: number } = {}; // idid
    private timeLineList: TimeLine[] = [];
    private tweenList: Tween[] = [];
    private schedulerList: Scheduler[] = [];

    public createDisplayObject(moduleName: string, option: any, parent?: DisplayObjectAbstract){
        if(!parent){
            parent = this;
        }
        if(!option.id){
            this.idCountDict[moduleName] = this.idCountDict[moduleName] || 1;
            option.id = `${moduleName}_${this.idCountDict[moduleName]++}`;
        }
        return createDisplayObject(moduleName, option, parent);
    }

    public createTimeLine(option: any){
        let timeLine = new TimeLine(option);
        this.timeLineList.push(timeLine);
        return timeLine;
    }

    public createTween(object: any){
        let tween = new Tween(object);
        this.tweenList.push(tween);
        return tween;
    }

    public setTimeout(time: number, callFunc: (data?: any) => void){
        let scheduler = Scheduler.setTimeout(time, callFunc);
        this.schedulerList.push(scheduler);
        return scheduler;
    }

    public setInterval(time: number, callFunc: (data?: any) => void){
        let scheduler = Scheduler.setInterval(time, callFunc);
        this.schedulerList.push(scheduler);
        return scheduler;
    }

    public setEnterFrame(callFunc: (data?: any) => void){
        let scheduler = Scheduler.setEnterFrame(callFunc);
        this.schedulerList.push(scheduler);
        return scheduler;
    }

    public release(){
        this.idCountDict = {};
        for(let i = 0; i < this.timeLineList.length; ++i){
            if(this.timeLineList[i]){
                this.timeLineList[i].release();
            }
        }
        this.timeLineList = [];
        for(let i = 0; i < this.tweenList.length; ++i){
            if(this.tweenList[i]){
                this.tweenList[i].release();
            }
        }
        this.tweenList = [];
        for(let i = 0; i < this.schedulerList.length; ++i){
            if(this.schedulerList[i]){
                this.schedulerList[i].stop();
            }
        }
        this.schedulerList = [];

        super.release();
    }
}
