
import { debug } from "../../utils/Utils";
import { DisplayObject } from "../DisplayObject";

export class HitArea implements Lifecycle {
    private _type: 'rect' | 'ellipse' = "rect";
    private _x = NaN;
    private _y = NaN;
    private _width = NaN;
    private _height = NaN;

    private _sprite: any;
    private _displayObj: DisplayObject;
    private _drawShape?: vf.Graphics;

    /**
     * 
     */
    public constructor(target: DisplayObject) {
        this._displayObj = target;
        target.plugs.set(HitArea.key, this);
    }

    public static key = "HitArea";

    public get type() {
        return this._type;
    }
    public set type(value) {
        if (this._type === value) {
            return;
        }
        this._type = value;
        this._displayObj.invalidateDisplayList();
    }

    public get x() {
        return this._x;
    }
    public set x(value) {
        if (this._x === value) {
            return;
        }
        this._x = value;
        this._displayObj.invalidateDisplayList();
    }
    public get y() {
        return this._y;
    }
    public set y(value) {
        if (this._y === value) {
            return;
        }
        this._y = value;
        this._displayObj.invalidateDisplayList();
    }
    public get width() {
        return this._width;
    }
    public set width(value) {
        if (this._width === value) {
            return;
        }
        this._width = value;
        this._displayObj.invalidateDisplayList();
    }
    public get height() {
        return this._height;
    }
    public set height(value) {
        if (this._height === value) {
            return;
        }
        this._height = value;
        this._displayObj.invalidateDisplayList();
    }

    public updataHitArea() {
        if (this._displayObj && this._displayObj.container) {
            this._displayObj.container.hitArea = this.getHitArea();

            if (debug) {
                const { _x, _y, _width, _height } = this;
                const x = isNaN(_x) ? 0 : _x;
                const y = isNaN(_y) ? 0 : _y;
                const width = isNaN(_width) ? this._displayObj.width : _width;
                const height = isNaN(_height) ? this._displayObj.height : _height;
                const halfWidth = width >> 1;
                const halfHeight = height >> 1;
                !this._drawShape && (this._drawShape = new vf.Graphics());
                this._drawShape.clear();
                this._drawShape.lineStyle(1, 0x00ffff, 1);
                this._drawShape.beginFill(0xff0000, 0.5);
                switch (this.type) {
                    case "rect":
                        this._drawShape.drawRect(x, y, width, height);
                        break;
                    case "ellipse":
                        this._drawShape.drawEllipse(x + halfWidth, y + halfHeight, halfWidth, halfHeight);
                        break;
                }
                this._drawShape.endFill();
                !this._drawShape.parent && this._displayObj.container.addChild(this._drawShape);
            }
        }
    }

    private getHitArea() {
        const { _x, _y, _width, _height } = this;

        const x = isNaN(_x) ? 0 : _x;
        const y = isNaN(_y) ? 0 : _y;
        const width = isNaN(_width) ? this._displayObj.width : _width;
        const height = isNaN(_height) ? this._displayObj.height : _height;
        const halfWidth = width >> 1;
        const halfHeight = height >> 1;
        switch (this.type) {
            case "rect":
                this._sprite = new vf.Rectangle(x, y, width, height);
                break;
            case "ellipse":
                this._sprite = new vf.Ellipse(x + halfWidth, y + halfHeight, halfWidth, halfHeight);
                break;
        }
        return this._sprite;
    }


    load() {
        //
    }

    public release() {
        this._type = "rect";
        this._x = NaN;
        this._y = NaN;
        this._width = NaN;
        this._height = NaN;
        this._sprite = undefined;
        this._displayObj = undefined as any;

        if (this._drawShape) {
            if (this._drawShape.parent) {
                this._drawShape.parent.removeChild(this._drawShape);
            }
            this._drawShape.destroy();
            this._drawShape = undefined;
        }
    }
}