
/**
 * 
 */
export class GraphicsCore extends vf.Graphics {
    private _lineType: "full" | "dash" = "full";//full    dash 
    public constructor(geometry?: vf.GraphicsGeometry | undefined) {
        super(geometry);
    }
    private RatioLine = 3;
    private RatioNone = 2;

    public lineStyle(width?: number, color?: number, alpha?: number, alignment?: number, native?: boolean, lineType: "full" | "dash" = "full"): vf.Graphics {
        this._lineType = lineType;
        return super.lineStyle(width, color, alpha, alignment, native);
    }

    public lineTo(x: number, y: number): vf.Graphics {
        if (this._lineType === "full") {
            return super.lineTo(x, y);
        }
        const lineWidth = this._lineStyle.width;
        if (lineWidth <= 0) {
            return this;
        }
        const pointStartX = this.currentPath ? this.currentPath.points[0] : 0;
        const pointStartY = this.currentPath ? this.currentPath.points[1] : 0;

        const ratioLine = this.RatioLine;
        const ratioNone = this.RatioNone;
        const dis = lineWidth * ratioLine;
        const dashDis = lineWidth * ratioNone;
        const totaDis = Math.sqrt(Math.pow(x - pointStartX, 2) + Math.pow(y - pointStartY, 2));
        const scale = lineWidth / totaDis;
        let dashX = pointStartX;
        let dashY = pointStartY;
        const addX = scale * (x - pointStartX) * ratioLine;
        const addY = scale * (y - pointStartY) * ratioLine;
        const addDashX = scale * (x - pointStartX) * (ratioLine + ratioNone);
        const addDashY = scale * (y - pointStartY) * (ratioLine + ratioNone);
        const maxD = totaDis;
        for (let i = 0; i < maxD; i = i + dis + dashDis) {
            let toX = dashX + addX
            let toY = dashY + addY;

            dashX = dashX + addDashX;
            dashY = dashY + addDashY;
            if (i + dis + dashDis > maxD) {
                dashX = toX = x;
                dashY = toY = y;
            }
            super.lineTo(toX, toY);
            this.moveTo(dashX, dashY);
        }
        return this;
    }


    public arc(cx: number, cy: number, radius: number, startAngle: number, endAngle: number, anticlockwise?: boolean): vf.Graphics {
        const startAgl = startAngle * Math.PI / 180;
        const endAgl = endAngle * Math.PI / 180
        if (this._lineType === "full") {
            if ((startAngle === 0 && endAngle === 360) || (startAngle === 360 && endAngle === 0)) {
                return this.drawCircle(cx, cy, radius);
            }
            return super.arc(cx, cy, radius, startAgl, endAgl, anticlockwise);
        }
        const lineWidth = this._lineStyle.width;
        if (lineWidth <= 0) {
            return this;
        }
        let startA = startAgl;
        const totalNum = radius * (Math.abs(endAgl - startAgl) / (Math.PI * 2));
        for (let i = 0; i < totalNum; i++) {
            const endA = (endAgl - startAgl) / (totalNum * 2) + startA;
            super.arc(cx, cy, radius, startA, endA, anticlockwise);
            startA = startA + (endAgl - startAgl) / totalNum;
            const endX = cx + Math.cos(startA) * radius;
            const endY = cy + Math.sin(startA) * radius;
            super.moveTo(endX, endY);
        }

        return this;
    }

    /** 
     * 
    */
    public drawEllipse(xx: number, yy: number, width: number, height: number): vf.Graphics {
        if (this._lineType === "full") {
            return super.drawEllipse(xx, yy, width, height);
        }
        const lineWidth = this._lineStyle.width;
        if (lineWidth <= 0) {
            return this;
        }
        const w = width * 2;
        const h = height * 2;
        const x = xx - (w / 2);
        const y = yy - (h / 2);
        const kappa = 0.5522848;
        const ox = (w / 2) * kappa; // control point offset horizontal
        const oy = (h / 2) * kappa; // control point offset vertical
        const xe = x + w; // x-end
        const ye = y + h; // y-end
        const xm = x + (w / 2); // x-middle
        const ym = y + (h / 2); // y-middle
        super.moveTo(x, ym);
        this.bezierCurveTo(x, ym - oy, xm - ox, y, xm, y);
        this.bezierCurveTo(xm + ox, y, xe, ym - oy, xe, ym);
        this.bezierCurveTo(xe, ym + oy, xm + ox, ye, xm, ye);
        this.bezierCurveTo(xm - ox, ye, x, ym + oy, x, ym);

        return this;
    }

    /**
     * 
     */
    public drawRoundedRect(x: number, y: number, width: number, height: number, radius: number): vf.Graphics {
        if (this._lineType === "full") {
            return super.drawRoundedRect(x, y, width, height, radius);
        }
        const lineWidth = this._lineStyle.width;
        if (lineWidth <= 0) {
            return this;
        }

        const maxRadius = Math.min(width, height) / 2 | 0;
        radius = radius > maxRadius ? maxRadius : radius;

        const w = radius * 2;
        const h = radius * 2;
        const kappa = 0.5522848;
        const xe = x + width; // x-end
        const ye = y + height; // y-end
        const ox = radius * kappa; // control point offset horizontal
        const oy = radius * kappa; // control point offset vertical
        const pI = Math.PI;
        super.moveTo(x + radius, y);
        this.lineTo(x + width - radius, y);
        this.bezierCurveTo(xe - radius + ox, y, xe, y + oy, xe, y + radius);
        this.lineTo(xe, ye - radius);
        this.bezierCurveTo(xe, ye - radius + oy, xe - radius + ox, ye, xe - radius, ye);
        this.lineTo(x + radius, ye);
        this.bezierCurveTo(x + radius - ox, ye, x, ye - radius + oy, x, ye - radius);
        this.lineTo(x, y + radius);
        this.bezierCurveTo(x, y + radius - oy, x + radius - ox, y, x + radius, y);

        return this;
    }

    /**
     * 
     */
    public drawPolygonOne(path: vf.Point[], closeStroke = true): vf.Graphics {
        if (this._lineType === "full") {
            const points = [];
            for (let i = 0; i < path.length; i++) {
                points.push(path[i].x, path[i].y);
            }
            const pt: any = { points: points, closeStroke: closeStroke };
            return super.drawPolygon(pt);
        }
        const lineWidth = this._lineStyle.width;
        if (lineWidth <= 0) {
            return this;
        }
        const startPoint = path[0]; // 0
        this.moveTo(startPoint.x, startPoint.y);
        for (let i = 0; i < path.length; i++) {
            const item = path[i];
            this.lineTo(item.x, item.y);
        }
        if (closeStroke) {
            this.lineTo(startPoint.x, startPoint.y);
        }


        return this;
    }

    /**
     * 
     * B_{3}(t) = (1 - t)^3P_0 + 3t(1 - t)^2P_1 + 3t^2(1 - t)P_2 + t^3P_3 , t  in[0, 1]
     * 
     * (x3-3*x2+3*x1-x0)t^3 + (3*x2-6*x1+3*x0)t^2 + (3*x1-3*x0)t + x0
     * 
     * cpX cpY 
     * cpX2 cpY2 
     * toX toY 
     * fromX fromY 
     * x y 
     */
    public checOnkBezier(cpX: number, cpY: number, cpX2: number, cpY2: number, toX: number, toY: number, fromX: number, fromY: number, x: number, y: number): number {

        let start = 0.0;
        let end = 1.0;
        let half = 0.5;
        const A = toX - 3 * cpX2 + 3 * cpX - fromX;
        const B = 3 * cpX2 - 6 * cpX + 3 * fromX;
        const C = 3 * cpX - 3 * fromX;
        const D = fromX;
        let fStart;
        let fHalf;
        let yEnd = 0;
        let index = 0;
        while (Math.abs(A * Math.pow(half, 3) + B * half * half + C * half + D - x) > 1) {
            fStart = A * Math.pow(start, 3) + B * start * start + C * start + D - x
            fHalf = A * Math.pow(half, 3) + B * half * half + C * half + D - x;
            index++;
            if (fStart * fHalf < 0) {
                end = half;
                half = (start + half) * 0.5;
            } else {
                start = half;
                half = (half + end) * 0.5;
            }
            if (index >= 10) { //
                break;
            }

        }
        const nt = 1 - half;
        yEnd = Math.round(fromY * Math.pow(nt, 3) + 3 * Math.pow(nt, 2) * half * cpY + 3 * half * half * nt * cpY2 + toY * Math.pow(nt, 3));
        if (Math.abs(yEnd - y) <= Math.ceil(this._lineStyle.width * 0.5)) { //  
            return nt;
        }
        return -1;
    }

    /**
     * 
     */
    public quadraticCurveTo(cpX: number, cpY: number, toX: number, toY: number): vf.Graphics {
        if (this._lineType === "full") {
            return super.quadraticCurveTo(cpX, cpY, toX, toY);
        }
        const lineWidth = this._lineStyle.width;
        if (lineWidth <= 0) {
            return this;
        }
        const p = this.currentPath.points;
        //
        const bPoints: vf.Point[] = this.quadPointLength(p[p.length - 2], p[p.length - 1], cpX, cpY, toX, toY);
        //
        const totalLength = this.bezierLength(bPoints);
        //
        const dashPointList = this.bezierToLineData(bPoints, totalLength);

        //
        super.moveTo(p[p.length - 2], p[p.length - 1]);
        this.drawBezierDash(dashPointList);
        super.moveTo(toX, toY);

        return this;

    }



    /**
     * 
     */
    public bezierCurveTo(cpX: number, cpY: number, cpX2: number, cpY2: number, toX: number, toY: number): vf.Graphics {
        if (this._lineType === "full") {
            return super.bezierCurveTo(cpX, cpY, cpX2, cpY2, toX, toY);
        }
        const lineWidth = this._lineStyle.width;
        if (lineWidth <= 0) {
            return this;
        }

        const p = this.currentPath.points;

        //
        const bPoints: vf.Point[] = this.bezierPointLength(p[p.length - 2], p[p.length - 1], cpX, cpY, cpX2, cpY2, toX, toY);
        //
        const totalLength = this.bezierLength(bPoints);
        //
        const dashPointList = this.bezierToLineData(bPoints, totalLength);

        //
        this.moveTo(p[p.length - 2], p[p.length - 1]);
        this.drawBezierDash(dashPointList);
        this.moveTo(toX, toY);

        return this;
    }

    private drawBezierDash(dashPointList: any) {
        //
        for (let i = 0; i < dashPointList.length; i++) {
            const item = dashPointList[i];
            const point = item.point;
            if (item.type === false) {
                super.moveTo(point.x, point.y);
            } else {
                super.lineTo(point.x, point.y);
            }
        }
    }
    /**
    * (  n)
    */
    public quadPointLength(fromX: number, fromY: number, cpX: number, cpY: number, toX: number, toY: number, n = 30): vf.Point[] {
        let t = 0.0;
        let xa = 0;
        let ya = 0;
        let x = 0.0;
        let y = 0.0;
        const points = [new vf.Point(fromX, fromY)];
        for (let i = 1; i <= n; ++i) {
            t = i / n;
            xa = fromX + ((cpX - fromX) * t);
            ya = fromY + ((cpY - fromY) * t);
            x = xa + (((cpX + ((toX - cpX) * t)) - xa) * t);
            y = ya + (((cpY + ((toY - cpY) * t)) - ya) * t);

            points.push(new vf.Point(x, y));
        }
        return points;
    }
    /**
     * (  n)
     */
    public bezierPointLength(fromX: number, fromY: number, cpX: number, cpY: number, cpX2: number, cpY2: number, toX: number, toY: number, n = 30): vf.Point[] {
        let t = 0.0;
        let t2 = 0.0;
        let t3 = 0.0;
        let nt = 0.0;
        let nt2 = 0.0;
        let nt3 = 0.0;
        let x = 0.0;
        let y = 0.0;
        const points = [new vf.Point(fromX, fromY)];
        for (let i = 1; i <= n; ++i) {
            t = i / n;
            t2 = t * t;
            t3 = t2 * t;
            nt = (1.0 - t);
            nt2 = nt * nt;
            nt3 = nt2 * nt;
            x = (nt3 * fromX) + (3.0 * nt2 * t * cpX) + (3.0 * nt * t2 * cpX2) + (t3 * toX);
            y = (nt3 * fromY) + (3.0 * nt2 * t * cpY) + (3 * nt * t2 * cpY2) + (t3 * toY);

            points.push(new vf.Point(x, y));
        }
        return points;
    }


    /**
     * 
     */

    private bezierLength(points: vf.Point[]) {
        if (!points || points.length <= 0) {
            return 0;
        }
        let result = 0.0;
        let x = 0.0;
        let y = 0.0;
        let dx = 0.0;
        let dy = 0.0;
        let prevX = points[0].x;
        let prevY = points[0].y;
        for (let i = 0; i < points.length; i++) {
            x = points[i].x;
            y = points[i].y;
            dx = prevX - x;
            dy = prevY - y;
            prevX = x;
            prevY = y;
            result += Math.sqrt((dx * dx) + (dy * dy));
        }
        return result;
    }

    private bezierToLineData(points: vf.Point[], length: number, oldLength = 0, oldlineS = true) {
        const lineWidth = this._lineStyle.width;
        const ratioLine = this.RatioLine;
        const ratioNone = this.RatioNone;
        const dis = lineWidth * ratioLine;
        const dashDis = lineWidth * ratioNone;

        let pointOld = points[0];
        let index = 1;
        const result: any[] = [];
        let oldX = oldLength;// //oldLength 
        let lineS: boolean = oldlineS;

        while (index < points.length) {//
            const nextPoint = points[index];
            const distanse = this.pointDistance(nextPoint, pointOld);
            const curL = (oldX > 0 ? oldX : (lineS ? dis : dashDis));
            const l = distanse - (curL);
            if (l >= 0) {
                // 
                const b = pointOld;
                const end = points[index];
                const scale = (curL) / distanse;
                const x = b.x + (end.x - b.x) * scale;
                const y = b.y + (end.y - b.y) * scale;
                const p = new vf.Point(x, y);

                result.push({ type: lineS, point: p });
                oldX = 0;
                pointOld = p;
                lineS = !lineS;
            } else {
                pointOld = points[index];
                index++;
                oldX = Math.abs(l);
                if (index >= points.length) {
                    result.push({ type: lineS, point: pointOld });
                    break;
                }
            }

        }



        return result;
    }

    private pointDistance(pointA: vf.Point, pointB: vf.Point) {
        const dx = pointB.x - pointA.x;
        const dy = pointB.y - pointA.y;

        return Math.sqrt(dx * dx + dy * dy);
    }


    /**
     * 
     */
    public getLineRotation(lineTyle: "M" | "L" | "Q" | "C", isBeignPoint: boolean, fromX: number, fromY: number, toX: number, toY: number, cpX = 0, cpY = 0, cpX2 = 0, cpY2 = 0) {
        if (lineTyle === "M") {
            return 0;
        }
        let beginPoint;
        let endPoint;
        let bPoints: vf.Point[] = [];
        switch (lineTyle) {
            case "L":
                if (isBeignPoint) {
                    beginPoint = new vf.Point(toX, toY);
                    endPoint = new vf.Point(fromX, fromY);
                } else {
                    beginPoint = new vf.Point(fromX, fromY);
                    endPoint = new vf.Point(toX, toY);
                }
                break;
            case "Q":
            case "C":
                //
                if (lineTyle === "Q") {
                    bPoints = this.quadPointLength(fromX, fromY, cpX, cpY, toX, toY)
                } else {
                    bPoints = this.bezierPointLength(fromX, fromY, cpX, cpY, cpX2, cpY2, toX, toY);
                }
                if (isBeignPoint) {
                    beginPoint = new vf.Point(bPoints[1].x, bPoints[1].y);
                    endPoint = new vf.Point(fromX, fromY);
                } else {
                    beginPoint = new vf.Point(bPoints[bPoints.length - 2].x, bPoints[bPoints.length - 2].y);
                    endPoint = new vf.Point(toX, toY);
                }
                break;
        }

        const x = endPoint.x - beginPoint.x;
        const y = endPoint.y - beginPoint.y;
        if (y > 0) {
            return -(Math.atan(x / y));
        } else if (y === 0) {
            return (Math.PI + Math.atan(x / y));
        } else if (y < 0) {
            return Math.PI - Math.atan(x / y);
        }
        return 0;
    }

}