import { getTexture, rgbToNumber } from "../utils/Utils";
import { DisplayLayoutAbstract } from "./DisplayLayoutAbstract";
import { updateTextureFill } from "./plugs/LineGradient";

export class Background {

    constructor(parent: any) {
        this.parent = parent;
    }

    private canvas?: HTMLCanvasElement;
    private context?: CanvasRenderingContext2D;
    private texture?: vf.Texture;

    private parent: any;
    private _container?: vf.Container;

    private _oldSrc: string | undefined;
    private _oldRepeat = "";

    private _rect?: vf.Graphics;
    private _image?: vf.Sprite | vf.TilingSprite;
    private _mask?: vf.Graphics;
    private _invalidateFlag = false;

    public isInit = false;

    private _type: 0 | 1 | 2 = 0;
    public get type(): 0 | 1 | 2 {
        return this._type;
    }
    public set type(value: 0 | 1 | 2) {
        this._type = value;
        if (this._container === undefined) {
            this._container = new vf.Container();
            this._container.name = 'background';
            (this.parent as DisplayLayoutAbstract).container.addChildAt(this._container, 0);
            this._mask = new vf.Graphics();
            this.isInit = true;
        }
    }

    private _color: number | undefined = undefined;
    public get color(): number | undefined {
        return this._color;
    }
    public set color(value: number | undefined) {

        this._color = value || undefined;
        if (value === 0) {
            this._color = 0;
        }
        this._invalidateFlag = true;
    }

    private _alpha = 1;
    public get alpha(): number {
        return this._alpha;
    }
    public set alpha(value: number) {
        this._alpha = value;
        if (this._container) {
            this._container.alpha = value;
        } else {
            this._invalidateFlag = true;
            this.parent.invalidateDisplayList();
        }
    }


    private _radius = 0;
    public get radius() {
        return this._radius;
    }
    public set radius(value) {
        this._radius = value;
        this._invalidateFlag = true;
    }

    private _src: string | undefined;
    public get src(): string | undefined {
        return this._src;
    }
    public set src(value: string | undefined) {
        this._src = value || undefined;
        this._invalidateFlag = true;
    }

    private _positionX = 0;
    public get positionX() {
        return this._positionX;
    }
    public set positionX(value) {
        this._positionX = value;
        this._invalidateFlag = true;
    }

    private _positionY = 0;
    public get positionY() {
        return this._positionY;
    }

    public set positionY(value) {
        this._positionY = value;
        this._invalidateFlag = true;
    }

    private _size: number[] | undefined;
    public get size(): number[] | undefined {
        return this._size;
    }
    public set size(value: number[] | undefined) {
        this._size = value;
        this._invalidateFlag = true;
    }

    private _repeat: "no-repeat" | "repeat" = "no-repeat";
    public get repeat(): "no-repeat" | "repeat" {
        return this._repeat;
    }
    public set repeat(value: "no-repeat" | "repeat") {
        this._repeat = value;
        this._invalidateFlag = true;
    }


    private _fillAlpha = 1;
    public get fillColor() {
        return this._color;
    }
    public set fillColor(value) {

        if (Array.isArray(value)) {
            if (Array.isArray(value[0])) {
                if (this.canvas === undefined) {
                    this.canvas = document.createElement("canvas");
                    this.context = this.canvas.getContext("2d") as CanvasRenderingContext2D;
                    this.texture = vf.Texture.from(this.canvas);
                }
                this._color = value;
                this._invalidateFlag = true;
                return; // color
            }
            this._fillAlpha = value[3] || 1;
            this.color = rgbToNumber(value[0], value[1], value[2]);
            return;
        } else {
            this.color = value || undefined;
        }
    }

    private _fillGradientStops: number[] = [];
    public get fillGradientStops() {
        return this._fillGradientStops;
    }

    public set fillGradientStops(value) {
        this._fillGradientStops = value;
        this._invalidateFlag = true;
    }


    private _fillGradientType: 0 | 1 = 1;
    public get fillGradientType() {
        return this._fillGradientType;
    }

    public set fillGradientType(value) {
        this._fillGradientType = value;
        this._invalidateFlag = true;
    }



    private lastW?: number;
    private lastH?: number
    public update(w: number, h: number) {

        if (this.lastW !== w || this.lastH !== h) {
            this._invalidateFlag = true;
            this.lastW = w;
            this.lastH = h;
        }
        if (!this._invalidateFlag) {
            return;
        }
        this._invalidateFlag = false;

        if (this._container === undefined) {
            return;
        }
        const _container = this._container;
        _container.alpha = this._alpha;
        if (this.type === 1) {
            if (this._image) {
                this.removeImage(this._image, _container);
            }
            const { _color, _radius, _rect } = this;
            if (_color === undefined && _rect && _rect.parent) {
                _container.removeChild(_rect);
                return;
            }
            if (this._rect === undefined) {
                this._rect = new vf.Graphics();
            }
            if (_color) {
                this.drawRect(this._rect, _color, w, h, _radius, this._fillAlpha);
                if (!this._rect.parent) {
                    _container.addChild(this._rect);
                }
            }
        } else if (this.type === 2) {
            if (this._mask === undefined) {
                return;
            }
            if (this._rect && this._rect.parent) {
                _container.removeChild(this._rect);
            }
            const { _repeat, _src, _size, _mask } = this;
            if ((this._oldRepeat != _repeat) || (this._oldSrc != _src)) {
                this._oldRepeat = _repeat;
                if (this._image) {
                    this._image.destroy();
                }
                switch (_repeat) {
                    case "no-repeat":
                        this._image = new vf.Sprite(getTexture(_src));
                        break;
                    case "repeat":
                        this._image = new vf.TilingSprite(getTexture(_src));
                        break;
                }
                if (this._image) {
                    this._image.mask = _mask;
                }

            }
            const image = this._image;
            if (image) {
                if (_src === undefined) {
                    this.removeImage(image, _container);
                    return;
                }
                if (image instanceof vf.TilingSprite) {
                    image.tilePosition.set(this._positionX, this._positionY);
                } else {
                    image.position.set(this._positionX, this._positionY);
                }
                this.drawRect(_mask, 0x000000, w, h, this._radius)
                if (_size !== undefined) {
                    image.width = _size[0];
                    image.height = _size[1];
                } else {
                    if (image.texture.width > 1) {
                        image.width = image.texture.width;
                        image.height = image.texture.height;
                    }
                }
                if (!_mask.parent) {
                    _container.addChild(_mask);
                }
                if (!image.parent) {
                    _container.addChild(image);
                }
            }
        }
    }

    private drawRect(bg: vf.Graphics, _color: number | number[] | number[][] | undefined, w: number, h: number, _radius: number, _alpha = 1) {
        bg.clear();
        if (typeof _color === 'number') {
            bg.beginFill(_color, _alpha);
        } else {
            updateTextureFill(bg, {
                canvas: this.canvas,
                context: this.context,
                texture: this.texture,
                width: w,
                height: h,
                anchorX: 0,
                anchorY: 0,
                style: {
                    fillColor: _color as number[],
                    fillGradientStops: this._fillGradientStops,
                    fillGradientType: this.fillGradientType
                }
            });
        }

        bg.drawRoundedRect(0, 0, w, h, _radius);
        bg.endFill();
    }
    private removeImage(image: vf.Sprite | vf.TilingSprite, _container: vf.Container) {
        if (this._mask && this._mask.parent) {
            _container.removeChild(this._mask);
        }
        if (image.parent) {
            _container.removeChild(image);
        }

    }

    public release() {

        if (this._mask && this._mask.parent) {
            this._mask.destroy();
        }
        if (this._image) {
            this._image.destroy();
            this._image = undefined;
        }
        if (this._rect) {
            this._rect.destroy();
            this._rect = undefined;
        }

        if (this.canvas) {
            this.canvas.height = this.canvas.width = 0;
            this.context = undefined;
            this.canvas = undefined;
        }

        if (this._container) {
            this._container.destroy();
            if (this._container.parent) {
                this._container.parent.removeChild(this._container);
            }
            this._container = undefined;
        }
        this._invalidateFlag = false;
        this._type = 0;
        this.parent = undefined;
    }

}