import { DisplayObject } from "../core/DisplayObject";
import { IAudioOption } from "vf.js";
import { getSound, now } from "../utils/Utils";
import { registrerAudioGroup, unRegistrerAudioGroup, updateAudioGroupStates } from "../interaction/AudioGroupController";
/**
 * 
 * 
 *  canplaythrough
 *
 *  play
 *
 *  pause
 *
 *  error
 *
 *  timeupdate
 *
 *  ended
 * 
 * @example let audio = new vf.gui.Audio();
 * 
 * 
 */

export enum PlayerStates {
    init = 'init',// 
    play = 'play', // 
    pause = 'pause',//
    end = 'end'   //
}

export class Audio extends DisplayObject {

    private audio?: vf.IAudio;
    private _id: string;
    private _src: any;

    private _autoplay = false;
    private _loop = false;
    private _playbackRate = 1;
    private _volume = 1;
    private _audioUid = ""; // IAudio id
    private _playerStates = PlayerStates.init;



    private stoping: any;
    public constructor() {
        super();
        this._id = now().toString();
        this._audioUid = this.uuid.toString();
        this.audioGroup = "";
        vf.utils.deprecation("1.0.0", "Audio");
    }

    public set audioUid(value: string) {
        this._audioUid = value;
    }

    private initAudio() {
        const o: IAudioOption = {
            autoplay: this._autoplay,
            loop: this._loop,
            playbackRate: this._playbackRate,
            volume: this._volume
        }

        this.audio = vf.AudioEngine.Ins().createAudio(this._audioUid, this._src, o);

        /**
        * 
        */
        this.audio.on("canplay", (e: any) => {
            this.emit("canplay", e);
        }, this);
        this.audio.on("canplaythrough", (e: any) => {
            this.emit("canplaythrough", e)
        }, this);
        this.audio.on("play", (e: any) => {
            this.emit("play", e);
        }, this);
        this.audio.on("pause", (e: any) => {
            this.emit("pause", e);
        }, this);
        this.audio.on("error", (e: any) => {
            this.emit("error", e);
        }), this;
        this.audio.on("timeupdate", (e: any, f: number) => {
            this.emit("timeupdate", e, f)
        });
        this.audio.on("ended", (e: any) => {
            this.emit("ended", e);
            this.dispose();
        }, this);
    }

    //~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
    /**
    * src 3 url base64 arraybuffer;
    */
    public set src(value) {
        const o = getSound(value);
        if (typeof (o) === "object" && o.url) {
            this._src = o.url
        } else {
            this._src = value;
        }
        this.audio && this.dispose();
        this._playerStates = PlayerStates.init;
        this.invalidateProperties();
    }

    public get src() {
        return this._src;
    }

    public set autoplay(value) {
        this._autoplay = value;
    }

    public get autoplay() {
        return this._autoplay;
    }

    public set loop(value) {
        this._loop = value;
        if (this.audio) {
            this.audio.loop = this._loop;
        }
    }

    public get loop() {
        if (this.audio) {
            return this.audio.loop;
        }
        return false;
    }

    public set playbackRate(value) {
        this._playbackRate = value;
        if (this.audio) {
            this.audio.playbackRate = this._playbackRate;
        }
    }

    public get playbackRate() {
        if (this.audio) {
            return this.audio.playbackRate;
        }
        return 0;
    }

    public set volume(value) {
        this._volume = value;
        if (this.audio) {
            this.audio.volume = this._volume;
        }
    }
    public get volume() {
        if (this.audio) {
            return this.audio.volume;
        }
        return 0;
    }

    /**/
    public get duration() {
        if (this.audio) {
            return this.audio.duration;
        }
        return 0;
    }
    public get paused() {
        if (this.audio) {
            return this.audio.paused;
        }
        return false;
    }


    /**
    * 
    */
    public get audioGroup(): string | undefined {
        return this._groupName;
    }

    public set audioGroup(value: string | undefined) {
        if (value === undefined) {
            unRegistrerAudioGroup(this)
        }
        if (this._groupName == value) {
            return;
        }
        this._groupName = value;//registrerCheckGroup
        registrerAudioGroup(this);
    }
    public get playerStates() {
        return this._playerStates;
    }

    public get isPlaying() {
        if (this._playerStates === PlayerStates.play) {
            return true;
        }
        return false;
    }

    /**
    * ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
    *    */
    /**
     * 
     *
     *  await sound.play()
     * @param {number} [time] - 
     * @param {number} [offset] - 
     * @param {number} [length] - 
     */

    public play(time?: number, offset?: number, length?: number) {
        //
        this._playerStates = PlayerStates.play;
        updateAudioGroupStates(this);
        if (this.stage?.syncManager && this.stage.syncManager.resumeStatusFlag) {
            return;
        }

        //nativeAudio
        if (this.useNativeAudio()) {
            this.nativeEmit(this._src, 'playAudio');
            this.emit("play", this);
            return;
        }

        if (!this.audio) {
            this.initAudio();
        }

        if(this.audio) {
            this.audio.play(time, offset, length);
        }
        
    }

    /**
    * 
    * @param time (optional) X
    */
    public stop(time?: number) {
        //

        if (this.stage?.syncManager && this.stage.syncManager.resumeStatusFlag) {
            return;
        }

        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const that = this;
        if (this.stoping) clearTimeout(this.stoping)
        if (time) {
            this.stoping = setTimeout(() => {
                this._playerStates = PlayerStates.end;
                that.emit("stop", that) // end  
            }, time);
        } else {
            this._playerStates = PlayerStates.end;
            this.emit("stop", this);
        }

        //nativeAudio
        if (this.useNativeAudio()) {
            this.nativeEmit(this._src, 'pauseAudio')
            return;
        }
        this.audio && this.audio.stop(time);

    }
    /**
    * 
    */
    public pause() {
        this._playerStates = PlayerStates.pause;
        //
        if (this.stage?.syncManager && this.stage.syncManager.resumeStatusFlag) {
            return;
        }
        //nativeAudio
        if (this.useNativeAudio()) {
            this.nativeEmit(this._src, 'pauseAudio')
            this.emit("pause", this);
            return;
        }
        this.audio && this.audio.pause();
    }
    /**
    * 
    */
    public dispose() {
        this._playerStates = PlayerStates.end;
        if (this.audio) {
            vf.AudioEngine.Ins().map.delete(this._audioUid);
            this.audio.removeAllListeners();
            this.audio.dispose();
            this.audio = undefined;
        }
        if (this.parent) {
            this.parent.removeChild(this);
        }
        //nativeAudio
        if (this.useNativeAudio()) {
            this.nativeEmit(this._src, 'pauseAudio')
            return;
        }
    }

    public release() {
        super.release();
        this.dispose();
    }

    /**
    * .~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
    */

    protected commitProperties() {
        if (!this.audio) {
            this.initAudio();
        }
        if (this._autoplay) {
            this.play();
        }
    }

    private useNativeAudio() {
        return (this.stage as any)?.config?.vfvars.useNativeAudio;
    }

    private nativeEmit(url: string, typeTag: string) {
        this.stage?.getSystemEvent().emit('message', {
            code: 'native',
            type: 'native',
            level: 'command',
            data: {
                type: typeTag,
                src: url,
                id: this._audioUid,
                mode: 'sound',
                signalling: false,
            }
        });
    }
}
