import { TickerShared } from "../../core/Ticker";
import { deepCopy } from "../../utils/Utils";
import { Animation } from "../animation/Animation";
export class AnimationBaseData { //   gotoplay
    public x = NaN;
    public y = NaN;
    public rotation = NaN;
}

export class AnimationBase extends vf.utils.EventEmitter {

    private _sprite: any;
    protected _spriteData?: AnimationBaseData;//
    private _options: any;
    private _duratime = NaN; //
    protected _currenTimer = NaN; //
    protected _revertAni = false; // sprite
    private _playing = false;
    private _valuesStart: any;
    protected _toValue: any;//
    private _addTick = false;

    constructor(sp?: any, options?: any, duratime?: number) {
        super();
        if (sp) {
            this.sprite = sp;
        }
        if (options) {
            this._options = options;
        }
        if (duratime) {
            this._duratime = duratime;
        }
    }
    public get playing() {
        return this._playing;
    }

    public get sprite() {
        return this._sprite;
    }

    public set sprite(value) {
        this._sprite = value;
    }

    public get options() {
        return this._options;
    }

    public set options(value) {
        this._options = value;
    }

    public get revertAni() {
        return this._revertAni;
    }

    protected cacheSprite() {
        if (this.sprite) {
            const object = this.sprite;
            const opt = this._options;
            this._valuesStart = {};
            for (const property in opt) {
                const start = object && object[property] && deepCopy(object[property]);
                this._valuesStart[property] = start;
            }
        }
    }

    //
    public play(curTimer = NaN) {
        // this.emit("play", this);
        if (this._revertAni === true) {
            this.cacheSprite();
        }
        this._playing = true;
        if (isNaN(curTimer)) {
            isNaN(this._currenTimer) && (this._currenTimer = 0);
        } else {
            this._currenTimer = curTimer;
        }
        if (!this._addTick) {
            this._addTick = true;
            TickerShared.add(this.updata, this);
        }
    }

    public pause() {
        this._playing = false;
        this.gotoAndStop(this._currenTimer);
    }
    /**
     *  
     */
    public complete() {
        TickerShared.remove(this.updata, this);
        this._playing = false;
        if (this._revertAni === true) {
            //
            this.setTimeStates(0);
        }
        this.emit("complete");
    }

    public gotoAndPlay(timer: number) {
        this.goto(timer);
        this._playing = true;
        this.play(timer);
    }

    public gotoAndStop(timer: number) {
        this._playing = false;
        this.goto(timer);
    }
    /**
     * 
     */
    public goto(timer: number) {
        //
        this._currenTimer = timer;
        this.setTimeStates(timer);
    }
    /**
     * 
     * sprite
     */
    private setTimeStates(timer: number) {
        const d = this.getTimeData(timer);
        if (this._sprite && d) {
            !isNaN(d.x) && (this._sprite.x = d.x);
            !isNaN(d.y) && (this._sprite.y = d.y);
            !isNaN(d.rotation) && (this._sprite.rotation = d.rotation);
        }
    }
    /**
     * sprite
     *   
     *   
     */
    public getTimeData(timer: number) {
        return this._spriteData;
    }

    /**
     * 
     */
    protected updata(delta: number) {
        if (this._playing === false) {
            return;
        }
        if (this._duratime > 0 && this._currenTimer >= this._duratime) {
            this.complete();
        } else {
            this._currenTimer += TickerShared.deltaMS;
        }
    }

    //
    public release() {
        this.emit("release");
        this.removeAllListeners();
        TickerShared.remove(this.updata, this);
        this._addTick = false;
        this._sprite = undefined;
        this._options = undefined;
        this._duratime = NaN;
        this._playing = false;
    }




}