import { updateTextureFill } from '../core/plugs/LineGradient';
import { GraphBase } from './private/GraphBase'

export class Arrow extends GraphBase {

    public constructor() {
        super();
    }

    private _angle: number = Math.PI / 6; //30

    public drawGraph() {
        const graphics = this.graphics;
        graphics.clear();
        if (this._lineWidth < 0) {
            console.log("0");
            return;
        }
        graphics.lineStyle(this._lineWidth, this._lineColor, this._lineAlpha, 0.5, false, this.lineType);


        if (this.width > 0 && this.height > 0) {
            //
            const w = this._anchorX ? this.width * this._anchorX : 0;
            const h = this._anchorY ? this.height * this._anchorY : 0;
            graphics.moveTo(0 - w, this.height * 0.5 - h);
            graphics.lineTo(this.width - w, this.height * 0.5 - h);

            // 
            const path = this.getArrowPath();
            let full = false;

            if (this.color) {
                graphics.beginFill(this._color, this._fillAlpha);
                full = true;
            } else {
                if(updateTextureFill(this.graphics,this)){ // 
                    full = true;
                }
            }


            graphics.drawPolygonOne(path, full);
            graphics.endFill();




        }



    }

    private getArrowPath(): any {
        const w = this._anchorX ? this.width * this._anchorX : 0;
        const h = this._anchorY ? this.height * this._anchorY : 0;
        const path: vf.Point[] = [];
        const angle = this._angle;
        const height = this.height;
        const width = this.width;
        const xD = height * 0.5 * Math.tan(Math.PI / 2 - angle);
        const yD = height * 0.5;
        const x = width - xD;
        const point1 = new vf.Point(x - w, 0 - h);
        const point2 = new vf.Point(width - w, yD - h);
        const point3 = new vf.Point(x - w, yD * 2 - h);
        path.push(point1, point2, point3);
        return path;
    }
}