import { AnimationBase, AnimationBaseData } from "../private/AnimationBase";
import { TweenEvent } from '../../event/TweenEvent';
import { Tween } from '../../tween/Tween';

export class AnimationJitter extends AnimationBase {
    private tween: any;

    constructor(sp?: any, options?: any, duratime?: number) {
        super(sp, options, duratime);
        this._revertAni = true;
    }


    public play(timer = 0) {
        if (this.playing === true) {
            return;
        }
        super.play(timer);
        const sp = this.sprite; //
        this.tween && this.clearTween();

        if (!this.tween) {
            this.tween = new Tween(sp)
                .to(this.options, 35)
                .repeat(Infinity)
                .easing(vf.gui.Easing.Linear.None)
                .yoyo(true)
                .delay(10);
            this.tween.on(TweenEvent.complete, this.complete.bind(this));
        }


        this.tween.start();


    }

    public gotoAndStop(timer: number) {
        this.tween && this.tween.gotoAndStop(timer);
        super.gotoAndStop(timer);
    }


    // public getTimeData(timer: number) {
    //     const d = new AnimationBaseData();
    //     if (timer <= 2000) {
    //         d.x = 200 + 200 * (timer / 1000);
    //     }

    //     return d;
    // }


    private clearTween() {
        const sp = this.sprite; //
        if (this.tween) {
            this.tween.release();
            this.tween = undefined;
        }
    }

    public complete() {
        this.clearTween();
        super.complete();

    }

    public release() {
        this.clearTween();
        super.release();

    }




}