import { DisplayObject } from "../../core/DisplayObject";
import { MaskSprite } from "../../core/MaskSprite";
import { hexToInt, rgbToNumber } from "../../utils/Utils";
import { GraphicsCore } from "./GraphicsCore"

/**
 * 
 */
export class GraphBase extends DisplayObject implements MaskSprite {

    public constructor() {
        super();
        this.graphics = new GraphicsCore();
        this.container.addChild(this.graphics);
    }

    public readonly graphics: GraphicsCore;

    public canvas?: HTMLCanvasElement;
    public context?: CanvasRenderingContext2D;
    public texture?: vf.Texture;

    /**  */
    public maskSprite() {
        return this.graphics;
    }

    /**
     * 
     */
    protected _radius = 0;
    public get radius() {
        return this._radius;
    }
    public set radius(value) {
        this._radius = value;
        this.invalidateSize();
        this.invalidateDisplayList();
    }
    /**
     * 
     */
    protected _alignment = 0.5;
    public get alignment() {
        return this._alignment;
    }
    public set alignment(value) {
        this._alignment = value;
        this.invalidateDisplayList();
    }
    /**
     * 
     */
    protected _lineType: "full" | "dash" = "full";
    public get lineType(): "full" | "dash" {
        return this._lineType;
    }
    public set lineType(value: "full" | "dash") {
        if (this._lineType === value) {
            return;
        }
        this._lineType = value;
        this.invalidateDisplayList();
    }
    /**
     * 
     */
    protected _lineColor: any = 0xffffff;
    public get lineColor() {
        return this._lineColor;
    }
    public set lineColor(value) {
        if (this._lineColor === value) {
            return;
        }
        if (Array.isArray(value)) {
            value = rgbToNumber(value[0], value[1], value[2]);
        }
        let color = 0xffffff;
        if(!isNaN(value))
            color = hexToInt(value, color);
        this._lineColor = color;
        this.invalidateDisplayList();
    }
    /**
     * 
     */
    protected _lineWidth = 0;
    public get lineWidth() {
        return this._lineWidth;
    }
    public set lineWidth(value) {
        this._lineWidth = value;
        this.invalidateDisplayList();
    }
    /**
     * 
     */
    protected _lineAlpha = 1;
    public get lineAlpha() {
        return this._lineAlpha;
    }
    public set lineAlpha(value) {
        this._lineAlpha = value;
        this.invalidateDisplayList();
    }
    /**
     * 
     */
    protected _fillAlpha = 1;
    public get fillAlpha() {
        return this._fillAlpha;
    }
    public set fillAlpha(value) {
        this._fillAlpha = value;
        this.invalidateDisplayList();
    }


    /** 
     *  
     */
    public get fillColor() {
        return this._color || this.style.fillColor;
    }
    public set fillColor(value: number[]) {
        if (Array.isArray(value[0])) {
            this._color = undefined;
            if (this.canvas === undefined) {
                this.canvas = document.createElement("canvas");
                this.context = this.canvas.getContext("2d") as CanvasRenderingContext2D;
                this.texture = vf.Texture.from(this.canvas);
            }
            return; // color
        }
        this.color = rgbToNumber(value[0], value[1], value[2]);
    }

    /** 
     *  
     */
    protected _color?: any;
    public get color() {
        return this._color;
    }
    public set color(value) {
        if (this._color === value) {
            return;
        }
        let color = undefined;
        color = hexToInt(value, color)
        this._color = color;
        this.invalidateDisplayList();
    }
    /**
     *  0-1
     */
    public get anchorX() {
        return this._anchorX;
    }
    public set anchorX(value) {
        this._anchorX = value;
        this.invalidateDisplayList();
    }
    /**
     *  0-1
     */
    public get anchorY() {
        return this._anchorY;
    }
    public set anchorY(value) {
        this._anchorY = value;
        this.invalidateDisplayList();
    }

    /**
     * 
     */
    public drawGraph() {
        //
    }

    public release() {
        super.release();
        if (this.graphics.parent) {
            this.graphics.parent.removeChild(this.graphics).destroy();
        }

        if (this.canvas) {
            this.canvas.height = this.canvas.width = 0;
            this.context = undefined;
            this.canvas = undefined;
        }
    }

    protected updateDisplayList(unscaledWidth: number, unscaledHeight: number) {
        this.drawGraph();
        super.updateDisplayList(unscaledWidth, unscaledHeight);
    }


}