import { DisplayObject } from "../core/DisplayObject";
import { InteractionEvent } from "../event/InteractionEvent";

/**
 * UI
 * @private
 */
export const _items: DisplayObject[] = [];
export const _dropTargets: any[] = [];  //
/**
 * 
 * @param item UI
 * @param e 
 * @returns true|false
 * @since 1.0.0
 */
export function add(item: DisplayObject, e: InteractionEvent) {
    item.attach.dragDropEventId = e.data.identifier;
    if (_items.indexOf(item) === -1) {
        _items.push(item);
        return true;
    }
    return false;
}

export function remove(item: DisplayObject) {
    const index = _items.indexOf(item); 
    if (index > -1) {
        _items.splice(index,1);
        return true;
    }
    return false;
}

/**
 * 
 * @param group 
 * @param target 
 */
export function addDropTarget(group: string, target: DisplayObject) {
    let obj: any = {
        group,
        target
    }
    _dropTargets.push(obj);
}

/**
 * 
 * @param group 
 */
export function getDropTargets(group: string) {
    let list: DisplayObject[] = [];
    _dropTargets.forEach(item => {
        if (item.group === group) {
            list.push(item.target);
        }
    })
    return list;
}

export function removeDropTarget(target: DisplayObject) {
    let index: number | undefined;
    for (let i = 0; i < _dropTargets.length; ++i) {
        if (_dropTargets[i].target === target) {
            index = i;
            break;
        }
    }
    if (index !== undefined) {
        _dropTargets.splice(index, 1);
    }
}

/**
 * 
 * @param item UI
 * @returns flase | item
 */
export function getItem(item: DisplayObject) {
    let index: number | undefined;
    for (let i = 0; i < _items.length; i++) {
        if (_items[i] === item) {
            index = i;
            break;
        }
    }

    if (index !== undefined) {
        _items.splice(index, 1);
        return item;
    }
    else {
        return false;
    }
}

/**
 * 
 * @param e 
 * @param group 
 */
export function getEventItem(e: InteractionEvent, group: string | undefined) {
    let item = null, index: number | undefined;
    const id = e.data.identifier;

    for (let i = 0; i < _items.length; i++) {
        if (_items[i].attach.dragDropEventId === id) {
            if (group !== _items[i].attach.dragGroup && _items[i].attach.dragGroup !== "") {
                return false;
            }
            item = _items[i];
            index = i;
            break;
        }
    }

    if (index !== undefined) {
        _items.splice(index, 1);
        return item;
    }
    else {
        return false;
    }
}