import * as tween from "../tween/private/index";
import { TickerShared } from "./Ticker";
import { DisplayLayoutAbstract } from "./DisplayLayoutAbstract";
import { DisplayObject } from "./DisplayObject";
import { releaseAllTimeLine } from "../tween/private/core"
import validatorShared from "./DisplayLayoutValidator";
import { SyncManager } from "../interaction/SyncManager";
import { Scheduler } from "./Scheduler";
import { ClickEvent, DragEvent } from "../interaction/Index";

/**
 * UIUI
 *
 * @class 
 * @param width {Number} 
 * @param height {Number} 
 */
export class Stage extends DisplayLayoutAbstract {


    public constructor(width: number, height: number, app: vf.Application) {
        super();
        this.width = width;
        this.height = height;
        this.setActualSize(width, height);
        this.container.name = "Stage";
        this.container.hitArea = new vf.Rectangle(0, 0, width, height);
        this.container.interactive = true;
        this.container.interactiveChildren = true;
        this.$nestLevel = 1;
        this.app = app;
        this.initialized = true;

        if (!TickerShared.started) {
            TickerShared.start();
        }
        TickerShared.add(tween.update, this);

        if (!this.container.parent) {
            this.app.stage.addChildAt(this.container,0);
        }

    }

    public app: vf.Application;
    public syncManager: SyncManager | undefined;
    public resumeStatusFlag = false;  //stage

    /**
     * 
     */
    public originalEventPreventDefault = false;

    public get stageWidth() {
        return this.app.view.width;
    }

    public get stageHeight() {
        return this.app.view.height;
    }

    public get scaleX() {
        return this.container.scale.x;
    }

    public set scaleX(value: number) {
        this.container.scale.x = value;
    }

    public get scaleY() {
        return this.container.scale.y;
    }

    public set scaleY(value: number) {
        this.container.scale.y = value;
    }

    public set Scale(value: vf.Point) {
        this.container.scale.copyFrom(value);
    }

    // /**
    //  * 
    //  * @param display 
    //  * @param {string} [format] - ,  "image/jpeg" or "image/webp".
    //  * @param {number} [quality] -  0 to 1.  is 0.92.
    //  */
    // public extractBase64(target?: vf.gui.DisplayObject,format?: string, quality?: number): any {

    // }

    /**
     * 
     */
    private _syncInteractiveFlag = false; //TODO:false
    public set syncInteractiveFlag(value: boolean) {
        this._syncInteractiveFlag = value;
        if (value) {
            if (!this.syncManager) {
                this.syncManager = new SyncManager(this);
                SyncManager.instance = this.syncManager;
            }
            this.syncManager.start();
        }
        else {
            if (this.syncManager) {
                this.syncManager.stop();
            }
        }
    }

    public get syncInteractiveFlag() {
        return this._syncInteractiveFlag;
    }

    public getSystemEvent(): vf.utils.EventEmitter {
        //
        return this;
    }

    public sendToPlayer(e: any): void {
        //
    }

    public release() {
        super.release();
        Scheduler.stopAll();
        TickerShared.remove(tween.update, this);
        releaseAllTimeLine();


        this.syncManager && this.syncManager.stop();
        this.syncManager = undefined;
        SyncManager.instance = undefined;
    }

    public releaseAll() {
        Scheduler.stopAll();
        TickerShared.remove(tween.update, this);
        releaseAllTimeLine();
        this.syncManager && this.syncManager.stop();

        this.syncManager = undefined;
        SyncManager.instance = undefined;

        for (let i = this.uiChildren.length - 1; i >= 0; i--) {
            const ui = this.uiChildren[i] as DisplayObject;

            ui.releaseAll();
        }

        this.uiChildren = [];
        this.container.removeAllListeners();
        this.container.removeChildren();
        validatorShared.removeAllListeners();
        validatorShared.removeDepthQueueAll();

        this.app = undefined as any;
    }


    public resize(): void {
        this.container.hitArea = new vf.Rectangle(0, 0, this.width, this.height);
        //this.updateChildren();
    }

    /**
     * player
     * @param msg 
     */
    public receiveFromPlayer(msg: any) {
        if (msg.code == 'syncEvent') {
            let data = msg.data; //{data: eventData, type: 'live/history'}
            this.syncManager && this.syncManager.receiveEvent(data.data, data.type);
        }
    }

    /**
     * 
     */
    private _virCanvas: any
    private getVitualCanvas() {
        if (!this._virCanvas) {
            this._virCanvas = document.createElement("canvas");
        }
        return this._virCanvas
    }
    public screenShots(x: number, y: number, width: number, height: number, endWidth = NaN, endHeight = NaN, displayObject?: vf.DisplayObject): vf.Texture {
        const newCanvas = this.getVitualCanvas();

        const ctx: any = newCanvas.getContext("2d");
        const canvas = this.app.renderer.extract.canvas(displayObject || this.container);
        const w = isNaN(endWidth) ? width : endWidth;
        const h = isNaN(endHeight) ? height : endHeight;
        ctx.clearRect(0, 0, newCanvas.width, newCanvas.height);
        newCanvas.width = w;
        newCanvas.height = h;
        ctx.drawImage(canvas, x, y, width, height, 0, 0, w, h)

        const dataURL = newCanvas.toDataURL("image/png", 1.0);

        return vf.Texture.from(dataURL);

    }
}