import { Label } from "./Label";
import { Image } from "./Image";
import { InputBase } from "./private/InputBase";
import { ComponentEvent } from "../interaction/Index";


/**
 * 
 * 
 * @example let button = new vf.gui.Button();
 * 
 * 
 */
export class Button extends InputBase {

    public constructor() {
        super();
        this.container.buttonMode = true;
        this.img.fillMode = "scale";
        this.img.scale9Grid = [3, 3, 3, 3];
        this.addChild(this.img);

        this.label.sprite.style.fontSize = 18;
        this.addChild(this.label);

        this.on(ComponentEvent.STATE_CHANGE, this.onStateChange, this);
    }

    protected _selectedStr: "AndSelected" | "" = "";
    protected _oldState = "";
    private _offsetY = 0;


    /**  */
    public readonly img = new Image();
    /**  */
    public readonly label = new Label();

    private _text = "";
    /**
     * 
     */
    public get text() {
        return this.label.text;
    }
    public set text(value) {
        this._text = value;
        this.invalidateDisplayList();

    }

    /**
     *  
     */
    public get textOffsetY() {
        return this._offsetY;
    }

    public set textOffsetY(value) {
        if (this._offsetY === value) {
            return;
        }
        this._offsetY = value;
        this.invalidateDisplayList();
    }


    public setSelected(value: boolean) {
        this._oldState = "";
        if(value){
            this._selectedStr = "AndSelected";
        }else{
            
            this._selectedStr = "";
        }
        this.emit(ComponentEvent.CHANGE,this);
        this.onStateChange(this,this.currentState);
    }


    protected updateDisplayList(unscaledWidth: number, unscaledHeight: number) {
        if(isNaN(this.explicitWidth) && isNaN(this.explicitHeight)) {
            return;
        }
        super.updateDisplayList(unscaledWidth, unscaledHeight);
        this.container.hitArea = new vf.Rectangle(0, 0, unscaledWidth, unscaledHeight);

        const img = this.img;
        if(this.width !== img.width) {
            img.width = unscaledWidth;
        }
        if(this.height !== img.height) {
            img.height = unscaledHeight;
        }
        const vh = unscaledHeight + this._offsetY;
        if (this.label.width !== unscaledWidth)
            this.label.width = unscaledWidth;
        if (this.label.height !== vh)
            this.label.height = vh;
        if (this.label.text !== this._text) {
            this.label.text = this._text;

        }

        this.onStateChange(this, this.currentState);

    }

    public release() {

        this.offAll(ComponentEvent.STATE_CHANGE);
        this.img && this.img.parent && this.img.parent.removeChild(this.img);
        this.img.release();
        (this.img as any) = undefined;
        this.label && this.label.parent && this.label.parent.removeChild(this.label);
        this.label.release();
        (this.label as any) = undefined;
        this.removeAllListeners();
        super.release();
    }


    protected onStateChange(label: Button, state: string) {
        if (this._oldState == state) {
            return;
        }
        this._oldState = state;
        this.img.src = (this as any)[state + this._selectedStr];
    }
}