    /**  fontFamily = "\"Comic Sans MS\", cursive, sans-serif" */
    private _fontFamily?: string | string[] = 'Arial';
    private _whiteFamily = ['Arial']; //   
    public get fontFamily() {
        return this._fontFamily;
    }
    public set fontFamily(value) {
        this._fontFamily = value;

        let familyList: string[] = [];
        if (value) {
            let list = [].concat(value as []);
            if (list) {
                list.forEach(element => {
                    if (element && element !== "") {
                        let item: any = element;
                        if (element && this._whiteFamily.indexOf(element as any) < 0) {
                            const o = getSource(element);
                            if (typeof o === 'object' && o.name) {
                                item = o.name;
                            }
                        }
                        familyList.push(item);
                    }
                });
            }
        }
        if (familyList.length === 0) {
            familyList = ['Arial'];
        }
        CSSFunction.updateFontStyle(this.parent, "fontFamily", deepCopy(familyList));
        this.parent.invalidateDisplayList();
    }
