    public set stroke(value) {
        this._stroke = value;
        if (Array.isArray(value)) {
            if (value.length === 0) {
                value = undefined;
            } else {
                value = `rgba(${value[0]}, ${value[1]}, ${value[2]}, ${value[3]})`;
            }
        }
        CSSFunction.updateFontStyle(this.parent, "stroke", value);
    }
