import { updateTextureFill } from '../core/plugs/LineGradient';
import { GraphBase } from './private/GraphBase'
export class Polygon extends GraphBase {
    public constructor() {
        super();
    }

    private path: vf.Point[] = [];
    private PI_2 = Math.PI * 2;
    private _triangleNum = 5; //   1
    private RADIUS_V = 1; //
    public drawGraph() {
        const graphics = this.graphics;
        graphics.clear();

        this.path = [];
        if (this._triangleNum <= 1 || this._lineWidth < 0) {
            console.log("0");
            return;
        }
        graphics.lineStyle(this._lineWidth, this._lineColor, this._lineAlpha, 0.5, false, this.lineType);
        this.setStarPath();
        if (this.color) {
            graphics.beginFill(this._color, this._fillAlpha);
        } else {
            updateTextureFill(this.graphics,this);
        }
        graphics.drawPolygonOne(this.path);
        graphics.endFill();

    }

    public get triangleNum() {
        return this._triangleNum;
    }

    public set triangleNum(value) {
        this._triangleNum = value;
        this.invalidateDisplayList();
    }

    private setStarPath() {
        const radius = this.RADIUS_V;
        const triangleNum = this._triangleNum;
        const PI_2 = this.PI_2;
        const startAngle = (-1 * Math.PI / 2);
        const len = triangleNum;
        const delta = PI_2 / len;

        const scaleWidth = this.width > 0 ? this.width / (radius * 2) : 1;
        const scaleHeight = this.height > 0 ? this.height / (radius * 2) : 1;
        const anchorX = this._anchorX ? this._anchorX : 0;
        const anchorY = this._anchorY ? this._anchorY : 0;
        for (let i = 0; i < len; i++) {
            const angle = (i * delta) + startAngle;
            const pX = (radius * Math.cos(angle) + anchorX + radius) * scaleWidth;
            const pY = (radius * Math.sin(angle) + anchorY + radius) * scaleHeight;
            const point = new vf.Point(pX, pY);
            this.path.push(point);
        }
    }
}