import { DisplayObject } from "../core/DisplayObject";
import { Video } from "../display/Video";

interface VideoGroupObject {
    groups: { [key: string]: { [value: string]: Video } };
    values: { [key: string]: string | undefined };
}

export const _videoGroupObject: VideoGroupObject = {
    groups: {},
    values: {}
}


/* video 
* 
* checkGroups = [key]:{["value"]:cb}
*/
export function registrerVideoGroup(cb: Video) {
    const name = cb.videoGroup;
    if (name || name === "") {
        let group = _videoGroupObject.groups[name];
        if (!group)
            group = _videoGroupObject.groups[name] = {};

        group[cb.uuid.toString()] = cb;

        // if (cb.checked)
        _videoGroupObject.values[name] = cb.uuid.toString();
    }
}

/**
 * 
 * @param cb video
 */
export function unRegistrerVideoGroup(cb: Video) {
    if ((cb.videoGroup || cb.videoGroup === "") && _videoGroupObject.groups[cb.videoGroup]) {
        delete _videoGroupObject.groups[cb.videoGroup][cb.uuid.toString()];
        let isKey = false;
        for (const key in _videoGroupObject.groups[cb.videoGroup]) {
            if (key) isKey = true;
            break;
        }
        if (!isKey) {
            delete _videoGroupObject.groups[cb.videoGroup];
        }
        // if (cb.checked)
        _videoGroupObject.values[cb.videoGroup] = undefined;
    }
}

/** checkbox  */
export function updateVideoGroupStates(cb: Video) {
    if (cb.videoGroup || cb.videoGroup === "") {
        const group = _videoGroupObject.groups[cb.videoGroup];
        for (const val in group) {
            const b = group[val];
            if (b !== cb) {
                if (b.isPlaying) {
                    b.pause();
                }
            } else {
                if (!b.isPlaying) {
                    b.play();
                }
            }
        }
        _videoGroupObject.values[cb.videoGroup] = cb.uuid.toString();
    }
}
