import { Label } from "./Label";
import { DisplayObject } from "../core/DisplayObject";
import { Image } from "./Image";
import { Rect } from "./Rect";
import { Scheduler } from "../core/Scheduler";
import { TouchMouseEvent, InteractionEvent } from "../interaction/Index";
import { Container } from "./Container";
import { DisplayLayoutAbstract } from "../core/DisplayLayoutAbstract";

export interface ButtonInfo {
    btnNormal?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    btnHover?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    btnPressed?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    text?: string
}

export class Alert extends DisplayObject {

    public static show(container: DisplayLayoutAbstract, content: string, textAlign: string = 'center', duration: number = 2000, icon?: number | string | vf.Texture, title?: string, buttons?: string[], x?: number, y?: number) {
        var alert: Alert = new Alert();
        alert.title = title || '';
        alert.content = content || '';
        alert.buttons = buttons || [];
        alert.duration = duration || 0;
        alert.icon = icon;
        alert.autoShow = true;
        alert.textAlign = textAlign;
        alert.x = x || (container.width / 2);
        alert.y = y || (container.height / 2);
        container.addChild(alert);
        return alert;
    }

    public constructor() {
        super();
        this.visible = false;
        this.scaleX = this.scaleY = 0.01;
        this.width = 300;
        this.height = 100;
    }

    /**
     * 
     */
    private _title = "";
    public set title(value: string) {
        this._title = value;
        this.invalidateProperties();
    }
    public get title() {
        return this._title;
    }

    /**
     * 
     */
    private _content = "";
    public set content(value: string) {
        this._content = value;
        this.invalidateProperties();
    }
    public get content() {
        return this._content;
    }

    /**
     * 
     */
    private _buttons: string[] = [];
    public set buttons(value: string[]) {
        this._buttons = value;
        this.invalidateProperties();
    }
    public get buttons() {
        return this._buttons;
    }

    private _imgButtons: ButtonInfo[] = [];
    public set imgButtons(value: ButtonInfo[]) {
        this._imgButtons = value;
        this.invalidateProperties();
    }
    public get imgButtons() {
        return this._imgButtons;
    }

    /**
     * 
     */
    public autoShow: boolean = false;

    /**
     * 
     */
    public autoRelease: boolean = false;

    /**
     * 
     */
    public duration = 0;

    /**
     *   //left  center
     */
    public textAlign = 'left';

    /**
     * 
     */
    public buttonWidth = 80;
    public buttonHeight = 30;

    /**
     * 
     */
    public maskColor = 0;
    public maskOpacity = 0.1;

    /**
     * 
     */
    public icon?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    public background?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    public btnNormal?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    public btnHover?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    public btnPressed?: string | number | vf.Texture | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    public backgroundColor = 0xeeeeee;
    public titleColor = 0;
    public contentColor = 0x333333;
    public btnColors: number[] = [0x1057d5, 0xdddddd, 0xd10043, 0xdddddd];
    public btnTextColors: number[] = [0xeeeeee, 0x333333, 0xeeeeee, 0x333333];

    /**
     * 
     */
    private _isShowing = false;

    /**
     * 
     */
    private _bgDisplay?: Image | Rect;
    private _titleDisplay?: Label;
    private _lineDisplay?: Rect;
    private _contentDisplay?: Label;
    private _iconDisplay?: Image;
    private _buttonsDisplays: any[] = [];


    protected commitProperties(): void {
        this.initDisplay();
        if (this.autoShow) {
            this.show();
        }
    }

    /**
     * 
     */
    private initDisplay() {
        //mask
        let mask = new Rect();
        mask.color = this.maskColor;
        mask.alpha = this.maskOpacity;
        mask.width = mask.height = 2000;
        mask.anchorX = mask.anchorY = 0.5;
        mask.interactabled = true;
        mask.on(TouchMouseEvent.onPress, () => {
            //do nothing
        });
        this.addChild(mask);

        //bg
        if (this.background !== undefined && this.background !== '') {
            this._bgDisplay = new Image();
            this._bgDisplay.src = this.background;
        } else {
            this._bgDisplay = new Rect();
            this._bgDisplay.color = this.backgroundColor;
            this._bgDisplay.radius = 10; //

        }
        this._bgDisplay.anchorX = 0.5;
        this._bgDisplay.anchorY = 0.5;
        this.addChild(this._bgDisplay);

        //icon
        if (this.icon !== undefined && this.icon !== '') {
            this._iconDisplay = new Image();
            this._iconDisplay.src = this.icon;
            this._iconDisplay.anchorX = this._iconDisplay.anchorY = 0.5;
            this._iconDisplay.width = 50;
            this._iconDisplay.height = 50;
            this.addChild(this._iconDisplay);
        }

        if (this.title) {
            this._titleDisplay = new Label();
            this._titleDisplay.text = this.title;
            this.addChild(this._titleDisplay);
            this._titleDisplay.style.fontSize = 20;
            this._titleDisplay.color = this.titleColor;

            this._lineDisplay = new Rect();
            this._lineDisplay.width = this.width - 40;
            this._lineDisplay.height = 2;
            this._lineDisplay.color = this.titleColor;
            this._lineDisplay.anchorY = this._lineDisplay._anchorX = 0.5;
            this._lineDisplay.alpha = 0.2;
            this.addChild(this._lineDisplay);
        }

        if (this.content) {
            this._contentDisplay = new Label();
            this._contentDisplay.text = this.content;
            this._contentDisplay.color = this.contentColor;
            this._contentDisplay.style.fontSize = 19;

            if (this._iconDisplay) {
                this._contentDisplay.style.wordWrap = true;
                this._contentDisplay.style.wordWrapWidth = this.width - 60 - 80;  //icon
            }
            else {
                this._contentDisplay.style.wordWrap = true;
                this._contentDisplay.style.wordWrapWidth = this.width - 60;
            }
            this.addChild(this._contentDisplay);
        }

        //buttons
        if (this.imgButtons.length > 0) {
            this.imgButtons.forEach((item, index) => {
                let btnContainer = new Container();
                this.addChild(btnContainer);
                let btn = new Image();
                btn.src = item.btnNormal;
                btnContainer.addChild(btn);
                btn.id = index + "_imgButton";
                btn.interactabled = true;
                btn.anchorY = btn.anchorX = 0.5;
                btn.width = this.buttonWidth;
                btn.height = this.buttonHeight;
                this._buttonsDisplays.push(btn);
                btn.on(TouchMouseEvent.onHover, this.onHover, this);
                btn.on(TouchMouseEvent.onPress, this.onPress, this);
                btn.on(TouchMouseEvent.onClick, this.onClick, this);

                if (item.text) {
                    let label = new Label();
                    label.text = item.text;
                    label.style.fontSize = 20;
                    label.color = this.btnTextColors[index];
                    btnContainer.addChild(label);

                    label.pivotX = label.width / 2;
                    label.pivotY = label.height / 2;

                    if (btn.width < label.width + 20) {
                        btn.width = label.width + 20;
                    }
                }
                btn.height = 35;
            })
        }
        else if (this.buttons.length > 0) {
            this.buttons.forEach((item, index) => {
                let btnContainer = new Container();
                this.addChild(btnContainer);
                let btn = null;
                if (this.btnNormal !== undefined && this.btnNormal !== '') {
                    btn = new Image();
                    btn.src = this.btnNormal;
                    btn.id = index + "_image";
                }
                else {
                    btn = new Rect();
                    if (this.btnColors.length > index) {
                        btn.color = this.btnColors[index];
                    }
                    btn.radius = 5;
                    btn.lineWidth = 1;
                    btn.lineColor = 0x888888;
                    btn.id = index + "_rect";
                }
                btn.interactabled = true;
                btn.anchorY = btn.anchorX = 0.5;
                btnContainer.addChild(btn);
                this._buttonsDisplays.push(btn);
                btn.on(TouchMouseEvent.onHover, this.onHover, this);
                btn.on(TouchMouseEvent.onPress, this.onPress, this);
                btn.on(TouchMouseEvent.onClick, this.onClick, this);

                let label = new Label();
                label.text = item;
                label.style.fontSize = 20;
                label.color = this.btnTextColors[index];
                btnContainer.addChild(label);
                label.pivotX = label.width / 2;
                label.pivotY = label.height / 2;
                btn.height = 30;
                btn.width = label.width + 20;
            })
        }
        this._updateDisplayList()
    }

    private _updateDisplayList() {
        //
        let _height = 0;  // title
        if (this._titleDisplay) {
            _height += 40;
        }
        _height += 90;
        if (this._contentDisplay) {
            let offsetY = this._contentDisplay.height - 50 > 0 ? this._contentDisplay.height - 50 : 0;
            _height += offsetY;
        }
        if (this._buttonsDisplays.length > 0) {
            _height += 40;  // buttons
        }
        this.height = _height;
        if (this._bgDisplay) {
            this._bgDisplay.width = this.width;
            this._bgDisplay.height = this.height;
        }
        let offY = -this.height / 2;
        let offX = 0;
        if (this._titleDisplay) {
            this._titleDisplay.pivotX = this._titleDisplay.width / 2;
            this._titleDisplay.pivotY = this._titleDisplay.height / 2;
            this._titleDisplay.x = 0;
            this._titleDisplay.y = offY + 25;
            offY += 40;
            if (this._lineDisplay) {
                this._lineDisplay.y = offY;
            }
        }
        if (this._iconDisplay) {
            this._iconDisplay.x = -this.width / 2 + 60;
            if (this._contentDisplay) {
                let offsetY = this._contentDisplay.height - 50 > 0 ? this._contentDisplay.height - 50 : 0;
                this._iconDisplay.y = offY + 45 + offsetY / 2;
            }
        }
        if (this._contentDisplay) {
            this._contentDisplay.pivotX = this._contentDisplay.width / 2;
            this._contentDisplay.pivotY = this._contentDisplay.height / 2;
            let offsetY = this._contentDisplay.height - 50 > 0 ? this._contentDisplay.height - 50 : 0;
            this._contentDisplay.y = offY + 45 + offsetY / 2;
            if (this._iconDisplay) {
                this._contentDisplay.x = -this.width / 2 + 80 + 30 + this._contentDisplay.width / 2;
            }
            else {
                if (this.textAlign == 'left') {
                    this._contentDisplay.x = -this.width / 2 + 30 + this._contentDisplay.width / 2;
                }
                else {
                    this._contentDisplay.x = 0;
                }
            }
            offY += offsetY;
        }
        offY += 90;
        //button
        let maxWidth = 0;
        for (let i = 0; i < this._buttonsDisplays.length; ++i) {
            let item = this._buttonsDisplays[i];
            if (item.width > maxWidth) {
                maxWidth = item.width;
            }
        }
        let btnX = -(this._buttonsDisplays.length * (maxWidth + 20) - 20) / 2;

        for (let i = 0; i < this._buttonsDisplays.length; ++i) {
            let item = this._buttonsDisplays[i];
            let btnContainer = item.parent;
            item.width = maxWidth;
            btnContainer.x = btnX + maxWidth / 2;
            btnX += maxWidth + 20;
            btnContainer.y = offY + item.height / 2;
        }

        this.scaleX = this.scaleY = 1;
    }

    /**
     * 
     */
    public show() {
        if (this._isShowing) return;
        this.emit('SHOWED', this);
        this._isShowing = true;
        this.visible = true;
        this.scaleX = this.scaleY = 1;

        if (this.duration > 0) {
            Scheduler.setTimeout(this.duration, () => {
                this.close();
            })
        }
    }

    /**
     * 
     */
    public close() {
        if (!this._isShowing) return;
        this.emit('CLOSED', this);
        this._isShowing = false;
        this.visible = false;
        if (this.autoRelease) {
            this.releaseAll();
        }
    }

    /**
     * 
     */
    public release() {
        super.release();
    }

    private onHover(e: InteractionEvent, thisObj: DisplayObject, over: boolean) {
        let arr = thisObj.id.split('_');
        let index = parseInt(arr[0]);
        let type = arr[1];
        if (over) {
            thisObj.style.cursor = 'pointer';
            if (type == 'rect') {
                (thisObj as any).alpha = 0.9;
            }
            else if (type == 'image') {
                (thisObj as Image).src = this.btnHover;
            }
            else {
                (thisObj as Image).src = this.imgButtons[index].btnHover;
            }
        }
        else {
            thisObj.style.cursor = '';
            if (type == 'rect') {
                (thisObj as any).alpha = 1;
            }
            else if (type == 'image') {
                (thisObj as Image).src = this.btnNormal;
            }
            else {
                (thisObj as Image).src = this.imgButtons[index].btnNormal;
            }
        }
    }

    private onPress(e: InteractionEvent, thisObj: DisplayObject, isPressed: boolean) {
        let arr = thisObj.id.split('_');
        let index = parseInt(arr[0]);
        let type = arr[1];
        if (isPressed) {
            if (type == 'rect') {
                (thisObj as any).alpha = 0.8;
            }
            else if (type == 'image') {
                (thisObj as Image).src = this.btnPressed;
            }
            else {
                (thisObj as Image).src = this.imgButtons[index].btnPressed;
            }
        }
        else {
            if (type == 'rect') {
                (thisObj as any).alpha = 1;
            }
            else if (type == 'image') {
                (thisObj as Image).src = this.btnNormal;
            }
            else {
                (thisObj as Image).src = this.imgButtons[index].btnNormal;
            }
        }
    }

    private onClick(e: InteractionEvent, thisObj: DisplayObject) {
        let arr = thisObj.id.split('_');
        let buttonIndex = parseInt(arr[0]);
        this.emit('CLICKBUTTON', this, buttonIndex);
    }
}
