import { AnimationJitter } from "./AnimationJitter";
import { AnimationPath } from "./AnimationPath";
export class Animation {

    private static _aniList: any[] = [];
    public static play(type: string, sprite: any, options: any, durtime?: number, callBack?: Function) {
        let cls: any;
        switch (type) {
            case "jitter":
                cls = AnimationJitter;
                break;
            case "path":
                cls = AnimationPath;
                break;
        }
        const _ani = new cls(sprite, options, durtime);

        Animation._aniList.push(_ani);
        if (_ani) {
            _ani.on("complete", () => {
                Animation.removeItem(_ani);
                _ani.release();
                callBack && callBack();
            });
            _ani.on('release', () => { // 
                Animation.removeItem(_ani);
            })
            _ani.play();
        }
        return _ani;
    }

    private static removeItem(_ani: any) {
        if (!Animation._aniList) {
            return;
        }
        for (let i = 0; i < Animation._aniList.length; i++) {
            if (Animation._aniList[i] === _ani) {
                Animation._aniList.splice(i, 1);
                return;
            }
        }
    }


    public static releaseAll() {
        if (Animation._aniList) {
            for (let i = 0; i < Animation._aniList.length; i++) {
                if (Animation._aniList[i]) {
                    Animation._aniList[i].release();
                }
            }
        }
        Animation._aniList = [];
    }

}