import { DisplayObject } from "../core/DisplayObject";

/**
 * 
 * @private
 */
export const _GroupObject = new Map<string, { [key: string]: DisplayObject }>();
/**
 * ID
 * @private
 */
export const _idObject = new Map<string, DisplayObject>();

export function getObjectById(id: string){
    return _idObject.get(id);
}
export function registrerID(ui: DisplayObject) {
    if ( ui.id) {
        if ( !_idObject.has( ui.id)) {
            _idObject.set( ui.id, ui);
            return true;
        }
    }
    return false;
}

export function unRegistrerID(ui: DisplayObject) {
    if ( ui.id) {
        if ( _idObject.has( ui.id)) {
            _idObject.delete( ui.id);
        }
    }
}


/** 
 * 
 */
export function registrerGroup(ui: DisplayObject) {
    if ( ui.groupName) {

        let group = _GroupObject.get( ui.groupName);
        if (!group) {
            group = {};
            _GroupObject.set( ui.groupName, group);
        }
        group[ui.uuid] = ui;
    }
}

/**
 * 
 */
export function unRegistrerGroup(ui: DisplayObject) {
    if (ui.groupName) {
        const group = _GroupObject.get(ui.groupName);
        if (group) {
            delete group[ui.uuid];
        }
        let isKey = false;
        for (const key in group) {
            isKey = true;
            break;
        }
        if (isKey) {
            _GroupObject.delete(ui.groupName);
        }
    }
}

/**  */
export function getGroup(name?: string): { [key: string]: DisplayObject } | undefined {
    if (name == undefined) {
        return undefined;
    }
    return _GroupObject.get(name);
}
