import { GifSprite } from '../display/private/GifSprite';
import { GifFrame } from './GifFrames';
export class GifSequence {

    private _playing = false;

    private _nextFrameTime = 0;

    private _loopedCount = 0;

    private _speedScale = 1;

    private _timeDistance = 33;

    constructor(public sprite: GifSprite, public name: string, public frames: Array<GifFrame>, public loopCount: number) {

    }

    public setEndlessLoop(): void {
        this.loopCount = 0;
    }

    public get speedScale(): number {
        return this._speedScale;
    }

    public set speedScale(value: number) {
        this._speedScale = value || 0.0000001;
    }

    public get duration(): number {
        let value = 0;
        for (const frame of this.frames) {
            value += frame.delayTime;
        }
        return value / this._speedScale;
    }

    public get playing(): boolean {
        return this._playing;
    }

    public stop(skipEmit?: boolean): void {
        this._playing = false;
        vf.gui.TickerShared.remove(this.update, this);
        skipEmit || this.sprite.emit(GifSprite.EVENT.STOP, this.sprite);
    }

    public play(): boolean {
        this._playing = true;
        this._loopedCount = 0;
        const currentFrame = this.frames[this.currentFrameIndex];
        if (this.sprite.texture != currentFrame.texture) {
            this.sprite.setGifFrame(currentFrame);
        }
        if (this.frames.length > 1) {
            vf.gui.TickerShared.add(this.update, this);
            this._nextFrameTime = vf.gui.Utils.now() + currentFrame.delayTime;//currentFrame.delayTime / this._speedScale;
            this.sprite.emit(GifSprite.EVENT.PLAY, this.sprite);
            return true;
        }
        return false;
    }

    private get currentFrameIndex(): number {
        let currentFrameIndex = this.frames.indexOf(this.sprite.currentGifFrame);
        if (currentFrameIndex == -1) {
            currentFrameIndex = 0;
        }
        return currentFrameIndex;
    }

    public get loopedCount(): number {
        return this._loopedCount;
    }

    private update() {
        if (vf.gui.Utils.now() >= this._nextFrameTime) {
            let nextFrameIndex = this.currentFrameIndex + 1;
            if (nextFrameIndex >= this.frames.length) {
                nextFrameIndex = 0;
                this._loopedCount++;
                this.sprite.emit(GifSprite.EVENT.END, this.sprite);
                if (this.loopCount && this._loopedCount >= this.loopCount) {
                    this.stop();
                    this.sprite.emit(GifSprite.EVENT.COMPLETE, this.sprite);
                    return;
                }
            }
            const nextFrame = this.frames[nextFrameIndex];
            this.sprite.setGifFrame(nextFrame);
            this._nextFrameTime += this.sprite.currentGifFrame.delayTime//nextFrame.delayTime / this._speedScale;
        }
    }
}