import { updateTextureFill } from '../core/plugs/LineGradient';
import { GraphBase } from './private/GraphBase'

/**
 * 
 * 
 *  lineWidth  color 
 * 
 * @example let rect = new vf.gui.Rect();
 * 
 */
export class Rect extends GraphBase {
    public constructor() {
        super();
    }

    public drawGraph() {
        const graphics = this.graphics;
        graphics.clear();
        if (this._lineWidth < 0) {
            console.log("0");
            return;
        }
        graphics.lineStyle(this._lineWidth, this._lineColor, this._lineAlpha, 0.5, false, this.lineType);

        if (this.color) {
            graphics.beginFill(this._color, this._fillAlpha);
        } else {
            updateTextureFill(this.graphics,this);
        }

        graphics.drawRoundedRect(this._anchorX ? -this._anchorX * this.width : 0, this._anchorY ? -this._anchorY * this.height : 0, this.width, this.height, this._radius); // _radius15
        graphics.endFill();
    }

}