export class TimeLineDefaultData {
    delay?: number = 0;//
    paused?: boolean = false; //true.
    repeat?: number = 0; //repeat11-1
    ease?: ((k: number) => number) | any;
    yoyo?: boolean = false;
    onStart?: Function;
    onStartParams?: any;
    onUpdate?: Function;
    onUpdateParams?: any;
    onRepeat?: Function;
    onRepeatParams?: any;
    onComplete?: Function;
    onCompleteParams?: any;
    onReverseComplete?: Function;
    onReverseCompleteParams?: any;
}

export class TimeLineDataItem {
    tween?: Tween;
    positionTime = NaN;
    hasPlay = false;
}
//timeline     )
