    private _repeat = 0;
    private _playStates = 0; // 0 1 2
    private _oldPlayStates = 0;
    private _tickIns: any = undefined;
    private _isFinite = false;

    private _needDownGrade = false;


    constructor(options: TimeLineDefaultData) {
        super();
        this._tweens = [];
        //
        this._defaultParam = options;
        options.repeat && (this._repeat = this._totalRepeat = options.repeat);
        if (!this._defaultParam.paused) {
            this.play();
        }
        addTimeLine(this);
        return this;
    }


