    private set playStates(value: any) {

        this._playStates = value;
    }

    public get duration() {
        return this._duration;
    }

    public get currenTimer() {
        return this._currenTimer;
    }

    public get reversed() {
        let states = this._playStates;
        if (this._playStates === 0) {
            states = this._oldPlayStates;
        }

        return states === 2;
    }

    public get paused() {
        return this._playStates === 0;
    }
    /**
     * 
     * 
     */
    public add(value: Tween, position = NaN) {

        //begin
        if (Array.isArray(value)) {
            value.map && value.map((_v) => {
                this.add(_v, position);
            })
            return this;
        }

        const { _defaultParam } = this;

        //tween
        const defaultdelay = _defaultParam.delay ? _defaultParam.delay : 0;
        const item = new TimeLineDataItem();
        if (value instanceof Tween) {
            item.tween = value;
        }
        const delay = (item.tween ? item.tween.delayTime : 0);
        position = isNaN(position) ? 0 : position;
        const during = (item.tween ? Number(item.tween.duration) : 0);
        item.positionTime = position + defaultdelay;
        this._reactDuration = Math.max((during + delay + position), 0);
        this._duration = this._reactDuration + defaultdelay;
        this._tweens.push(item);

        //
        this._tweens.sort((a, b) => {
            return a.positionTime - b.positionTime;
        });

        //to

        this._objList = [];
        this._propertyList = [];
        for (let i = 0; i < this._tweens.length; i++) {
            const item = this._tweens[i];
            if (item && item.tween) {
                const obj = item.tween.object;
                if (this._objList.indexOf(obj) >= 0) {
                    continue;
                } else {
                    this._objList.push(obj);
                    const propItem = this.getPropertyList(obj);
                    this._propertyList.push(propItem);
                }
            }

        }

        this.cacheStartData();
        // this.cacheStartDataTest();
        return this;
    }

