    private cacheStartData() {
        //itemstart    
        const {
            _tweens
        } = this;
        const dList: any = {};
        for (let i = 0; i <= _tweens.length - 1; i++) {
            const item = _tweens[i];
            if (item && item.tween) {
                const objIdx = this._objList.indexOf(item.tween.object);
                const propertyList = this._propertyList[objIdx];
                if (!dList[objIdx]) {
                    dList[objIdx] = {};
                }
                const virtualData = dList[objIdx];
                item.tween._virtualValueStart = {};
                for (let j: number = propertyList.length - 1; j >= 0; j--) {
                    const p = propertyList[j];
                    let curValue = item.tween.object[p];
                    if (virtualData.hasOwnProperty(p)) {
                        curValue = virtualData[p];
                        //TODO
                    } else {
                        virtualData[p] = curValue;
                        //TODO
                    }
                    item.tween._virtualValueStart[p] = item.tween.valuesStart[p] = curValue;
                    if (!item.tween.valuesEnd.hasOwnProperty(p)) {
                        item.tween.valuesEnd[p] = curValue;
                        //TODO
                    } else {
                        virtualData[p] = item.tween.valuesEnd[p] = item.tween.valuesEnd[p] as any;
                        //TODO
                    }
                }
            }
        }

    }

