    public allInvalidate() {
        this.invalidateProperties();
        this.invalidateSize();
        this.invalidateDisplayList();
        this.invalidateParentLayout();
    }

    /**
     * @private
     * undefinedmeasure()
     */
    public get width(): number {
        const v = this.$values;
        if(this.getPreferredUWidth() !== v[UIKeys.width]){
            this.validateSize();
        }
        return v[UIKeys.width];
    }

    /**
     * @private
     *
     * @param value
     */
    public set width(value: number) {
        value = +value;
        const values = this.$values;
        if (value < 0 || values[UIKeys.explicitWidth] === value)
            return;
        values[UIKeys.explicitWidth] = value;
        this.emit(ComponentEvent.WIDTH_CHANGE, value);
        this.invalidateSize();
        this.invalidateParentLayout();
    }


    /**
     * @private
     * ,NaN,NaNmeasure()
     */
    public get height(): number {
        const v = this.$values;
        if(this.getPreferredUHeight() !== v[UIKeys.height]){
            this.validateSize();
        }
        return v[UIKeys.height];
    }

    /**
     * @private
     *
     * @param value
     */
    public set height(value: number) {

        value = +value;
        const values = this.$values;
        if (value < 0 || values[UIKeys.explicitHeight] === value)
            return;
        values[UIKeys.explicitHeight] = value;

        this.invalidateSize();
        this.invalidateParentLayout();
    }

