import { updateTextureFill } from '../core/plugs/LineGradient';
import { GraphBase } from './private/GraphBase'

/**
 * 
 * 
 *  lineWidth  color 
 * 
 * @example let circle = new vf.gui.Circle();
 * 
 * 
 */
export class Circle extends GraphBase {
    public constructor() {
        super();
    }

    /**
     * 
     */
    protected _startAngle = 0;
    public get startAngle() {
        return this._startAngle;
    }
    public set startAngle(value) {
        this._startAngle = value;
        this.invalidateDisplayList();
    }

    /**
     * 
     */
    protected _endAngle = 360;
    public get endAngle() {
        return this._endAngle;
    }
    public set endAngle(value) {
        this._endAngle = value;
        this.invalidateDisplayList();
    }

    /**
     * 
     */
    protected _anticlockwise = false;
    public get anticlockwise() {
        return this._anticlockwise;
    }
    public set anticlockwise(value) {
        this._anticlockwise = value;
        this.invalidateDisplayList();
    }

    public drawGraph() {
        const graphics = this.graphics;
        graphics.clear();
        if (this._lineWidth < 0) {
            console.log("0");
            return;
        }
        graphics.lineStyle(this._lineWidth, this._lineColor, this._lineAlpha, this._alignment, false, this.lineType);
        const radius = Math.ceil(this.explicitWidth || this.explicitHeight || this._radius * 2) / 2;
        const diameter =  radius * 2;

        this.setActualSize(diameter,diameter);

        if (this.color) {
            graphics.beginFill(this._color, this._fillAlpha);
        } else {
            updateTextureFill(this.graphics,this);
        }

        graphics.arc(this._anchorX ? (0.5 - this._anchorX) * diameter : radius
            , this._anchorY ? (0.5 - this._anchorY) * diameter : radius
            , radius
            , this._startAngle
            , this._endAngle
            , this._anticlockwise);

        graphics.endFill();
    }

}